<template>
    <div v-if="menuList['repayment']" class="content">
        <div class="main-title">
            <div class="main-title__wrap main-title--nowrap">
                <div class="h1">{{currentPage.title}}</div>
            </div>

            <div class="main-title__right --icon">
                <span class="i i-info"></span>
                <span class="t">{{currentPage.description}}</span>
            </div>
        </div>

        <section class="container">
            <repayment-list v-if="menuList['repayment']"/>
        </section>

        <popup :is-set-styled="true"  :show="popups.hideCoupon.show" :name="popups.hideCoupon.name" :add-class="'hide-coupon'">
            <hide-coupon></hide-coupon>
        </popup>

        <popup :is-set-styled="true"  :show="popups.markVerify.show" :name="popups.markVerify.name" :add-class="'markVerify'">
            <mark-verify></mark-verify>
        </popup>
    </div>
</template>
<script>
    import {mapState} from 'vuex';
    import RepaymentList from '@/components/moderator/RepaymentList.vue';
    import Tooltip from '@/components/Tooltip.vue';
    import TextareaField from '@/components/forms/TextareaField.vue';
    import HideCoupon from '@/components/modal/HideCoupon.vue';
    import MarkVerify from '@/components/modal/MarkVerify.vue';
    import Popup from '@/components/modal/Popup.vue';

    export default {
        metaInfo() {
            return {
                title: 'Поиск и погашение купонов',
                titleTemplate: this.$store.state.options.titleTemplate
            }
        },
        name: "Repayment",
        components: {
            RepaymentList,
            Tooltip,
            TextareaField,
            Popup,
            HideCoupon,
            MarkVerify
        },
        data: function(){
          return {
            items: [{id: 0, opened: false}, {id: 1, opened: false}, {id: 2, opened: false}, {id: 3, opened: false}, {id: 4, opened: false}, {id: 5, opened: false}],
          }
        },
        computed: {
            ...mapState({
                menuList: state => state.menus.itemsAction,
                popups: state => state.popups.items,
            }),
            defaultStatus() {
                return this.menuList['repayment'][0]['code'];
            },
            currentStatus() {
                return this.$route.params.action ? this.$route.params.action : this.defaultStatus;
            },
            currentPage() {
                for(let ind in this.menuList['repayment']) {
                    if (this.menuList['repayment'][ind]['code'] === this.currentStatus) {
                        return this.$store.getters['menus/RepaymentPage']({currentStatus: this.menuList['repayment'][ind]['code']});
                    }
                }
            }
        },
        methods: {
          tooltipClose: function(ID) {
              let i = ID;

              this.$store.commit('moderatorCooperation/setItemActive', {
                  value: false,
                  index: i
              });

              if(i == this.isItemListActive) {
                  this.$store.commit('moderatorCooperation/setItemActive', {
                      value: true,
                      index: i
                  });
              }
          },
          tooltipShow: function(ID) {
              let i = ID;

              if(i !== this.isItemListActive) {
                  this.isItemListActive = i;
              }

              this.$store.commit('moderatorCooperation/setItemActive', {
                  value: true,
                  index: i
              });
          },
            fetchData () {
                this.$store.dispatch('moderatorRepayment/getItems',{status:this.currentStatus, reload: true});
            },
            handleScroll: function () {
                let scroll = window.scrollY;
                let viewport = window.innerHeight;
                let bodyHeight = document.body.clientHeight;
                if(bodyHeight-80 <= scroll + viewport){
                    this.$store.dispatch('moderatorCooperation/getItems', {
                        status:this.currentStatus
                    })
                }
            },
            openHideCoupon(){
              this.$store.dispatch('popups/open', {name: this.popups.hideCoupon.name});
            },
        },
        watch: {
            '$route': 'fetchData'
        },
    }
</script>
