<template>
    <div class="row-b__col elem-pos-fix" style="width: 240px; " :class="{'--has-file':file.finish}">
        <div class="ro2w-b__col elem-pos-fix" v-if="!file.name">
            <span @click="openInput()" class="add-file-link" :class="{error:error}">{{ btnText }}</span>
        </div>

        <!-- <div class="row-b__col elem-pos-fit" v-if="!file.name">
            <span class="text-size-s text-color-grey">{{ hint }}</span>
        </div> -->

        <span class="added-file-text">
            <span class="t">{{ file.name }}</span>
            <span v-if="isSend && !file.finish" class="i --preloader"></span>
            <span v-show="!error" class="del" v-if="file.finish" @click="deleteFile"></span>
        </span>

        <input v-if="!isSend" :accept="acceptType" style="display: none;" ref="fileInput" @change="onFileChange" type="file">
    </div>
</template>

<script>
    export default {
        name: "FileUpload",
        props: {
            acceptType: {default: ''},
            value: {default: function () {
                    return {}
            }},
            maxFilesSize: {default: 10*1024*1024},
            hint: {default: 'Максимальный вес — 10Мб'},
            btnText: {default: 'Загрузить файл'},
            fieldName: {default: 'file'},
            error: {default: false},
            actionDelete: {type: String,required: true},
            actionSend: {type: String,required: true},
            formParams: {default: {}}
        },
        data: function () {
            return {
                file: {},
                fileInput: false,
                errorSize: false,
                isSend: false,
                targetFile: false,
                targetFileType: false,
                ajax: false,
            }
        },
        methods: {
            openInput: function() {
                if(!this.isSend) {
                    this.$refs.fileInput.click();
                    this.errorSize = false;
                }
            },

            onFileChange: function(e) {
                let files = e.target.files || e.dataTransfer.files;
                if (!files.length)
                    return;
                this.$emit('change');
                this.createImage(files[0]);
            },

            getExt: function(name) {
                let parts;
                return ( parts = name.split(".") ).length > 1 ? parts.pop() : "";
            },

            createImage: function(file) {

                if(file.size > this.maxFilesSize) {
                    let self = this;
                    self.errorSize = true;
                    setTimeout(function () {
                        self.errorSize = false;
                    }, 3000);
                } else {
                    this.file = {
                        id: false,
                        name: file.name,
                        size: file.size,
                        path: false,
                        icon: false,
                        finish: false,
                        ext: this.getExt(file.name),
                        progress: 0,
                    };
                    this.fileUpload(this.currentIndex, file);
                }

            },

            deleteFile: function() {
                this.file = {};
                this.$store.dispatch(this.actionDelete, {fieldName: this.fieldName});
                this.$emit('input', {});
            },

            fileUpload: function (fileIndex, file) {
                let self = this;
                let form_data = new FormData();
                form_data.append(self.fieldName.toString(), file, file.name);

                for(let key in this.formParams) {
                    form_data.append(key, this.formParams[key]);
                }

                if(!self.isSend) {
                    self.isSend = true;
                    this.$store.dispatch(this.actionSend, {form_data:form_data}).then(function(f) {
                        self.isSend = false;
                        self.file = {
                            id: f.id,
                            name: self.file.name,
                            size: self.file.size,
                            path: f.path,
                            finish: true,
                            icon: f.icon,
                            ext: self.file.ext,
                        };
                        self.$emit('input', self.file);
                    });
                }
            }
        }
    }
</script>

