<template>
    <div>
        <div class="popup__header">
            <div class="title">Отметить проверенным купон</div>

            <div class="subtitle">Паркетная доска из массива дуба (Москва)</div>
            <div class="text-color-white-trans mt16">
                Покупатель: Юрий Сергеевич +7(987)654-32-10
            </div>
        </div>


        <div class="popup__content">
            <div class="h2">Служебный комментарий</div>

            <div class="mt16">
                <textarea-field  :placeholder="text.placeholder"
                                 v-model="text.value"
                                 :auto-focus="true"
                                 :is-error="text.error"
                                 @focus="removeError(text.name)"
                                 :add-style="{height: 80 + 'px'}"
                ></textarea-field>
            </div>




            <div class="row-b mt32">
                <div class="row-b__col elem-pos-fix mr24">
                    <button class="btn" >
                        <span>Подтвердить</span>
                    </button>
                </div>


                <div class="row-b__col elem-pos-fit">
                    Напишите комментарий, чтобы впоследствии вспомнить,<br>о чём вы договорились с покупателем.
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import {mapState} from 'vuex'
    import TextareaField from '@/components/forms/TextareaField.vue'

    export default {
        name: "MarkVerify",
        components: {
            TextareaField
        },
        props: {
            action: {
                default() {
                    return {}
                }
            }
        },
        data() {
            return {
                text: {
                    name: 'text',
                    value: '',
                    required: true,
                    error: false,
                    disabled: false,
                    placeholder: 'Например, «Созвонились с покупателем, он подтвердил слова поставщика...»',
                },
            }
        },
        methods: {
            removeError (field) {
                this[field].error = false;
            },
        },
        computed: {
            ...mapState({
                popups: state => state.popups.items,
            }),
        }
    }
</script>

