<template>
    <div>
        <div class="popup__content">
            <div class="h1 mb8 text-color-red" style="font-size: 31px; ">Ошибка!</div>

            <p>
                Сначала проверьте ИНН и ОГРН поставщика по скан-копии и через РосРеестр
            </p>

            <!-- end row-b -->


            <div class="row-b mt32">
                <div class="row-b__col elem-pos-fix mr24">
                    <button class="btn" @click="close">Понятно</button>
                </div>


                <div class="row-b__col elem-pos-fit">
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import {mapState} from 'vuex'

    export default {
        name: "PrintContract",
        data: function () {
            return {}
        },
        created() {
        },
        methods: {
            close: function () {
                this.$store.commit('popups/close', {name: this.popups.printContract.name});
                this.$emit('close');
            }
        },
        computed: {
            ...mapState({
                popups: state => state.popups.items,
            }),
        }
    }
</script>

