<template>
    <div>
        <div class="popup__content">
            <div class="h1 mb8" style="font-size: 31px; ">Подтвердите эксклюзивность скидки {{ stepsCreateAction.one.fields.discount.value - 3}}%</div>
            
            <div class="text-bold">Для этого укажите максимальную скидку, которую вы предоставляете обычным покупателям</div>
            
            <div class="row-b mt40">
                <div class="row-b__col elem-pos-fix" style="width: 164px; min-width: 164px; ">
                    <input-styled
                            name="discount"
                            :mask="masks.percent"
                            v-model="discount"
                            :is-error="discountErrorTwo || discountErrorOne"
                            @focus="discountErrorTwo = false;discountErrorOne = false"
                            :placeholder="'Обычная скидка'"
                            :placeholder-input="'%'"
                            :fieldAutocomplete="'nope'"></input-styled>
                </div>
                
                <div class="row-b__col elem-pos-fit">
                    <div class="construct-form__note" v-if="!discountErrorOne && !discountErrorTwo">Если вы не предоставляете обычным покупателям никаких скидок,<br> оставьте это поле пустым и нажмите «Продолжить»</div>
                    <div class="construct-form__note text-color-red" v-if="discountErrorOne">Указанная скидка больше той, что вы готовы<br> предоставить нашему проекту. Так не пойдёт!</div>
                    <div class="construct-form__note text-color-red" v-if="discountErrorTwo">Указанная скидка почти такая же, что вы готовы<br> предоставить нашему проекту. Так не пойдёт!</div>
                </div>
            </div>
            <!-- end row-b -->
            
            
            <div class="row-b mt32">
                <div class="row-b__col elem-pos-fix mr24">
                    <button class="btn" @click="next">Продолжить</button>
                </div>
                
                
                <div class="row-b__col elem-pos-fit">
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import {mapState} from 'vuex'
    import InputStyled from '@/components/forms/InputStyled.vue'

    export default {
        name: "ConfirmDiscount",
        components: {
            InputStyled
        },
        props: {
        },
        data: function() {
            return {
                discount: '',
                discountErrorOne: false,
                discountErrorTwo: false,
            }
        },
        created() {},
        methods: {
            next() {
                this.discountErrorOne = false;
                this.discountErrorTwo = false;
                if (+this.discount > (this.stepsCreateAction.one.fields.discount.value - 3)) {
                    setTimeout(()=>{
                        this.discountErrorOne = true;
                    },150);

                } else if ((this.stepsCreateAction.one.fields.discount.value - this.discount ) < 6 ) {
                    setTimeout(()=>{
                        this.discountErrorTwo = true;
                    },150);
                } else {
                    this.$emit('change',this.discount)
                    if(+this.discount === 0){
                        this.$store.commit('popups/close', {name: this.popups.confirmDiscount.name});
                        this.$store.dispatch('popups/open', {name: this.popups.correctTextNoDiscount.name});
                        this.$store.commit('partnerActions/setFormCreateField', {
                            name: 'onenessDiscount',
                            value: 'Обычным покупателям мы не даём никаких скидок, ' +
                            'а для покупателей<br> с купоном готовы предоставить <strong>#DISCOUNT#%</strong>.<br>' +
                            'Такую скидку невозможно получить, минуя ZemSkidki.ru',
                            step: 'one',
                        })
                    } else {
                        this.$store.commit('popups/close', {name: this.popups.confirmDiscount.name});
                        this.$store.dispatch('popups/open', {name: this.popups.correctText.name});
                        this.$store.commit('partnerActions/setFormCreateField', {
                            name: 'onenessDiscount',
                            value: 'Обычным покупателям мы даём скидку не более <strong>'+this.discount+'%</strong>, ' +
                            'а для покупателей<br> с купоном готовы предоставить <strong>#DISCOUNT#%</strong>.<br>' +
                            'Такую скидку невозможно получить, минуя ZemSkidki.ru',
                            step: 'one',
                        })
                    }
                }

            }
        },
        computed: {
            ...mapState({
                popups: state => state.popups.items,
                items: state => state.partnerActions.templates.modals.origin,
                masks: state => state.masks.items,
                stepsCreateAction: state => state.partnerActions.stepsCreateAction,
            }),
        },
        beforeDestroy(){
            this.$store.commit('popups/close', {name: this.popups.confirmDiscount.name});
        }
    }
</script>

