<template>
    <transition name="fade-popup">
        <div class="city-select-container" id="contract-modal" v-if="show">
            <header class="header">
                <div class="header-main header-main--editor">
                    <div class="inner-wrapper">
                        <div class="h-left">
                            <div class="h-editor-text" style="opacity: 0.3;">
                                <span class="i i-info"></span>
                                <span class="t">Внимание! Редактировать договор можно только в крайнем случае и очень внимательно.</span>
                            </div>
                        </div>

                        <div class="h-right__editor">
                            <a href="#" @click.prevent="close" class="h-editor-link">Отмена</a>
                            <span @click="save" class="btn h-editor-btn btn--ico btn--save">
                                <span class="i i-ok2"></span>
                                <span class="t">Сохранить</span>
                            </span>
                        </div>
                    </div>
                </div>
            </header>
            <contract ref="contract" @close="close" :isEdit="true" v-if="init"></contract>
        </div>
    </transition>
</template>

<script>


    import {mapState} from 'vuex';
    import Contract from '@/components/partner/Contract.vue';

    export default {
        name: "ContractEdit",
        components: {
            Contract
        },
        props: {
            regionsProp: {},
            show: {
                default: function () {
                    return false
                }
            },
            name: '',
        },
        data: function () {
            return {
                regions: {},
                countSelect: 0,
                init: false,
            }
        },
        mounted: function () {
            this.getPartner();
        },

        watch: {
            show: function() {
                if(this.show) {
                    document.getElementsByTagName('body')[0].classList.add("noscroll");
                } else {
                    document.getElementsByTagName('body')[0].classList.remove("noscroll");
                }
            }
        },
        computed: {
        },

        beforeDestroy: function() {

        },

        methods: {
            close: function () {
                if (this.show && this.name) {
                    this.$store.commit('popups/close', {name: this.name});
                    document.getElementsByTagName('body')[0].classList.remove("noscroll")
                }
            },
            save: function () {
                this.$refs.contract.finish();
            },
            getPartner: function () {
                let self = this;
                this.$store.dispatch('partner/getByID', {id: this.$route.params.id}).then(() => {
                    self.init = true;
                }).catch(() => {
                });
            }
        }
    }
</script>

<style scoped>
    .fade-status-enter-active, .fade-status-leave-active {
        transition: opacity .3s;
    }

    .fade-status-enter, .fade-status-leave-to {
        opacity: 0;
    }

    #contract-modal {
        background: #fff;
    }

    #contract-modal .h-editor-text {
        color: #fff !important;
        opacity: 0.7 !important;
        font-size: 10px !important;
        line-height: 12px !important;
    }

</style>
