<template>
    <div class="popup-coupon-stat">
        <div class="h1 text-color-grey">Статистика акции</div>


        <div class="coupon">
            <router-link :to="'/partner/coupons/id'+action.id" class="coupon__link"></router-link>

            <div class="coupon__wrap">
                <div class="coupon__img" v-if="action.image" :style="{ backgroundImage: 'url(//api.zemskidki.ru/'+action.image+')' }"></div>
                <div class="coupon__img" v-else>
                                        <span class="coupon__img__empty">
                                            <span class="icon">
                                                <span class="i i-photo"></span>
                                            </span>
                                            <span class="t">нет фото</span>
                                        </span>
                </div>


                <div class="coupon__content">

                    <div class="top">
                        <div class="coupon__company" v-html="partner.name"></div>
                    </div>

                    <div class="coupon__name" v-html="action.name"></div>

                    <div class="w">
                        <div class="coupon__economy">
                            <div class="t">
                                Экономия
                                
                                <tooltip :direction="'bottom-right-direction'"
                                         :add-class-wrap="'dib mt-2'"
                                         :add-style="{width: 376 + 'px'}">
                                    <span slot="tooltipBtn" class="i i-question3 icon-btn"></span>
                                    
                                    <div slot="tooltipContent">
                                        <p><strong>Как рассчитывается экономия? Очень просто:</strong></p>

                                        <ul class="ul-disc-small">
                                            <li>Средний чек у данного поставщика составляет {{ action.averageCheckFormat }} <span class="rouble">{{ $store.getters['options/setRouble'] }}</span></li>
                                            <li>Предоставляемая скидка составляет {{ action.discountFrom }}%</li>
                                            <li>{{ action.discountFrom }}% от {{ action.averageCheckFormat }} <span class="rouble">{{ $store.getters['options/setRouble'] }}</span> – это примерно {{ action.realSavingsFormat }} <span class="rouble">{{ $store.getters['options/setRouble'] }}</span></li>
                                        </ul>

                                        <p>То есть, при обращении к данному поставщику экономия обычно составляет ≈{{ action.realSavingsFormat }} <span class="rouble">{{ $store.getters['options/setRouble'] }}</span></p>
                                    </div>
                                </tooltip>
                            </div>

                            <div class="p" v-if="action.realSavingsFormat">
                                {{ action.realSavingsFormat }}
                                <span class="rouble">{{ $store.getters['options/setRouble'] }}</span>
                            </div>
                            <div class="p" v-else>
                                ?
                            </div>
                        </div>

                        <div class="coupon__info" v-if="action.averageCheckFormat">
                            Средний чек
                            {{ action.averageCheckFormat }}
                            <span class="rouble">{{ $store.getters['options/setRouble'] }}</span>,

                            скидка
                            <span class="n --text" v-if="action.instrumentAction">Бесплатная <br> доставка</span>
                            <span class="n" v-else>{{ action.discountFrom }}%</span>
                        </div>
                        
                        <div class="coupon__info" v-else>
                            Средний чек
                            ?,
                            скидка
                            <span class="n">?</span>
                        </div>
                    </div>


                    <div class="coupon__stat">
                                            <span class="elem" v-if="action.city">
                                                <span class="i i-marker3"></span>
                                                <span class="t">{{ getCitiesName(action.city)}}</span>
                                            </span>

                        <span class="elem --views">
                                                <span class="i i-eye"></span>
                                                <span class="t">{{ action.showCounterFormat }}</span>
                                            </span>

                        <span class="elem --buy" v-if="action.statistics && action.statistics.sold > 0">
                                                <span class="i i-cart"></span>
                                                <span class="t">{{ action.statistics.sold }}</span>
                                            </span>
                    </div>
                </div>
            </div>
        </div>


        <div class="coupon-details__stat">
            <div class="col-left">
                <div class="coupon-details__stat-row">
                    <div class="line">
                        <div class="l">Оформлено</div>
                        <div class="r">{{ action.statistics.sold }}</div>
                    </div>

                    <div class="n">Общее количество купонов</div>
                </div>

                <div class="coupon-details__stat-row">
                    <div class="line">
                        <div class="l">Использовано</div>
                        <div class="r">{{ action.statistics.ext }}</div>
                    </div>

                    <div class="n">Купоны, по которым получена обещанная скидка</div>
                </div>

                <div class="coupon-details__stat-row">
                    <div class="line">
                        <div class="l">Ждут своего часа</div>
                        <div class="r">{{ action.statistics.valid }}</div>
                    </div>

                    <div class="n">Оформленные, но еще не использованные купоны</div>
                </div>

                <!--<div class="coupon-details__stat-row">
                    <div class="line">
                        <div class="l">Просрочено</div>
                        <div class="r">{{ action.statistics.exp }}</div>
                    </div>

                    <div class="n">Купоны, которыми не воспользовались вовремя</div>
                </div>-->

                <div class="coupon-details__stat-row">
                    <div class="line">
                        <div class="l">Аннулировано поставщиком</div>
                        <div class="r">{{ action.statistics.canceledProvider }}</div>
                    </div>

                    <div class="n">Купоны, которые отозвал поставщик</div>
                </div>

                <div class="coupon-details__stat-row">
                    <div class="line">
                        <div class="l">Аннулировано модератором</div>
                        <div class="r">{{ action.statistics.canceledModerator }}</div>
                    </div>

                    <div class="n">Купоны, отозванные нами</div>
                </div>

                <div class="coupon-details__stat-row">
                    <div class="line">
                        <div class="l">Отменено</div>
                        <div class="r">{{ action.statistics.withdrawn }}</div>
                    </div>

                    <div class="n">Купоны, от которых отказались сами покупатели</div>
                </div>
            </div>



            <div class="col-right">

                <div class="coupon-details__stat-card">
                    <div class="icon">
                        <span class="i i-discount2" style=""></span>
                    </div>

                    <div class="t">
                        Всего сэкономлено
                    </div>
                    <div class="d">{{action.statistics.sumFormat}} <span class="rouble">{{ $store.getters['options/setRouble'] }}</span></div>
                    <div class="n" v-if="+action.statistics.ext === 0">потому что погашено {{ action.statistics.ext }} купонов</div>
                    <div class="n" v-else>после погашения {{ action.statistics.ext }} {{ declOfNumber(action.statistics.ext,['купона','купонов','купонов']) }}</div>
                </div>


                <div class="coupon-details__stat-card" v-if="false">
                    <div class="icon">
                        <span class="i i-trophy" style="transform: scale(1.25);"></span>
                    </div>

                    <div class="t">
                        Позиция в рейтинге
                    </div>

                    <div class="d">#287</div>
                    <div class="n">по сумме скидок</div>

                    <div class="d">#158</div>
                    <div class="n">по количеству оформлений</div>
                </div>


                <div class="coupon-details__stat-card">
                    <div class="icon">
                        <span class="i i-calendar"></span>
                    </div>

                    <div class="t">
                        На сайте
                    </div>

                    <div class="d">{{ action.dateDiff}}</div>
                    <div class="n">Опубликовано {{ action.dateStatus}}</div>
                </div>
            </div>
        </div>
        <!-- end coupon-details__stat -->
    </div>
</template>

<script>
    import {mapState} from 'vuex';
    import Tooltip from '@/components/Tooltip.vue';
    import {declOfNumber} from '@/helpers/formatted';

    export default {
        name: "PartnerCouponsStat",
        components: {
            Tooltip
        },
        props: {
            action: {
                default(){
                    return {}
                }
            }
        },
        data() {
            return {
            }
        },
        mounted() {

        },
        computed: {
            ...mapState({
                popups: state => state.popups.items,
                cities: state => state.cities.items,
                partner: state => state.partner.current,
            }),
        },
        methods: {
            closePartnerCouponsRemove: function () {
                this.$store.commit('popups/close', {name: this.popups.partnerCouponsRemove.name});
            },
            getCitiesName: function(cities){
                let text = '';
                if(cities.length === 0){
                    return 'Не выбрано';
                }
                let i = 1;
                for(let index in cities) {
                    let city = [...this.cities].find(city => city.xmlId === cities[index])
                    if(city !== undefined) {

                        if(i > 3) {
                            break;
                        }
                        if (i < 3 && i < cities.length) {
                            text += city.name+', ';
                            i++;
                        } else if(i === 3 || (i === cities.length && cities.length < 3) ) {
                            text += city.name;
                            i++;
                        }
                    }
                }
                if(cities.length > 3){
                    text += ' + ещё ' + (cities.length - 3);
                }
                return text;
            },
            declOfNumber(number, titles){
                return declOfNumber(number, titles);
            },
        }
    }
</script>