<script>
    import {mapState} from 'vuex'

    export default {
        name: "Chart",
        components: {
        },
        props: {
            graph: {
                default: function () {
                    return []
                }
            },
            years: {
                default: function () {
                    return []
                }
            },
        },
        data() {
            return {
                defaultYear: 0,
                currentYear: 0,
                showNav: false
            }
        },
        mounted: function () {
            this.defaultYear = this.years[0] ? this.years[0] : 0;
            this.currentYear = this.defaultYear;
            this.showNav = this.years.length > 1;

        },
        computed: {
            months: function () {
                let self = this;
                return this.graph.filter(month => month.year === self.currentYear);
            },
        },
        methods: {
            nextYear: function () {
                let currentIndex = this.years.indexOf(this.currentYear),
                    newIndex = currentIndex;

                newIndex++;

                if (newIndex < 0) {
                    newIndex = this.years.length - 1;
                } else if (newIndex > this.years.length - 1) {
                    newIndex = 0;
                }

                this.currentYear = this.years[newIndex];
            },
            prevYear: function () {
                let currentIndex = this.years.indexOf(this.currentYear),
                    newIndex = currentIndex;

                newIndex++;

                if (newIndex < 0) {
                    newIndex = this.years.length - 1;
                } else if (newIndex > this.years.length - 1) {
                    newIndex = 0;
                }

                this.currentYear = this.years[newIndex];
            }
        }
    }
</script>
