<template>
    <div class="partner-history">

        <div class="partner-history__item" v-for="status in item.history" :key="status.id">
            <div v-if="status.statusCode=='other'" class="stat" :class="{'text-color-green': status.color == 'green','text-color-red': status.color == 'red','text-color-grey': status.color == 'gray'}" v-html="formatHistoryTitle(status.title, status.date)"></div>
            <div v-else class="stat" :class="{'text-color-green': status.color == 'green','text-color-red': status.color == 'red','text-color-grey': status.color == 'gray'}">{{ status.status }} {{ status.date }} <span v-if="status.moderatorNameTitle">({{status.moderatorNameTitle}})</span></div>

            <div v-if="!(['other','overtime'].includes(status.statusCode) && !status.comment) && status.statusCode !== 'moderation'" class="title">Служебный комментарий</div>
            <div v-if="!(['other','overtime'].includes(status.statusCode) && !status.comment) && status.statusCode !== 'moderation'" class="text" v-html="status.comment"></div>

            <div v-if="!(['other','overtime'].includes(status.statusCode) && !status.email_title) && status.statusCode !== 'wait_contract' && status.statusCode !== 'moderation'" class="title">Письмо поставщику</div>
            <div v-if="!(['other','overtime'].includes(status.statusCode)  && !status.email_title) && status.statusCode !== 'wait_contract' && status.statusCode !== 'moderation'" class="text" v-html="'Тема: ' + status.email_title"></div>
            <div v-if="!(['other','overtime'].includes(status.statusCode)  && !status.email_title) && status.statusCode !== 'wait_contract' && status.statusCode !== 'moderation'" class="text" v-html="status.email"></div>

            <div v-if="!(['other','overtime'].includes(status.statusCode)  && !status.sms) && status.statusCode !== 'wait_contract' && status.statusCode !== 'moderation'" class="title">СМС поставщику</div>
            <div v-if="!(['other','overtime'].includes(status.statusCode)  && !status.sms) && status.statusCode !== 'wait_contract' && status.statusCode !== 'moderation'" class="text" v-html="status.sms"></div>
        </div>
        <!-- end partner-history__item -->


        <div class="sect-blank wrapper-content" v-if="item.history.length === 0">
            <div class="wrap text-center">
                <div class="text-size-l text-bolder text-color-grey mt16">Здесь пока ничего нет...</div>
            </div>

            <div class="mini-zems"></div>
        </div>

    </div>
    <!-- end partner-history -->
</template>
<script>
    export default {
        name: 'ProviderHistory',
        props:['item'],
        methods: {
            formatHistoryTitle(title, date) {
                let formatted = '';
                formatted = title.split('. ').join(".<br>");

                return formatted + ' ' + date;
            },
        }
    }
</script>