<template>
    <div>
        <coupons-promotion  v-if="$route.path === '/moderator/coupons/promotion/'"></coupons-promotion>
        <div v-else-if="menuList['coupons']" class="content">
            <div class="main-title">
                <div class="main-title__wrap main-title--nowrap">
                    <div class="h1">{{pageTitle}}</div>
                </div>

                <div class="main-title__right --icon">
                    <div class="pb2" >
                        <span class="i i-info i--dark-trans"></span>
                        <span class="dib t text-color-grey">{{getCurDescr}}</span>
                    </div>
                </div>
            </div>

            <section class="container">
                <coupon-list v-if="menuList['coupons']"/>
            </section>
        </div>
    </div>

</template>


<script>
    import {mapState} from 'vuex';
    import CouponList from '@/components/moderator/CouponList.vue'
    import CouponsPromotion from '@/components/moderator/CouponsPromotion.vue'

    export default {
        metaInfo() {
            return {
                title: this.pageTitle,
                titleTemplate: this.$store.state.options.titleTemplate
            }
        },
        name: "Coupons",
        components: {
            CouponList,
            CouponsPromotion
        },
        computed: {
            ...mapState({
                menuList: state => state.menus.itemsAction,
                counters: state => state.moderatorCoupon.counters,
            }),
            defaultStatus() {
                return this.menuList['coupons'] ?this.menuList['coupons'][0]['code']:'moderation';
            },
            pageTitle() {
                if(this.$route.path === '/moderator/coupons/promotion/'){
                    return 'Продвижние акций';
                } else {
                    let action = this.$route.params.action ? this.$route.params.action : this.defaultStatus;
                    return this.$store.getters['menus/ActionPageCouponTitle']({action:action, defaultStatus: this.defaultStatus});
                }
            },
            currentStatus() {
                return this.$route.params.action ? this.$route.params.action : this.defaultStatus;
            },
            getCurDescr(){
                return this.menuList['coupons'][this.getIndexItem(this.currentStatus)].descr;
            }
        },
        methods:{
            getIndexItem(action) {
                let menuList = this.menuList['coupons'],
                    result;

                for (let index in menuList) {
                    if (menuList[index].code == action) {
                        result = index;
                        break;
                    }
                }

                return result;
            },
        }
    }
</script>