<template>
    <div>
        <div class="popup__header popup__header--dotted ">
            <div class="title">Выписть счет на {{ number_format(totalCommission) }} <span class="rouble">{{ $store.getters['options/setRouble'] }}</span></div>
            <div class="subtitle">{{item.provider.ownership}}</div>
        </div>
        <div class="popup__content">
            <div class="h2 mb16">СМС бухгалтеру поставщика</div>
            <textarea-field @focus="form.smsError = false " :is-error="form.smsError" :placeholder="'Напишите текст СМС'" :hasCounterSMS="true" v-model="form.smsText" ></textarea-field>
            
            <div class="h2 mb16">Письмо бухгалтеру поставщика</div>
            <div class="mb8">
                <input @focus="form.emailTitleError=false" :class="{error:form.emailTitleError}" :placeholder="'Тема письма'" type="text" class="inp size-s full" v-model="form.emailTitle">
            </div>
            <text-editor-min @error="form.emailTextError=false" :error="form.emailTextError" v-model="form.emailText" :placeholder="'Напишите текст письма'" :hide-toolbar="true" :value-default="form.emailText" />

            <div class="h2 mb16">Приложения к письму</div>
            <div>
                <div class="mt16"><input v-model="form.act_wo_stamp" type="checkbox" id="act_wo_stamp"/><label for="act_wo_stamp">Акт без печати</label></div>
                <!--<div class="mt16"><input v-model="form.bill_wo_stamp" type="checkbox" id="bill_wo_stamp"/><label for="bill_wo_stamp">Счет без печати</label></div>-->
                <div class="mt16"><input v-model="form.act_w_stamp" type="checkbox" id="act_w_stamp"/><label for="act_w_stamp">Акт c печатью</label></div>
                <div class="mt16"><input v-model="form.bill_w_stamp" type="checkbox" id="bill_w_stamp"/><label for="bill_w_stamp">Счет с печатью</label></div>
                <div class="mt16"><input v-model="form.agent_w_stamp" type="checkbox" id="agent_w_stamp"/><label for="agent_w_stamp">Отчет агента с печатью</label></div>
                <div class="mt16"><input v-model="form.agent_wo_stamp" type="checkbox" id="agent_wo_stamp"/><label for="agent_wo_stamp">Отчет агента с без печати</label></div>

            </div>



            <div class="row-b mt32">
                <div class="row-b__col elem-pos-fix mr24">
                    <submit-btn class="" @click.native="send" :type="form.btnType">Отправить</submit-btn>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
    import {mapState} from 'vuex';
    import {number_format} from '@/helpers/formatted.js';
    import TextareaField from '@/components/forms/TextareaField.vue'
    import TextEditorMin from '@/components/editor/TextEditorMin.vue'
    import SubmitBtn from '@/components/forms/SubmitBtn.vue'

    export default {
        components: {
            TextareaField,
            TextEditorMin,
            SubmitBtn
        },
        computed: {
            ...mapState({
                item: state => state.auditorProviders.itemDetail,
                templates: state => state.auditorProviders.templates,
                popups: state => state.popups.items,
            }),
            totalCommission () {
                return this.$store.getters['auditorProviders/getTotalCommission'];
            },
        },
        methods: {
            number_format: function (v) {
                return number_format(v, 0, '', ' ')
            },
            send: function(){
                let error = false;
                this.form.emailTitleError = false;
                this.form.emailTextError = false;
                this.form.smsError = false;

                /*if((this.form.emailTitle.length == 0) &&
                (this.form.emailText.length == 0) &&
                this.form.smsText.length == 0){
                    setTimeout(()=>{
                        this.form.emailTitleError = true;
                        this.form.emailTextError = true;
                        this.form.smsError = true;
                    },150);

                    error = true;     
                }*/
                if((this.form.emailTitle.length == 0) && this.form.emailText.length > 0){
                    this.form.emailTitleError = true;
                    setTimeout(()=>{
                        this.form.emailTextError = true;
                    },150);
                    error = true;  
                }
                if((this.form.emailTitle.length > 0) && this.form.emailText.length == 0){

                    setTimeout(()=>{
                        this.form.emailTextError = true;
                    },150);
                    error = true;
                }
                if(!error){
                    if(!this.formLoad){
                        this.formLoad = true;
                        this.form.commentError = false;
                        this.form.btnType = 'load';
                        this.$store.dispatch('auditorProviders/sendForm', {
                            type: 'docs',
                            id: this.item.provider.id,
                            smsText: this.form.smsText,
                            emailTitle: this.form.emailTitle,
                            emailText: this.form.emailText,
                            act_wo_stamp: this.form.act_wo_stamp,
                            bill_wo_stamp: this.form.bill_wo_stamp,
                            act_w_stamp: this.form.act_w_stamp,
                            bill_w_stamp: this.form.bill_w_stamp,
                            agent_w_stamp: this.form.agent_w_stamp,
                            agent_wo_stamp: this.form.agent_wo_stamp,
                        }).then(()=>{
                            this.form.formLoad = false;
                            this.form.btnType = 'ok';
                            setTimeout(()=>{
                                this.$store.commit('popups/close', {name: this.popups.auditorBill.name});
                                this.$store.dispatch('popups/open', {name: this.popups.billSendOk.name});
                                //this.$store.dispatch('moderatorCooperation/getItem',{id:this.item.id});
                                this.$store.dispatch('auditorProviders/getItem', {id: this.item.provider.id, reload: true});
                            }, 300);
                        });
                    }
                }
            },
        },
        data: function(){
            return {
                form:{
                    commentError: false,
                    smsText: '',
                    emailTitle: '',
                    emailText: '',
                    formLoad: false,
                    btnType: 'normal',
                    emailTitleError: false,
                    emailTextError: false,
                    smsError: false,
                    act_wo_stamp: false,
                    bill_wo_stamp: false,
                    act_w_stamp: false,
                    bill_w_stamp: false,
                    agent_w_stamp: false,
                    agent_wo_stamp: false,
                }
            }
        },
        mounted() {
            this.form.smsText = this.templates.sendBillSMS.text;
            this.form.emailTitle = this.templates.sendBillEmail.title;
            this.form.emailText = this.templates.sendBillEmail.text;
        },
        beforeDestroy(){
            this.$store.commit('popups/close', {name: this.popups.auditorBill.name}); 
        }
    }
</script>