<template>
    <div style="position: relative;" id="coupon-tags" class="full">
        <div class="pseudo-textarea textarea-tags"
                 placeholder="После ввода каждого тега нажмите ENTER"
                 :class="{error:error, disabled: tags.disabled}"
                 @click="focusCouponTagsInput();"
                 style="min-height: 136px; ">
            <span v-for="(tag, index) in value"
                        :key="index"
                        class="tag">
                    <span class="text">{{ tag }}</span>
                    <span class="del"
                                @click="deleteTag(index)"
                    ></span>
            </span>
            
            
            <span style="position: absolute;z-index: 99;top: 11px;left: 9px;color: rgba(84,84,84,0.5);" v-if="tags.valueText.length == 0 && value.length == 0">Например, «Электрика» или «Пластиковые окна». После ввода каждого тэга нажмите Enter.</span>
            <input maxlength="20" @keyup.enter="addTag()"
                         @keyup.delete="deleteLastTag()"
                         :style="tags.style"
                         @input="styleCouponTagsInput"
                         @focus="focus()"
                         @blur="blur()"
                         v-model="tags.valueText"
                         ref="couponTagsInput"
                         type="text"
                         class="textarea-tags__input" v-if="value.length < 10">
            <span ref="couponTagsInputBuffer" class="inp-buffer" v-html="tags.valueBuffer"></span>
        </div>
    </div>
    
</template>

<script>
    export default {
        name: "TagEditor",
        props: {
            value: {
                default(){
                    return [];
                }
            },
            error: {
                default: false,
            },
        },
        data: function () {
            return {
                tags: {
                    valueText: '',
                    valueTextPrevLen: 0,
                    valueBuffer: '',
                    error: false,
                    disabled: false,
                    type: 'text',
                    required: false,
                    style: {},
                    scrollTo: '#coupon-tags',
                },
            }
        },
        computed: {

        },
        created() {

        },
        mounted() {
        },
        methods: {
            focus() {
                this.$emit("focus", false);
            },
            blur() {
                this.$emit("blur", false);
            },
            addTag: function () {
                let tags = [...this.value];
                if (this.tags.valueText.trim().length > 0) {
                    tags.push(this.tags.valueText.trim().replace(',', ''));
                }
                this.$set(this.tags, 'valueText', '');
                this.$set(this.tags, 'valueBuffer', '');
                this.$nextTick(function () {
                    this.$set(this.tags, 'style', {width: '3px'});
                });
                this.tags.valueTextPrevLen = 0;
                this.$emit('input', tags)
            },
            deleteTag: function (index) {
                let tags = [...this.value];
                if (tags.hasOwnProperty(index)) {
                    tags.splice(index, 1);
                }
                this.$emit('input', tags)
            },
            deleteLastTag: function () {
                let tags = [...this.value];
                if (this.tags.valueText.length === 0 && this.tags.valueTextPrevLen === 0) {
                    tags.splice(tags.length -1, 1);
                }
                this.tags.valueTextPrevLen = this.tags.valueText.length;
                this.$emit('input', tags)
            },
            focusCouponTagsInput: function () {
                this.$nextTick(function () {
                    this.$refs.couponTagsInput.focus()
                });
            },
            styleCouponTagsInput: function () {
                if(this.tags.valueText.substr(-1,1) === ','){
                    this.addTag();
                } else {
                    this.$set(this.tags, 'valueBuffer', this.tags.valueText.replace(/ /ig, '&nbsp;'));
                    this.$nextTick(function () {
                        let width = this.$refs.couponTagsInputBuffer.offsetWidth;
                        this.$set(this.tags, 'style', {width: (width + 13) + 'px'});
                    });
                }
            },
        }
    }
</script>

