 <template>
    <div>
        <div class="popup__header popup__header--dotted --bg-red">
            <div class="title">Заблокировать акцию</div>
            <div class="subtitle">{{item.name}}</div>
        </div>
        <div class="popup__content">
            <div class="h2 mb16">Служебный комментарий</div>
            <textarea-field @focus="removeError('commentError')" v-model="form.comment" :is-error="form.commentError" placeholder="Прокомментируйте своё решение" :add-style="{minHeight: 88 + 'px'}"></textarea-field>

            <div class="h2 mb16">СМС поставщику на {{this.templates.ban_sms.recipient}}</div>
            <textarea-field :hasCounterSMS="true" v-model="form.smsText" ></textarea-field>

            <div class="h2 mb16">Письмо поставщику на {{this.templates.ban_email.recipient}}</div>
            <div class="mb8">
                <input type="text" class="inp size-s full" v-model="form.emailTitle">
            </div>
            <text-editor-min v-model="form.emailText" :hide-toolbar="true" :value-default="form.emailText" />

            <div class="h2 mb16">Публичная причина блокировки</div>

            <div class="mt16">
                <input type="text" class="inp size-s full" @focus="removeError('reasonBanError')" :class="{'error':form.reasonBanError}" v-model="form.reasonBan"  placeholder="Например, «Мошенничество с купонами»...">
            </div>

            <div class="h2 mb16">Неиспользованные купоны <span class="text-color-grey">{{item.statistics.valid}} шт.</span></div>
            <template v-if="item.statistics.valid>0">
                <div class="mt16 text-color-grey text-size-s">Если отношения с поставщиком после блокировки испортятся, то купоны, находящиеся у покупателей,<br>
                    лучше аннулировать. Во всех остальных случаях купоны лучше оставить как есть...</div>

                <div class="h2 mb16">СМС покупателям</div>
                <textarea-field :hasCounterSMS="true" v-model="form.smsCouponText" ></textarea-field>
            </template>

            <div class="row-b mt32">
                <div class="row-b__col elem-pos-fix mr24">
                    <submit-btn class="btn--red" @click.native="send" :type="form.btnType">Подтвердить</submit-btn>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
    import {mapState} from 'vuex'
    import TextareaField from '@/components/forms/TextareaField.vue'
    import TextEditorMin from '@/components/editor/TextEditorMin.vue'
    import SubmitBtn from '@/components/forms/SubmitBtn.vue'

    export default {
        name: "openCouponBlock",
        components: {
            TextareaField,
            TextEditorMin,
            SubmitBtn
        },
        methods: {
            send(){
                let error = false;
                this.form.commentError = false;
                this.form.reasonBanError = false;
                if (this.form.comment.length<10) {
                    setTimeout(()=>{
                        this.form.commentError = true;
                    },150);

                    error = true;
                }
                if (this.form.reasonBan.length<10) {
                    setTimeout(()=>{
                        this.form.reasonBanError = true;
                    },150);

                    error = true;
                }
                if(!error) {
                    if(!this.formLoad){
                        this.formLoad = true;
                        this.form.commentError = false;
                        this.form.btnType = 'load';
                        this.$store.dispatch('moderatorCoupon/sendForm', {
                            type: 'ban',
                            id: this.item.id,
                            comment: this.form.comment,
                            reasonBan: this.form.reasonBan,
                            smsText: this.form.smsText,
                            emailTitle: this.form.emailTitle,
                            emailText: this.form.emailText,
                            smsCouponText: this.form.smsCouponText,
                        }).then(()=>{
                            this.form.formLoad = false,
                            this.form.btnType = 'ok';
                            setTimeout(()=>{
                                this.$store.commit('popups/close', {name: this.popups.moderatorCouponBlock.name});
                                this.$router.push('/moderator/coupons/' + this.actionBackCode + '/');
                            }, 300);
                        });
                    }
                }
            },
            removeError(field) {
                this.form[field] = false;
            }
        },
        data: function(){
            return {
                form:{
                    comment: '',
                    commentError: false,
                    reasonBan: '',
                    reasonBanError: false,
                    smsText: '',
                    smsCouponText: '',
                    emailTitle: '',
                    emailText: '',
                    formLoad: false,
                    btnType: 'normal'
                }
            }
        },
        mounted() {
            this.form.smsText = this.templates.ban_sms.text;
            this.form.smsCouponText = this.templates.ban_coupon_sms.text;
            this.form.emailTitle = this.templates.ban_email.title;
            this.form.emailText = this.templates.ban_email.text;
        },
        computed: {
            ...mapState({
                item: state => state.moderatorCoupon.itemDetail,
                popups: state => state.popups.items,
                templates: state=> state.moderatorCoupon.itemDetailTemplates,
                actionListStatus: state => state.moderatorCoupon.listStatus,
                actionMenuList: state => state.menus.itemsAction,
            }),
            actionBackId() {
                return this.actionListStatus ? this.actionListStatus : this.item['status'];
            },
            actionBackCode() {
                let statusId = this.actionListStatus ? this.actionListStatus : this.item.status_moderator;

                for (let itemIndex in this.actionMenuList['coupons']) {
                    if (this.actionMenuList['coupons'][itemIndex]['id'] == statusId) {
                        return this.actionMenuList['coupons'][itemIndex]['code'];
                    }
                }
            },
        },
        beforeDestroy(){
            this.$store.commit('popups/close', {name: this.popups.moderatorCouponBlock.name});
        }
    }
</script>