<template>
    <div>
        <div class="popup__header popup__header--dotted" :class="['--bg-'+bill.color]">
            <div class="title">Счёт #{{ bill.number }} от {{ bill.dateExposed }} на сумму {{ number_format(totalCommission) }} <span class="rouble">{{ $store.getters['options/setRouble'] }}</span></div>
            <div class="subtitle">{{item.provider.ownership}}</div>
            <div class="subtitle --icon">
                <span class="i --big" :class="['i-']+bill.icon"></span>
                <span class="t">{{ bill.status }}</span>
            </div>
        </div>
        
        
        <div class="popup__content" :style="{'padding-left': 24 + 'px', 'padding-right': 24 + 'px'}">
            <div class="buh-coupons" v-if="bill.coupons.length > 0">
                <div class="item">
                    <div class="item__title">
                        <span class="item__buyer">покупатель</span>
                        <span class="item__paid">погашен</span>
                        <span class="item__sum">сумма покупки</span>
                        <span class="item__discount">скидка</span>
                        <span class="item__transfer">поступило в кассу</span>
                        <span class="item__percent">комиссия 3%</span>
                    </div>
                </div>


                <div class="item" v-for="coupon in bill.coupons">
                    <div class="item__link">
                        <span class="item__buyer">{{ coupon.phone }}</span>
                        <span class="item__paid">{{ coupon.date.pay }}</span>
                        <span class="item__sum">{{ coupon.price }} <span class="rouble">{{ $store.getters['options/setRouble'] }}</span></span>
                        <span class="item__discount">{{ coupon.discount }} <span class="rouble">{{ $store.getters['options/setRouble'] }}</span></span>
                        <span class="item__transfer">{{ coupon.transfer }} <span class="rouble">{{ $store.getters['options/setRouble'] }}</span></span>
                        <span class="item__percent">{{ coupon.our }} <span class="rouble">{{ $store.getters['options/setRouble'] }}</span></span>
                    </div>
                </div>

                <div class="item" v-if="bill.coupons.length > 1">
                    <div class="item__total">
                        Общая комиссия за <strong>{{ bill.coupons.length }}</strong> {{declOfNumber(bill.coupons.length,['купон','купона','купонов'])}}: <strong>{{ number_format(totalCommission) }} <span class="rouble">{{ $store.getters['options/setRouble'] }}</span></strong>
                    </div>
                </div>
            </div>
            <!-- end buh-coupons -->
            <div v-if="bill.statusCode == 'ok'">
                <div class="hr --scissors --wide"></div>
                <template v-if="bill.history">
                    <div v-for="history in bill.history" :key="history.id" data-v-7e2cd7d0="" class="mt16">
                        <div v-html="history.text"></div>
                        <div class="mt8 text-color-grey text-size-xs">{{history.date}}, Бухгалтер: {{history.user}}</div>
                    </div>
                </template>
                <div class="mt21" v-if="!bill.history && bill.comment">
                    <div class="h2 mt0 mb16">Служебный комментарий</div>
                    <p>
                        {{bill.comment}}<br>
                        <strong v-if="bill.user_comment">{{bill.user_comment}}</strong>
                    </p>
                </div>
                <div class="row-b mt32">
                    <div class="row-b__col elem-pos-fix mr24">
                        <div class="row-b__col">
                            <button @click="close(popups.buhPartnerBill.name)" class="btn btn--white">Назад</button>
                        </div>
                    </div>
                    <div class="row-b__col elem-pos-fit pt2 dowload-link">
                        <a href="javascript:void(0);" @click="downloadBill(bill.id)" class="link-icon text-size-s">
                            <span class="i i-file"></span>
                            <span class="text">Скачать документы</span>
                        </a>
                    </div>
                </div>
            </div>
            <div  v-if="bill.statusCode == 'wait'">
                <div class="hr --scissors --wide"></div>
                <div class="mt21">
                    <div class="h2 mt0 mb16">Служебный комментарий</div>
                    <textarea-field :add-style="{height: 80 + 'px'}" @focus="form.commentError = false " :placeholder="'Текст комментария'"  v-model="form.comment" :is-error="form.commentError"></textarea-field>
                </div>
                <div class="row-b mt32">
                    <div class="row-b__col elem-pos-fix mr24">
                        <!--<button @click="submit('pay')" class="btn">Отметить оплаченным</button>-->
                        <submit-btn @click.native="submit('pay')" :type="form.btnType">Отметить оплаченным</submit-btn>
                    </div>
                    <div class="row-b__col elem-pos-fit pt2">
                        <a @click="submit('ungroup')" class="link-grey">Расформировать</a>
                    </div>
                    <div class="row-b__col elem-pos-fit pt2 dowload-link">
                        <a href="javascript:void(0);" @click="downloadBill(bill.id)" class="link-icon text-size-s">
                            <span class="i i-file"></span>
                            <span class="text">Скачать документы</span>
                        </a>
                    </div>
                </div>
            </div>
            <div v-if="bill.statusCode == 'pay'">
                <div class="hr --scissors --wide"></div>
                    <template v-if="bill.history">
                        <div v-for="history in bill.history" :key="history.id" data-v-7e2cd7d0="" class="mt16">
                            <div v-html="history.text"></div>
                            <div class="mt8 text-color-grey text-size-xs">{{history.date}}, Бухгалтер: {{history.user}}</div>
                        </div>
                    </template>
                <div class="mt32">
                    <div class="h2 mt0 mb16">Служебный комментарий</div>
                    <textarea-field :add-style="{height: 80 + 'px'}" @focus="form.commentError = false " :placeholder="'Текст комментария'"  v-model="form.comment" :is-error="form.commentError"></textarea-field>
                </div>
                <div class="mt32">
                    <input @click="form.checkedError = false " :class="{error: form.checkedError}" type="checkbox" id="originals" v-model="form.checked"/>
                    <label :class="{error: form.checkedError}" for="originals">Оригиналы документов получены</label>
                </div>
                <div class="row-b mt32">
                    <div class="row-b__col elem-pos-fix mr24">
                        <div class="row-b__col">
                            <!--<button @click="submit('confirm_docs')" class="btn">Погасить счёт</button>-->
                            <submit-btn @click.native="submit('confirm_docs')" :type="form.btnType">Погасить счёт</submit-btn>
                        </div>
                    </div>
                    <div class="row-b__col elem-pos-fit pt2 dowload-link">
                        <a href="javascript:void(0);" @click="downloadBill(bill.id)" class="link-icon text-size-s">
                            <span class="i i-file"></span>
                            <span class="text">Скачать документы</span>
                        </a>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import {mapState} from 'vuex'
    import {number_format} from '@/helpers/formatted.js';
    import {declOfNumber} from '@/helpers/formatted.js';
    import TextareaField from '@/components/forms/TextareaField.vue'
    import TextEditorMin from '@/components/editor/TextEditorMin.vue'
    import SubmitBtn from '@/components/forms/SubmitBtn.vue'

    export default {
        name: "AuditorPartnerBill",
        components: {
            TextareaField,
            TextEditorMin,
            SubmitBtn
        },
        props: {
            bill: {},
        },
        data: function(){
            return {
                form:{
                    comment: '',
                    commentError: false,
                    checked: false,
                    checkedError: false,
                    type: '',
                    formLoad: false,
                    btnType: 'normal'
                    /*emailTitle: '',
                    emailText: '',
                    formLoad: false,
                    btnType: 'normal'*/
                }
            }
        },
        methods: {
            close: function (popup) {
                this.$store.commit('popups/close', {name: popup});
            },
            submit: function(type){
                let error = false;
                if(type == 'confirm_docs' || type == 'pay'){
                    this.form.commentError = false;
                    this.form.checkedError = false;

                    if(this.form.comment.length < 10){
                        setTimeout(()=>{
                            this.form.commentError = true;
                        },150);

                        error = true;  
                    }
                }
                if(type == 'confirm_docs'){
                    if(this.form.checked == false){
                        setTimeout(()=>{
                            this.form.checkedError = true;
                        },150);

                        error = true;
                    }
                }
                if(!error){
                    if(!this.formLoad){
                        this.form.btnType = 'load';
                        this.formLoad = true;
                        this.$store.dispatch('auditorProviders/sendForm', {
                            type: type,
                            id: this.bill.id,
                            comment: this.form.comment,
                            /*sendTo: this.sendTo,
                            smsText: this.form.sms.text,
                            emailText: this.form.emailText.text,
                            emailTitle: this.form.emailTitle.text, */
                        }).then(()=>{
                            this.form.btnType = 'ok';
                            this.form.formLoad = false;
                            setTimeout(()=>{
                                this.$store.commit('popups/close', {name: this.popups.buhPartnerBill.name});
                                this.$store.dispatch('auditorProviders/getItem', {id: this.item.provider.id, reload: true});
                            }, 300);
                        });   
                    }
                }
            },
            number_format: function (v) {
                return number_format(v, 0, '', ' ')
            },
            declOfNumber(number, titles){
                return declOfNumber(number, titles);
            },
            downloadBill(id) {
                let token = (localStorage.getItem('token') !== 'false' && localStorage.getItem('token') !== null && localStorage.getItem('token') !== undefined) ? localStorage.getItem('token') : false;
                window.open(process.env.VUE_APP_API_PATH+'/auditor/bill_docs/'+id+'?token='+token, '_blank');
            },
        },
        computed: {
            token(){
                return this.localStorage.token !== 'false' && this.localStorage.token !== null && this.localStorage.token !== undefined ? this.localStorage.token : false
            },
            ...mapState({
                popups: state => state.popups.items,
                item: state => state.auditorProviders.itemDetail,
            }),
            totalCommission () {
                let sum = 0;
                for(let item of this.bill.coupons) {
                    sum += parseInt(item.our.replace(' ',''));
                }
                return sum;
            },
        },
        beforeDestroy(){
            this.close(this.popups.buhPartnerBill.name);    
        }
    }
</script>

<style scoped>
  .mt21{
      margin-top: 21px !important;
  }
  .dowload-link{
      justify-content: flex-end;
  }
  .dowload-link .i-file{
      transform: scale(0.75);
  }
</style>