<template>
    <ul v-if="Object.keys(categories).length > 0" class="moderator-nav-list" :style="{display: hidden}" >
        <li class="moderator-nav-list-item" v-for="cat in categories" :key="cat.id">
            <a href="#" class="moderator-nav-list-item-link open" :class="{active: isActive(cat.id), dropdown: cat.sections.length>0}"><span>{{cat.name}} <span v-if="!cat.isActive" class="dis i i-eye2"></span></span><span class="num">{{cat.count}}</span></a>
            <categories-coupon @setInvisable="setInvisable" v-if="cat.sections.length>0" :selected="selected" :categories="cat.sections"></categories-coupon>
        </li>
    </ul>
</template>
<script>
    import {mapState} from 'vuex';
    import CategoriesCoupon from '@/components/moderator/CategoriesCoupon.vue'

    export default {
        name: "CategoriesCoupon",
        props: ["selected","categories","firstLevel"],
        components:{
            CategoriesCoupon
        },
        data: function () {
            return {
                invisable: false,
            }
        },
        computed: {
            /*...mapState({
                categories: state => state.moderatorCoupon.categories,
            }),*/
            hidden: function () {
                let hide = true,
                    hasChildren = false;
                this.categories.map((value, key)=>{
                    if(!value.isActive && this.isActive(value.id)){
                        hide = false;
                    }
                    if(value.sections.length>0){
                        hasChildren = true;
                    }
                });

                if(!this.firstLevel){
                    if((!hasChildren && hide) ||  (this.invisable && hide) ){
                        this.$emit('setInvisable');
                        return 'none';
                    }
                }
                else{
                    return 'block';
                }
            }
        },
        methods:{
            isActive(id){
                return this.selected.includes(id);
            },
            setInvisable(){
                this.invisable = true;
            }
        },
        mounted(){
            /*let hide = true;
            this.categories.map(function(value, key){
                console.log(value.name);
                if(!value.isActive && this.isActive(value.id)){
                    hide = false;
                }
            });
            if(hide){
                this.$emit('hideList');
            }*/
        }
    }
</script>
<style scoped>
.nav-list{
    display:block;
}
.nav-list-item .hidden{
    display:none !important;
}
</style>
