<template>
    <div>
        <div class="popup__header popup__header--dotted --bg-green">
            <div class="title">Продлить купон на {{form.days.text}} дн.</div>
            <div class="subtitle">{{name}}</div>
        </div>


        <div class="popup__content">
            <div class="h2">Прокомментируйте своё решение</div>

            <div class="mt16">
                <textarea-field
                        :is-error="form.comment.error"
                        @focus="form.comment.error = false"
                        :auto-focus="true"
                        v-model="form.comment.text"
                        :placeholder="'Например, «Собирается поехать к поставщику в ближайшие выходные...»'"
                        :add-style="{minHeight: 65 + 'px', 'height': 'auto'}"
                ></textarea-field>
            </div>


            <div class="mt24">
                <input type="checkbox" id="checkAutoAnnul" v-model="form.checkAutoAnnul">

                <label for="checkAutoAnnul">
                    Автоматически аннулировать через
                    <input type="number"
                           class="inp size-xs ml4 mr4"
                           :class="{error: form.days.error}"
                           @focus="form.days.error = false"
                           style="width: 28px; margin-top: -1px; text-align: center;"
                           v-model="form.days.text">
                    дн.
                </label>
            </div>


            <div class="row-b mt24">
                <div class="row-b__col elem-pos-fix mr24">
                    <submit-btn @click.native="send" :type="form.btnType" class="btn--green">Подтвердить</submit-btn>
                </div>


                <div class="row-b__col elem-pos-fit">
                    <div class="text-color-grey"></div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import TextareaField from '@/components/forms/TextareaField.vue'
    import {mapState} from 'vuex';
    import SubmitBtn from '@/components/forms/SubmitBtn.vue';

    export default {
        name: "CouponExtend",
        components: {
            TextareaField,
            SubmitBtn
        },
        props: ['id', 'name', 'annul'],
        data: function () {
            return {
                form: {
                    checkAutoAnnul: false,
                    btnType: 'normal',
                    type: 'extend',
                    days: {
                        text: 15,
                        error: false
                    },
                    comment: {
                        text: '',
                        error: false
                    }
                }
            }
        },
        mounted() {
            this.$set(this.form.days, 'text', this.annul);
        },
        computed: {
            ...mapState({
                popups: state => state.popups.items,
                //item: state => state.moderatorRepayment.itemDetail,
                items: state => state.moderatorRepayment.detailItems,
            }),
        },
        methods: {
            send: function () {
                let error = false,
                    days = 0;
                this.form.comment.error = false;
                this.form.days.error = false;
                if (this.form.comment.text.length < 10) {
                    error = true;
                    setTimeout(()=>{
                        this.form.comment.error = true;
                    },150);
                }

                if (this.form.checkAutoAnnul && !this.form.days.text) {
                    error = true;
                    setTimeout(()=>{
                        this.form.days.error = true;
                    },150);
                }

                if (this.form.checkAutoAnnul) {
                    days = this.form.days.text
                }

                if (!error) {
                    this.form.btnType = 'load';
                    this.$store.dispatch('moderatorRepayment/sendForm', {
                        type: this.form.type,
                        id: this.id,
                        comment: this.form.comment.text,
                        days: days,
                    }).then(() => {
                        this.form.btnType = 'ok';
                        
                        let apply = true;
                        for (let ind in this.items) { 
                            if(this.items[ind].status == 4 && this.items[ind].is_extend!=true && this.id!=this.items[ind].id){
                                apply = false;
                            }
                        }
                        setTimeout(() => {
                            this.$store.dispatch('moderatorRepayment/initDetailPage', {route: this.$route}).then(() => {});
                            this.$emit('close', {apply: apply});
                        }, 300);
                    });
                }
            }
        },
        beforeDestroy() {
            this.$store.commit('popups/close', {name: this.popups.couponExtend.name});
        }
    }
</script>

<style scoped>
    .inp.size-xs {
        padding-left: 4px;
        padding-right: 4px;
        border-width: 1px;
        border-color: rgba(0, 0, 0, 0.2);
    }

    .inp.size-xs.error {
        border-color: #DB5A5A;
        color: #DB5A5A;
    }

    [type="checkbox"] + label:before {
        top: 2px;
    }

    [type="checkbox"] + label:after {
        top: 7px;
    }
</style>