<template>
    <div>
        <div class="popup__header popup__header--dotted --bg-red">
            <div class="title">Отклонить заявку</div>
            <div class="subtitle">{{item.direction}}</div>
        </div>


        <div class="popup__content">

            <div class="h2 mb16">Служебный комментарий</div>

            <textarea-field @focus="removeError()" v-model="form.comment" :is-error="form.commentError"
                            :placeholder="'Прокомментируйте своё решение'"
                            :add-style="{minHeight: 88 + 'px'}"></textarea-field>

            <div class="h2 mb16">СМС поставщику на {{this.templates.reject_sms.recipient}}</div>

            <textarea-field :placeholder="'Напишите текст СМС'" :hasCounterSMS="true"
                            v-model="form.smsText"></textarea-field>


            <div class="h2 mb16">Письмо поставщику на {{this.templates.reject_email.recipient}}</div>

            <div class="mb8">
                <input :placeholder="'Тема письма'" type="text" class="inp size-s full" v-model="form.emailTitle">
            </div>

            <text-editor-min :placeholder="'Напишите текст письма'" v-model="form.emailText" :hide-toolbar="true"
                             :value-default="form.emailText"/>


            <div class="row-b mt32">
                <div class="row-b__col elem-pos-fix mr24">
                    <submit-btn class="btn--red" @click.native="send" :type="form.btnType">Подтвердить</submit-btn>
                </div>


                <div class="row-b__col elem-pos-fit">
                    <div class="text-color-grey">Если вы не хотите отправлять поставщику письмо или СМС,<br>
                        просто оставьте соответствующее поле пустым...
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import {mapState} from 'vuex'
    import TextareaField from '@/components/forms/TextareaField.vue'
    import TextEditorMin from '@/components/editor/TextEditorMin.vue'
    import SubmitBtn from '@/components/forms/SubmitBtn.vue'

    export default {
        name: "RequestReject",
        components: {
            TextareaField,
            TextEditorMin,
            SubmitBtn
        },
        methods: {
            send: function () {
                this.form.commentError = false;
                if (this.form.comment.length < 10) {
                    setTimeout(()=>{
                        this.form.commentError = true;
                    },150);
                } else {
                    if (!this.formLoad) {
                        this.formLoad = true;
                        this.form.commentError = false;
                        this.form.btnType = 'load';
                        this.$store.dispatch('moderatorCooperation/sendForm', {
                            type: 'reject',
                            id: this.item.id,
                            comment: this.form.comment,
                            smsText: this.form.smsText,
                            emailTitle: this.form.emailTitle,
                            emailText: this.form.emailText,
                        }).then(() => {
                            this.form.formLoad = false,
                                this.form.btnType = 'ok';
                            setTimeout(() => {
                                this.$store.commit('popups/close', {name: this.popups.requestReject.name});
                                this.$router.push('/moderator/cooperation/' + this.cooperationBackCode + '/');
                            }, 300);
                        });
                    }
                }
            },
            removeError() {
                this.form.commentError = false;
            }
        },
        data: function () {
            return {
                form: {
                    comment: '',
                    commentError: false,
                    smsText: '',
                    emailTitle: '',
                    emailText: '',
                    formLoad: false,
                    btnType: 'normal'
                }
            }
        },
        mounted() {
            this.form.smsText = this.templates.reject_sms.text;
            this.form.emailTitle = this.templates.reject_email.title;
            this.form.emailText = this.templates.reject_email.text;
        },
        computed: {
            ...mapState({
                popups: state => state.popups.items,
                item: state => state.moderatorCooperation.itemDetail,
                templates: state => state.moderatorCooperation.itemDetailTemplates,
                cooperationListStatus: state => state.moderatorCooperation.listStatus,
                actionMenuList: state => state.menus.itemsAction,
            }),
            requestBackId() {
                return this.cooperationListStatus ? this.cooperationListStatus : this.item['status'];
            },
            cooperationBackCode() {
                let statusId = this.cooperationListStatus ? this.cooperationListStatus : this.item['status'];

                for (let itemIndex in this.actionMenuList['cooperation']) {
                    if (this.actionMenuList['cooperation'][itemIndex]['id'] == statusId) {
                        return this.actionMenuList['cooperation'][itemIndex]['code'];
                    }
                }
            },
        },
        beforeDestroy() {
            this.$store.commit('popups/close', {name: this.popups.requestReject.name});
        }
    }
</script>

