<template>
    <div class="content">
        <template v-if="providerLoading">
            <div class="preloader-page">
                <div class="preloader"></div>
            </div>
        </template>
        <template v-else>
            <div class="main-title">
                <div class="main-title__wrap main-title--nowrap">
                    <div class="h1"><span v-html="item.name"></span><span class="ml8 text-size-s text-normal text-color-grey">{{item.ownership}}</span></div>
                </div>
                <div class="main-title__right">
                    <!-- г. {{item.cityName}} -->
                </div>
            </div>



            <section class="container">

                <tabs-outer
                        ref="tabs"
                        @changed="changeTab"
                        class="coupon-details --wide"
                        :data="{isProviderDetail: true, statusProvider: item.statusCode, contractLink: item.contractLink, contractNumber: item.contract.number}"
                        @popup="openPopup"
                >
                    <section class="container__left">
                        <div>
                            <tab :suffix="moderateAboutSuffix" id="tab-about" name="О поставщике" :is-disabled="false">
                                <provider-about class="coupon-details wrapper-content"
                                    :partner="item"
                                    :providerId="item.id"
                                    :providerName="item.companyName"
                                    :requisitesArr="item.requisitesArr"
                                    :inn="item.inn"
                                    :is-providers="true"
                                    :ogrn="item.ogrn"
                                    :ogrnip="item.ogrnip"
                                    :innOgrnCheck="true"
                                    :requisites="item.requisites"
                                    :site="item.site"
                                    :canEditText="true"
                                    :text="item.about"></provider-about>
                            </tab>

                            <tab id="tab-actions" class="coupon-list mt0" name="Акции" :is-disabled="false">
                                <catalog-list-item v-for="coupon in item.coupons"
                                                   :item="coupon"
                                                   :isModerator="true"
                                                   :key="coupon.id"
                                                    v-if="item.coupons.length > 0">
                                </catalog-list-item>

                                <div class="sect-blank wrapper-content" v-if="item.coupons.length === 0">
                                    <div class="wrap text-center">
                                        <div class="text-size-l text-bolder text-color-grey mt16">Здесь пока ничего нет...</div>
                                    </div>

                                    <div class="mini-zems"></div>
                                </div>
                            </tab>

                            <tab id="tab-stat" name="Статистика" :is-disabled="false">
                                <provider-stat :item="item"></provider-stat>
                            </tab>

                            <tab id="tab-history" name="История" :is-disabled="false">
                                <provider-history :item="item"></provider-history>
                            </tab>

                            <tab id="tab-request" name="Первичная заявка" :is-disabled="false">
                                <provider-request :item="item"></provider-request>
                            </tab>
                        </div>
                        <!-- end wrapper-content -->
                    </section>
                    <!-- end container__left -->


                    <aside class="aside aside--right">
                        <div class="aside__sticky js-sticky">
                            <div class="provider-request-contacts">
                                <div class="wrap" style="padding-bottom: 20px; ">
                                    <div class="item">
                                        <div class="t">
                                            <span class="t__t">Регион</span>
                                            <span class="t__d" :class="{'text-color-red': !item.city_is_worktime}">{{item.city_time}}</span>
                                        </div>
                                        
                                        <div v-if="item.cityName" class="d text-bold">
                                            {{item.cityName}}
                                        </div>
                                        <div v-else class="d text-bold">
                                            Не указан
                                        </div>
                                    </div>
                                </div>
                                
                                <div class="wrap">
                                    <div class="item">
                                        <div class="t" v-html="item.position"></div>
                                        <div class="d text-bold mb8">{{ providerName }}</div>
                                        
                                        <div class="d text-bold">
                                            <a class="text-bold" :href="'tel:'+item.phone.replace(/[^\+0-9]/g, '')">{{ item.phone }}</a>
                                            <a @click.prevent="openSendPopup" href="" class="d__send i i-mail2" title="Написать поставщику"></a>
                                        </div>
                                    </div>

                                    <!-- email убрали из дизайна
                                    <div class="item">
                                        <div class="t">E-mail</div>
                                        <div class="d text-bold">
                                            {{item.email}}
                                        </div>
                                    </div> -->
                                </div>
                                
                                
                                <div class="wrap">
                                    <div class="item">
                                        <div class="t">Бухгалтер</div>
                                        <div class="d mb8 text-bold" v-if="item.bookerName" v-html="item.bookerName"></div>
                                        <div class="d text-bold mb8" v-else>
                                            <span class="text-color-grey">Нет данных</span>
                                        </div>
                                        
                                        <div v-if="item.bookerPhone" class="d text-bold">
                                            <a class="text-bold" :href="'tel:'+item.bookerPhone.replace(/[^\+0-9]/g, '')">{{ item.bookerPhone }}</a>
                                            <a @click.prevent="openSendBuhPopup" href="" class="d__send i i-mail2" title="Написать бухгалтеру"></a>
                                        </div>
                                        
                                        <div v-else class="d text-bold">
                                            <span class="text-color-grey">Нет данных</span>
                                        </div>
                                    </div>
                                    
                                    <!-- email убрали из дизайна
                                    <div class="item">
                                        <div class="t">E-mail</div>
                                        <div class="d text-bold" v-if="item.bookerEmail" v-html="item.bookerEmail"></div>
                                        <div class="d text-bold" v-else>
                                            <span class="text-color-grey">Нет данных</span>
                                        </div>
                                    </div> -->
                                </div>
                                
                                <div class="wrap">
                                    <notes type="provider" :title="'Заметки о поставщике'" :entityId="item.id"></notes>
                                </div>
                            </div>
                        </div>
                    </aside>
                    <!-- end aside aside--right -->
                </tabs-outer>
            </section>
            <!-- end container -->
        </template>

        <!-- предварительно одобрить поставщика -->
        <popup :is-set-styled="true" :show="popups.providerApprove.show" :name="popups.providerApprove.name">
            <provider-approve></provider-approve>
        </popup>

        <!-- окончательно одобрить -->
        <popup :is-set-styled="true" :show="popups.providerFinalApprove.show" :name="popups.providerFinalApprove.name">
            <provider-final-approve></provider-final-approve>
        </popup>

        <!-- отклонить поставщика -->
        <popup :is-set-styled="true" :show="popups.providerReject.show" :name="popups.providerReject.name">
            <provider-reject></provider-reject>
        </popup>

        <!-- дополнительное время -->
        <popup :is-set-styled="true" :show="popups.providerOvertime.show" :name="popups.providerOvertime.name">
            <provider-overtime></provider-overtime>
        </popup>

        <!-- распечатать договор -->
        <popup :is-set-styled="true" :show="popups.providerPrint.show" :name="popups.providerPrint.name">
            <provider-print></provider-print>
        </popup>

        <!-- заблокировать -->
        <popup :is-set-styled="true" :show="popups.providerBlock.show" :name="popups.providerBlock.name">
            <provider-block></provider-block>
        </popup>

        <!-- отмена блокировки -->
        <popup :is-set-styled="true" :show="popups.providerCancelBlock.show" :name="popups.providerCancelBlock.name">
            <provider-cancel-block></provider-cancel-block>
        </popup>

        <!-- отмена отклонения -->
        <popup :is-set-styled="true" :show="popups.providerCancelReject.show" :name="popups.providerCancelReject.name">
            <provider-cancel-reject></provider-cancel-reject>
        </popup>

        <!-- отметить отправку договора -->
        <popup :is-set-styled="true" :show="popups.providerCheckSend.show" :name="popups.providerCheckSend.name">
            <provider-check-send></provider-check-send>
        </popup>

        <!-- письмо поставщику -->
        <popup :is-set-styled="true" :show="popups.sendMessagePartner.show" :name="popups.sendMessagePartner.name">
            <send-message-partner type="partner" send-to="ouner" :id="item.id"></send-message-partner>
        </popup>

        <!-- письмо бухгалтеру -->
        <popup :is-set-styled="true" :show="popups.sendMessagePartnerBuh.show" :name="popups.sendMessagePartnerBuh.name">
            <send-message-partner-buh type="partner" send-to="buh" :id="item.id"></send-message-partner-buh>
        </popup>

        <popup :show="popups.printContract.show" :name="popups.printContract.name" :isBtnClose="false">
            <print-contract @close="moveToReq"></print-contract>
        </popup>

        <contract-edit :show="popups.contractEdit.show"
                         :name="popups.contractEdit.name"></contract-edit>
    </div>
</template>
<script>
    import {mapState} from 'vuex'
    import ContractEdit from '@/components/forms/ContractEdit.vue'
    import Popup from '@/components/modal/Popup.vue'
    import TabsOuter from '@/components/tabs/TabsOuter.vue';
    import Tab from '@/components/tabs/Tab.vue';
    import Loader from '@/components/Loader.vue'
    import ProviderApprove from '@/components/modal/ProviderApprove.vue';
    import ProviderReject from '@/components/modal/ProviderReject.vue';
    import ProviderOvertime from '@/components/modal/ProviderOvertime.vue';
    import ProviderBlock from '@/components/modal/ProviderBlock.vue';
    import ProviderCancelBlock from '@/components/modal/ProviderCancelBlock.vue';
    import ProviderCancelReject from '@/components/modal/ProviderCancelReject.vue';
    import ProviderCheckSend from '@/components/modal/ProviderCheckSend.vue';
    import ProviderFinalApprove from '@/components/modal/ProviderFinalApprove.vue';
    import ProviderPrint from '@/components/modal/ProviderPrint.vue';
    import SendMessagePartner from '@/components/modal/SendMessagePartner.vue';
    import SendMessagePartnerBuh from '@/components/modal/SendMessagePartnerBuh.vue';
    import CatalogListItem from '@/components/catalog/CatalogListItem.vue'
    import ProviderAbout from '@/components/moderator/ProviderAbout.vue'
    import Notes from '@/components/moderator/Notes.vue'
    import Chart from '@/components/moderator/Chart.vue'
    import ChartStatistic from '@/components/moderator/ChartStatistic.vue'
    import ProviderRequest from '@/components/moderator/ProviderRequest.vue'
    import ProviderStat from '@/components/moderator/ProviderStat.vue'
    import PrintContract from "@/components/modal/PrintContract.vue";
    import ProviderHistory from '@/components/moderator/ProviderHistory.vue';
    import {scrollTo} from '@/helpers/animate.js';

    export default {
        metaInfo() {
            return {
                title: this.title ? this.title : this.item.name,
                titleTemplate: this.$store.state.options.titleTemplate
            }
        },
        components: {
            PrintContract,
            Chart,
            ChartStatistic,
            Notes,
            ProviderAbout,
            CatalogListItem,
            Popup,
            TabsOuter,
            Tab,
            Loader,
            ProviderApprove,
            ProviderReject,
            ProviderBlock,
            ProviderCancelBlock,
            ProviderCancelReject,
            ProviderOvertime,
            ProviderCheckSend,
            ProviderFinalApprove,
            ProviderPrint,
            SendMessagePartner,
            SendMessagePartnerBuh,
            ProviderRequest,
            ProviderStat,
            ProviderHistory,
            ContractEdit
        },
        data: function () {
            return {
                unsubscribe: '',
                currentTab: ''
            }
        },
        created() {
            this.$store.dispatch('moderatorProviders/getItem', {id: this.$route.params.id});
        },
        mounted: function () {
            this.unsubscribe = this.$store.subscribe((mutation, state) => {
                if(mutation.type=='moderatorCategories/setClose'){
                    if(state.moderatorCategories.itemSaves){
                        this.$store.dispatch('moderatorProviders/refreshSectionAction', {id: state.moderatorCategories.itemId});    
                    }                   
                }  
            });
        },
        beforeDestroy: function(){
            this.$store.commit('moderatorProviders/setItemDetail', {item: []});
            this.unsubscribe();    
        },
        computed: {
            ...mapState({
                item: state => state.moderatorProviders.itemDetail,
                providerLoading: state => state.moderatorProviders.isLoad,
                popups: state => state.popups.items,
            }),
            providerName: function () {
                let result = this.item.requestFIO;

                return result;
            },
            moderateAboutSuffix(){
                let suffix;
                if((+this.item.countActive > 0 || +this.item.countModerate > 0) && this.item.datePublicAbout.length ===0 && this.item.statusCode === 'verify'){
                    suffix = "<sup class='--suffix i i--orange i-info3'></sup>";
                } else if(this.item.dateEditAbout && this.item.statusCode === 'verify'){
                    suffix = "<sup class='--suffix i i--yellow i-info3'></sup>";
                }
                return suffix;


            },
            title(){
                let title =  this.item ? this.item.ownership === '' ? this.item.name : this.item.ownership : ''
                return title ? title : '';
            }
        },
        methods: {
            formatHistoryTitle(title, date) {
                let formatted = '';
                formatted = title.split('. ').join(".<br>");

                return formatted + ' ' + date;
            },
            openSendPopup() {
                this.$store.dispatch('popups/open', {name: this.popups.sendMessagePartner.name});
            },
            openSendBuhPopup() {
                this.$store.dispatch('popups/open', {name: this.popups.sendMessagePartnerBuh.name});
            },
            openPopup(popupName) {
                //сделано для открытия this.popups.contractEdit.name из TabsOuter.vue
                if (typeof(this.popups[popupName]) !== "undefined") {
                    this.$store.dispatch('popups/open', {name: this.popups[popupName].name});
                }
            },
            moveToReq(){
                if(this.currentTab !== '#tab-about'){
                    this.$refs.tabs.selectTab('#tab-about');
                    this.$nextTick( function () {
                        scrollTo('#req-provider-detail');
                        this.$store.commit('moderatorProviders/errorReq', {
                            type: 'inn',
                            value:  true
                        });
                        this.$store.commit('moderatorProviders/errorReq', {
                            type: 'ogrn',
                            value:  true
                        });
                        this.$store.commit('moderatorProviders/errorReq', {
                            type: 'ogrnip',
                            value:  true
                        });
                    })
                }
            },
            changeTab(data) {
                this.currentTab = data.tab.hash;
            },
        }
    }
</script>


<style scoped>
    .coupon-container + .coupon-container {
        margin-top: 53px;
    }
</style>

<style >
    .tabs-nav__btn.active .--suffix{
        display: none;
    }
</style>