<template>
    <div>
        <div class="popup__header popup__header--dotted --bg-red">
            <div class="title">Заблокировать поставщика</div>
            <div class="subtitle" v-html="item.provider.ownership"></div>
        </div>
        
        
        <div class="popup__content">
            
            <div class="h2 mb16">Служебный комментарий</div>
            
            <textarea-field @focus="removeError('commentError')" v-model="form.comment" :is-error="form.commentError"  :placeholder="'Прокомментируйте своё решение'" :add-style="{minHeight: 88 + 'px'}"></textarea-field>
            
            <div class="h2 mb16">СМС поставщику</div>
            
            <textarea-field :hasCounterSMS="true" @focus="removeError('smsTextError')" :is-error="form.smsTextError" v-model="form.smsText" ></textarea-field>
            
            
            
            <div class="h2 mb16">Письмо поставщику</div>
            
            <div class="mb8">
                <input type="text" class="inp size-s full" v-model="form.emailTitle">
            </div>
            
            <text-editor-min v-model="form.emailText" @click.native="removeError('emailTextError')" :error="form.emailTextError" :hide-toolbar="true" :value-default="form.emailText" />
            
            
            
            <div class="h2 mb16">Публичная причина блокировки</div>
            
            <div class="mt16">
                <input type="text" class="inp size-s full" @focus="removeError('banTextError')" :class="{'error':form.banTextError}" v-model="form.banText"  placeholder="Например, «Мошенничество с купонами»...">
            </div>

            <div class="h2 mb16">Неиспользованные купоны <span class="text-color-grey">{{item.stat.data.COUPONS_WAITING}} шт.</span></div>
            <template v-if="item.stat.data.COUPONS_WAITING>0">
                <div class="h2 mb16">СМС покупателям</div>

                <div class="text-size-s text-color-grey mb16">
                    Все активные купоны, находящиеся у покупателей будут аннулированы с возвратом депозита.<br>Составьте
                    по этому поводу понятное СМС покупателям.
                </div>

                <textarea-field :hasCounterSMS="true" @focus="removeError('smsUserError')" :is-error="form.smsUserError"
                                v-model="form.smsUser"></textarea-field>
            </template>

            
            <div class="row-b mt32">
                <div class="row-b__col elem-pos-fix mr24">
                    <submit-btn class="btn--red" @click.native="send" :type="form.btnType">Подтвердить</submit-btn>
                </div>
                
                
                <div class="row-b__col elem-pos-fit">
                    <div class="text-color-grey">Для блокировки поставщика нужна веская причина.<br>Убедитесь, что она у вас есть.</div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import {mapState} from 'vuex'
    import TextareaField from '@/components/forms/TextareaField.vue'
    import TextEditorMin from '@/components/editor/TextEditorMin.vue'
    import SubmitBtn from '@/components/forms/SubmitBtn.vue'

    export default {
        name: "AuditorProviderBlock",
        components: {
            TextareaField,
            TextEditorMin,
            SubmitBtn
        },
        methods: {
            validation: function() {
                let isSuccess = true;
                this.form.commentError = false;
                this.form.banTextError = false;
                this.form.smsUserError = false;
                this.form.smsTextError = false;
                this.form.emailTitleError = false;
                this.form.emailTextError = false;

                if (this.form.comment.length < 10) {
                    isSuccess = false;
                    setTimeout(()=>{
                        this.form.commentError = true;
                    },150);

                }


                if((this.form.emailTitle.length == 0) && this.form.emailText.length > 0){
                    setTimeout(()=>{
                        this.form.emailTitleError = true;

                    },150);
                    isSuccess = false;
                }
                if((this.form.emailTitle.length > 0) && this.form.emailText.length == 0){
                    setTimeout(()=>{
                        this.form.emailTextError = true;
                    },150);

                    isSuccess = false;
                }

                if (this.form.banText.length < 10) {
                    setTimeout(()=>{
                        this.form.banTextError = true;
                    },150);

                    isSuccess = false;
                }

                if (this.form.smsUser.length < 10) {
                    setTimeout(()=>{
                        this.form.smsUserError = true;
                    },150);

                    isSuccess = false;
                }

                return isSuccess;
            },
            send: function () {
                if (this.validation()) {
                    if (!this.formLoad) {
                        this.formLoad = true;
                        this.form.commentError = false;
                        this.form.btnType = 'load';
                        this.$store.dispatch('auditorProviders/sendForm', {
                            type: 'blocked',
                            id: this.item.provider.id,
                            comment: this.form.comment,
                            banText: this.form.banText,
                            smsUser: this.form.smsUser,
                            sms: {value: this.form.smsText} ,
                            emailTitle: {value: this.form.emailTitle},
                            email: {value: this.form.emailText},
                        }).then(() => {
                            this.form.formLoad = false,
                            this.form.btnType = 'ok';
                            setTimeout(() => {
                                this.$store.commit('popups/close', {name: this.popups.auditorProviderBlock.name});
                                this.$store.dispatch('auditorProviders/getItem', {id: this.item.provider.id, reload: true});
                                //this.$store.dispatch('moderatorProviders/getItem', {id: this.item.id});
                            }, 300);
                        });
                    }
                }
            },
            removeError(field) {
                this.form[field] = false;
            }
        },
        data: function(){
            return {
                form:{
                    comment: '',
                    commentError: false,
                    banText: '',
                    banTextError: false,
                    smsUser: '',
                    smsUserError: false,
                    smsText: '',
                    smsTextError: false,
                    emailTitle: '',
                    emailTitleError: false,
                    emailText: '',
                    emailTextError: false,
                    formLoad: false,
                    btnType: 'normal'
                }
            }
        },
        mounted() {
            this.form.smsText = this.templates.banSMS.text;
            this.form.emailTitle = this.templates.banEmail.title;
            this.form.emailText = this.templates.banEmail.text;
        },
        computed: {
            ...mapState({
                popups: state => state.popups.items,
                item: state => state.auditorProviders.itemDetail,
                templates: state=> state.auditorProviders.templates,
            }),
        },
        beforeDestroy(){
            this.$store.commit('popups/close', {name: this.popups.auditorProviderBlock.name});
        },
    }
</script>
