 <template>
    <div>
        <div class="popup__header popup__header--dotted --bg-green">
            <div class="title">Опубликовать изменения в акции</div>
            <div class="subtitle">{{item.name}}</div>
        </div>
        <div class="popup__content">
            <div class="h2 mb16">Служебный комментарий</div>
            <textarea-field @focus="removeError('commentError')" v-model="form.comment" :is-error="form.commentError" placeholder="Прокомментируйте своё решение" :add-style="{minHeight: 88 + 'px'}"></textarea-field>

            <div class="h2 mb16">СМС редактору на {{this.templates.approve_sms.recipient}}</div>
            <textarea-field @focus="removeError('smsError')" :hasCounterSMS="true" v-model="form.smsText"  :is-error="form.smsError"></textarea-field>

            <div class="h2 mb16">Письмо редактору на {{this.templates.approve_email.recipient}}</div>
            <div class="mb8">
                <input :class="{error:form.emailTitleError}" @focus="removeError('emailTitleError')" type="text" class="inp size-s full" v-model="form.emailTitle">
            </div>
            <text-editor-min @error="form.emailTextError=false" :error="form.emailTextError" v-model="form.emailText" :hide-toolbar="true" :value-default="form.emailText" />

            <div class="row-b mt32">
                <div class="row-b__col elem-pos-fix mr24">
                    <submit-btn class="btn--green" @click.native="send" :type="form.btnType">Подтвердить</submit-btn>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
    import {mapState} from 'vuex'
    import TextareaField from '@/components/forms/TextareaField.vue'
    import TextEditorMin from '@/components/editor/TextEditorMin.vue'
    import SubmitBtn from '@/components/forms/SubmitBtn.vue'

    export default {
        name: "ModeratorCouponApprove",
        components: {
            TextareaField,
            TextEditorMin,
            SubmitBtn
        },
        methods: {
            validation(){
                let isSuccess = true;

                this.form.emailTitleError = false;
                this.form.emailTextError = false;
                this.form.smsError = false;
                this.form.commentError = false;

                /*if((this.form.emailTitle.length == 0) &&
                (this.form.emailText.length == 0) &&
                this.form.smsText.length == 0){
                    this.form.emailTitleError = true;
                    this.form.emailTextError = true;
                    this.form.smsError = true;
                    isSuccess = false;     
                }*/
                if((this.form.emailTitle.length == 0) && this.form.emailText.length > 0){
                    setTimeout(()=>{
                        this.form.emailTitleError = true;
                    },150);
                    isSuccess = false;  
                }
                if((this.form.emailTitle.length > 0) && this.form.emailText.length == 0){
                    setTimeout(()=>{
                        this.form.emailTextError = true;
                    },150);
                    isSuccess = false; 
                }
                if (this.form.comment.length<10) {
                    setTimeout(()=>{
                        this.form.commentError = true;
                    },150);
                    isSuccess = false; 
                } 
                return isSuccess;
            },
            send(){

                if (this.validation()) {
                    if(!this.formLoad){
                        this.formLoad = true;
                        this.form.commentError = false;
                        this.form.btnType = 'load';
                        this.$store.dispatch('moderatorCoupon/sendForm', {
                            type: 'approveEdit',
                            id: this.item.id,
                            comment: this.form.comment,
                            smsText: this.form.smsText,
                            emailTitle: this.form.emailTitle,
                            emailText: this.form.emailText,
                        }).then(()=>{
                            this.form.formLoad = false,
                            this.form.btnType = 'ok';
                            setTimeout(()=>{
                                this.$store.commit('popups/close', {name: this.popups.moderatorCouponApproveEdit.name});
                                this.$store.dispatch('moderatorCoupon/initDetailPage',{route: this.$route});
                            }, 300);
                        });
                    }
                }
            },
            removeError(field){
                this.form[field] = false;
            },
        },
        data: function(){
            return {
                form:{
                    comment: '',
                    commentError: false,
                    emailTitleError: false,
                    emailTextError: false,
                    smsError: false,
                    smsText: '',
                    emailTitle: '',
                    emailText: '',
                    formLoad: false,
                    btnType: 'normal'
                }
            }
        },
        mounted() {
            this.form.smsText = this.templates.approve_edit_sms.text;
            this.form.emailTitle = this.templates.approve_edit_email.title;
            this.form.emailText = this.templates.approve_edit_email.text;
        },
        computed: {
            ...mapState({
                item: state => state.moderatorCoupon.itemDetail,
                popups: state => state.popups.items,
                templates: state=> state.moderatorCoupon.itemDetailTemplates,
            }),
        },
        beforeDestroy(){
            this.$store.commit('popups/close', {name: this.popups.moderatorCouponApproveEdit.name});
        }
    }
</script>