<template>
    <div>
        <div class="popup__content">
            <div class="h1">Вы уверены, что хотите сделать вашу акцию доступной для всей России?</div>
            
            
            <div class="mt16">
                Это значит, что принять в ней участие смогут покупатели из любого города и вам придется<br> обеспечивать указанные условия по всей стране.
            </div>
            
            <div class="mt16">
                Такие акции могут создавать только крупные компании с развитой логистической службой<br> или интернент-магазины с доставкой по всей стране.
            </div>
            
            
            <div class="row-b mt32">
                <div class="row-b__col elem-pos-fix mr24">
                    <button class="btn mr32" @click="save">Я понимаю, что делаю!</button>
                    <button class="btn btn--grey" @click="close">Отмена</button>
                </div>
                
                
                <div class="row-b__col elem-pos-fit">
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import {mapState} from 'vuex'

    export default {
        name: "ConstructActionAllCity",
        components: {
        },
        props: {
        },
        data: function() {
            return {
            }
        },
        created() {},
        methods: {
            save() {
                this.$store.commit('partnerActions/setFormCreateField', {
                    name: 'cities',
                    value: [ this.$store.getters['cities/getByXmlId']('all')],
                    step: 'five',
                })
                this.$store.commit('popups/close', {name: this.popups.constructActionAllCity.name});
                this.$store.commit('popups/close', {name: this.popups.constructActionCity.name});
            },
            close() {
                this.$store.commit('popups/close', {name: this.popups.constructActionAllCity.name});
            }
        },
        computed: {
            ...mapState({
                popups: state => state.popups.items,
            }),
        },
        beforeDestroy(){
            this.$store.commit('popups/close', {name: this.popups.constructActionAllCity.name});
        }
    }
</script>

