<template>
    <div class="wrapper-content-inner">
        <div class="content-title">
            <div class="content-title__wrap">
                <div class="h2" v-html="partner.name"></div>
            </div>

            <div class="content-title__right" v-if="partner.site">
                <a :href="'//'+partner.site" class="content-title__pointer" target="_blank" rel="noopener">
                    <span class="i i-link"></span>
                    <span class="t">{{ partner.site }}</span>
                </a>
            </div>
            
            <div class="content-title__info" v-if="(partner.dateEditAbout || !partner.datePublicAbout) && partner.aboutText.length > 0">
                <tooltip 
                         :direction="'top-left-direction'"
                         :add-class-wrap="'l-icon'"
                         :add-class-btn="moderateClass + ' i hover'"
                         :add-style="{width: partner.datePublicAbout?356 + 'px':'336px'}">
                    <span slot="tooltipBtn"></span>
                    
                    <div slot="tooltipContent">
                        <span class="text-size-m" v-if="partner.datePublicAbout">
                            Страница находится на модерации. Покупатели <br>
                            пока видят старую версию от {{partner.datePublicAbout}}</span>
                        <span class="text-size-m pr8" v-else >Страница находится на срочной модерации<br> и будет опубликована в ближайшее время</span>
                    </div>
                </tooltip>
            </div>
            
            <div class="content-title__info" v-else-if="partner.aboutText.length > 0">
                <tooltip :direction="'top-left-direction'"
                         :add-class-wrap="'l-icon'"
                         :add-class="'tooltip--stat'"
                         :is-padding="false"
                         :isBtnClose="false"
                         :add-style="{width: 280 + 'px'}">
                    <span slot="tooltipBtn" class="i--green i-ok3 i-ok3-small  i hover"></span>
                    
                    <div slot="tooltipContent">
                        <span class="text-size-m">Страница проверена модератором и опубликована {{partner.datePublicAbout}}</span>
                    </div>
                </tooltip>
            </div>
        </div>

        <partner-about-main-text :partner="partner"></partner-about-main-text>


        <div class="partner-about__bottom mt24">
            <h2>Реквизиты</h2>
            <div class="requisites">
                <div v-html="partner.requisites"></div>

                <div class="requisites__logo" v-if="partner.logo.icon">
                    <img :src="'//api.zemskidki.ru/'+partner.logo.icon" alt="">
                </div>
            </div>
        </div>
        <!-- end tab-content-bottom -->
    </div>
</template>



<script>
    import {mapState} from 'vuex';
    import Tooltip from '@/components/Tooltip.vue';
    import PartnerAboutMainText from '@/components/partner/PartnerAboutMainText.vue';

    export default {
        name: "PartnerAboutText",
        components: {
            Tooltip,
            PartnerAboutMainText
        },
        data: function() {
            return {

            }
        },
        mounted: function () {

        },
        computed: {
            ...mapState({
                partner: state => state.partner.current,
            }),
            moderateClass(){
                if(this.partner.dateEditAbout || !this.partner.datePublicAbout){
                    if(this.partner.datePublicAbout){
                        return 'i--yellow i-info3';
                    } else {
                        return 'i--orange i-info3';
                    }
                } else {
                    return 'i--green i-ok3 i-ok3-small';
                }
            }
        },
        methods: {

        },
    }
    
</script>

<style scoped>
    .content-title {
        position: relative;
    }

    .content-title__info {
        position: absolute;
        top: 3px;
        left: -17px;
    }
    .i-ok3-small{
        transform: scale(0.75)
    }
</style>