<template>
    <div class="content">
        <div class="main-title">
            <div class="main-title__wrap main-title--nowrap">
                <div class="h1">{{pageTitle}}</div>
            </div>


            <div class="main-title__right">
                <span class="i i-info mr4"></span>
                <span class="t text-color">{{pageDescription}}</span>
            </div>
        </div>

          <cooperation-list v-if="menu['cooperation']"/>
    </div>
</template>
<script>
    import {mapState} from 'vuex';
    import CooperationList from '@/components/moderator/CooperationList.vue';
    
    export default {
        metaInfo() {
            return {
                title: this.pageTitle,
                titleTemplate: this.$store.state.options.titleTemplate
            }
        },
        name: "Cooperations",
        components: {
            CooperationList
        },
        computed: {
            ...mapState({
                menu: state => state.menus.itemsAction,
            }),
            defaultStatus() {
                let id = 0;
                if(this.menu['cooperation']){
                    id = this.menu['cooperation'][0]['code'];
                }
                return id;
            },
            pageTitle() {
                if (this.menu['cooperation']) {
                    let action = this.$route.params.action ? this.$route.params.action : this.defaultStatus;
                    //return this.menu['cooperation'][this.getIndexItem(action)].name;
                    return this.menu['cooperation'][this.getIndexItem(action)].name + ' заявки';
                }
            },
            pageDescription() {
                let action = this.$route.params.action ? this.$route.params.action : this.defaultStatus,
                    currentIndex = this.getIndexItem(action),
                    descr = '';

                if (typeof (this.menu['cooperation']) !== 'undefined') {
                    descr = this.menu['cooperation'][currentIndex]['descr']
                }

                return descr;
            }
        },
        methods:{
            getIndexItem(action) {
                let menuList = this.menu['cooperation'],
                    result;

                for (let index in menuList) {
                    if (menuList[index].code == action) {
                        result = index;
                        break;
                    }
                }

                return result;
            },
        }
    }
</script>
