<template>
    <div class="content">
        <template v-if="!cooperationLoading">
            <div class="main-title">
                <div class="main-title__wrap main-title--nowrap">
                    <div class="h1">{{item.direction}}</div>
                </div>

                <div class="main-title__right"></div>
            </div>
            
            <section class="container">
                <section class="container__left">
                    <div class="wrapper-content">
                        <div class="wrapper-content-inner">
                            <div class="provider-request-data">
                                <div class="item">
                                    <div class="t">Средний чек</div>
                                    <div class="d">{{item.avg_check}} <span class="rouble">{{ $store.getters['options/setRouble'] }}</span></div>
                                </div>


                                <div class="item">
                                    <div class="t">Обычная скидка</div>
                                    <div class="d">{{item.discount_retail}}%</div>
                                </div>


                                <div class="item">
                                    <div class="t">Эксклюзивная скидка</div>
                                    <div class="d">{{item.discount_our}}%</div>
                                </div>


                                <div class="item">
                                    <div class="t">Польза от проекта</div>
                                    <div class="d">{{item.realSaving}}   <span class="rouble">{{ $store.getters['options/setRouble'] }}</span></div>
                                </div>
                            </div>
                            <!-- end provider-request-data -->


                            <div class="hr --dotted --wide"></div>



                            <div class="provider-request-info">
                                <div class="content-title">
                                    <div class="content-title__wrap">
                                        <div class="h2">{{ item.name }}</div>
                                    </div>

                                    <div class="content-title__right">
                                        <a :href="'http://' + item.site" target="_blank" rel="noopener" class="content-title__pointer"><span class="i i-link"></span><span class="t ml4">{{ item.site }}</span></a>
                                    </div>
                                </div>

                                <p v-html="item.note"></p>

                                <template v-if="item.moderator_comment">
                                    <div class="h2">Служебный комментарий</div>
                                    <p>{{item.moderator_comment}}</p>
                                </template>
                                <template v-if="item.cooperation_email_title">
                                    <div class="h2">Письмо поставщику</div>
                                    <div class="mt16">
                                        <strong>{{item.cooperation_email_title}}</strong><br>
                                        <div v-html="item.cooperation_email"></div>
                                    </div>
                                </template>
                                <template v-if="item.cooperation_sms">
                                    <div class="h2">СМС поставщику</div>
                                    <p>{{item.cooperation_sms}}</p>
                                </template>
                            </div>
                            <!-- end provider-request-info -->
                        </div>
                    </div>
                    <!-- end wrapper-content -->

                </section>
                <!-- end container__left -->



                <aside class="aside aside--right">
                    <div class="aside__sticky">
                        <div class="provider-request-contacts">
                            <div class="wrap" style="padding-bottom: 20px; ">
                                <div class="item">
                                    <div class="t">
                                        <span class="t__t">Регион</span>
                                        <span class="t__d" :class="{'text-color-red': !item.city_is_worktime}">{{item.city_time}}</span>
                                    </div>

                                    <div class="d text-bold">
                                        {{item.city}}
                                    </div>
                                </div>
                            </div>


                            <div class="wrap">
                                <div class="item">
                                    <div class="t">{{item.position}}</div>
                                    <div class="d text-bold mb8">{{item.fio}}</div>
                                    <div class="d">
                                        <a class="text-bold" :href="'tel:'+item.phone.replace(/[^\+0-9]/g, '')">{{ item.phone }}</a>
                                        <a @click.prevent="openSendPopup" href="" class="d__send i i-mail2" title="Написать поставщику"></a>
                                    </div>
                                </div>

                                <!-- email убрали из дизайна
                                <div class="item">
                                    <div class="t">E-mail</div>
                                    <div class="d">{{item.email}}</div>
                                </div> -->
                            </div>

                            <div class="wrap">
                                <notes type="provider" :title="'Заметки о поставщике'" :entityId="item.id"></notes>
                            </div>
                        </div>
                    </div>
                </aside>
                <!-- end aside aside--right -->
            </section>
            <!-- end container -->
        </template>


        <template v-else>
            <div class="preloader-page">
                <div class="preloader"></div>
            </div>
        </template>


        <!-- одобрить заявку -->
        <popup :is-set-styled="true" :show="popups.requestApprove.show" :name="popups.requestApprove.name">
            <request-approve></request-approve>
        </popup>


        <!-- отклонить заявку -->
        <popup :is-set-styled="true" :show="popups.requestReject.show" :name="popups.requestReject.name">
            <request-reject></request-reject>
        </popup>

        <!-- отложить заявку -->
        <popup :is-set-styled="true" :show="popups.requestPause.show" :name="popups.requestPause.name">
            <request-pause></request-pause>
        </popup>

        <!-- письмо поставщику -->
        <popup :is-set-styled="true" :show="popups.sendMessagePartner.show" :name="popups.sendMessagePartner.name">
            <send-message-partner type="cooperation" send-to="ouner" :id="item.id"></send-message-partner>
        </popup>
    </div>
</template>


<script>
    import {mapState} from 'vuex'
    import Popup from '@/components/modal/Popup.vue'
    import RequestApprove from '@/components/modal/RequestApprove.vue';
    import RequestReject from '@/components/modal/RequestReject.vue';
    import RequestPause from '@/components/modal/RequestPause.vue';
    import SendMessagePartner from '@/components/modal/SendMessagePartner.vue';
    import Loader from '@/components/Loader.vue';
    import Notes from '@/components/moderator/Notes.vue';

    export default {
        metaInfo() {
            return {
                title: this.item.direction ? this.item.direction : '',
                titleTemplate: this.$store.state.options.titleTemplate
            }
        },
        components: {
            Popup,
            RequestApprove,
            RequestReject,
            RequestPause,
            SendMessagePartner,
            Loader,
            Notes
        },
        created() {
            this.$store.dispatch('moderatorCooperation/getItem',{id:this.$route.params.id});
        },
        computed: {
            ...mapState({
                item: state => state.moderatorCooperation.itemDetail,
                cooperationLoading: state => state.moderatorCooperation.isLoad,
                popups: state => state.popups.items,
            })
        },
        methods: {
            openSendPopup(){
                this.$store.dispatch('popups/open', {name: this.popups.sendMessagePartner.name});
            }
        }
    }
</script>
