<template>
    <div>
        <div class="popup__header popup__header--dotted --bg-green">
            <div class="title">Отменить отклонение поставщика</div>
            <div class="subtitle" v-html="item.ownership"></div>
        </div>
        
        
        <div class="popup__content">
            
            <div class="h2 mb16">Служебный комментарий</div>
            
            <textarea-field @focus="removeError('commentError')" v-model="form.comment" :is-error="form.commentError"  :placeholder="'Прокомментируйте своё решение'" :add-style="{minHeight: 88 + 'px'}"></textarea-field>
            
            <div class="h2 mb16">СМС поставщику на {{this.templates.cancel_reject_sms.recipient}}</div>
            
            <textarea-field :hasCounterSMS="true" @focus="removeError('smsTextError')" :is-error="form.smsTextError" v-model="form.smsText" ></textarea-field>
            
            
            
            <div class="h2 mb16">Письмо поставщику на {{this.templates.cancel_reject_email.recipient}}</div>
            
            <div class="mb8">
                <input type="text" class="inp size-s full" v-model="form.emailTitle">
            </div>
            
            <!--<textarea-field  :add-style="{minHeight: 333 + 'px'}" v-model="form.emailText"></textarea-field>-->
            <text-editor-min v-model="form.emailText" @click.native="removeError('emailTextError')" :error="form.emailTextError" :hide-toolbar="true" :value-default="form.emailText" />
            
            <div class="row-b mt32">
                <div class="row-b__col elem-pos-fix mr24">
                    <submit-btn class="btn--green" @click.native="send" :type="form.btnType">Подтвердить</submit-btn>
                </div>
                
                
                <div class="row-b__col elem-pos-fit">
                    <div class="text-color-grey">После отмены отклонения поставщик сможет продолжить работу<br>
                    с кабинетом с того этапа, на котором она была приостановлена</div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import {mapState} from 'vuex'
    import TextareaField from '@/components/forms/TextareaField.vue'
    import TextEditorMin from '@/components/editor/TextEditorMin.vue'
    import SubmitBtn from '@/components/forms/SubmitBtn.vue'

    export default {
        name: "ProviderCancelReject",
        components: {
            TextareaField,
            TextEditorMin,
            SubmitBtn
        },
        methods: {
            validation: function() {
                let isSuccess = true;
                this.form.commentError = false;
                this.form.smsTextError = false;
                this.form.emailTitleError = false;
                this.form.emailTextError = false;

                if (this.form.comment.length < 10) {
                    isSuccess = false;
                    setTimeout(()=>{
                        this.form.commentError = true;
                    },150);
                }

                if((this.form.emailTitle.length == 0) && this.form.emailText.length > 0){
                    setTimeout(()=>{
                        this.form.emailTitleError = true;
                    },150);

                    isSuccess = false;
                }
                if((this.form.emailTitle.length > 0) && this.form.emailText.length == 0){
                    setTimeout(()=>{
                        this.form.emailTextError = true;
                    },150);
                    isSuccess = false;
                }

                return isSuccess;
            },
            send: function(){
                if (this.validation()) {
                    if(!this.formLoad){
                        this.formLoad = true;
                        this.form.commentError = false;
                        this.form.btnType = 'load';
                        this.$store.dispatch('moderatorProviders/sendForm', {
                            type: 'cancel_reject',
                            id: this.item.id,
                            comment: this.form.comment,
                            smsText: this.form.smsText,
                            emailTitle: this.form.emailTitle,
                            emailText: this.form.emailText,
                        }).then(()=>{
                            this.form.formLoad = false,
                            this.form.btnType = 'ok';
                            setTimeout(()=>{
                                this.$store.commit('popups/close', {name: this.popups.providerCancelReject.name});
                                this.$router.push('/moderator/providers/' + this.providerBackCode + '/');
                            }, 300);
                        });
                    }
                }
            },
            removeError(field) {
                this.form[field] = false;
            }
        },
        data: function(){
            return {
                form:{
                    comment: '',
                    commentError: false,
                    smsText: '',
                    smsTextError: false,
                    emailTitle: '',
                    emailTitleError: false,
                    emailText: '',
                    emailTextError: false,
                    formLoad: false,
                    btnType: 'normal'
                }
            }
        },
        mounted() {
            this.form.smsText = this.templates.cancel_reject_sms.text;
            this.form.emailTitle = this.templates.cancel_reject_email.title;
            this.form.emailText = this.templates.cancel_reject_email.text;
        },
        computed: {
            ...mapState({
                popups: state => state.popups.items,
                item: state => state.moderatorProviders.itemDetail,
                templates: state=> state.moderatorProviders.itemDetailTemplates,
                providerListStatus: state => state.moderatorProviders.listStatus,
                actionMenuList: state => state.menus.itemsAction,
            }),
            providerBackId() {
                return this.providerListStatus ? this.providerListStatus : this.item['status'];
            },
            providerBackCode() {
                let statusId = this.providerListStatus ? this.providerListStatus : this.item['status'];

                for (let itemIndex in this.actionMenuList['providers']) {
                    if (this.actionMenuList['providers'][itemIndex]['id'] == statusId) {
                        return this.actionMenuList['providers'][itemIndex]['code'];
                    }
                }
            },
        }, 
        beforeDestroy(){
            this.$store.commit('popups/close', {name: this.popups.providerCancelReject.name});
        }
    }
</script>

