<template>
    <div>
        <div class="popup__content">
            <div class="h1 mb8">Откуда берётся скидка {{ stepsCreateAction.one.fields.discount.value - 3}}%?</div>
            
            <div :class="{error:this.error}">Выберите один из вариантов. Эта информация будет отображаться на странице вашей акции.</div>
            <div class="mt24" v-for="(item,index) in items">
                <input @change="change(item)" :value="index" v-model="selected" type="radio" name="origin-discount-radio" :id="index">
                <label :for="index" class="" >
                    <div class="text-size-ml text-bolder">{{ item.name}}</div>
                    <div v-html="generateHtml(item)"></div>
                </label>
            </div>
            
            
            <div class="row-b mt32">
                <div class="row-b__col elem-pos-fix mr24">
                    <button class="btn" @click="next">Продолжить</button>
                </div>
                
                
                <div class="row-b__col elem-pos-fit">
                    <div class="text-color-grey text-size-s">Если вам не подходит ни один из вариантов,<br> напишите нам на skidki@zems.pro, мы добавим...</div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import {mapState} from 'vuex';
    import {actionDiscountHtml} from '@/helpers/tempateHtml';

    export default {
        name: "OriginDiscount",
        components: {
        },
        props: {
            selectedProp: {
                default: false
            },
        },
        data: function() {
            return {
                selected: '',
                html: '',
                item: '',
                error: false,
            }
        },
        created() {

        },
        methods: {
            generateHtml(item) {
                let html =  actionDiscountHtml(item.text, this.stepsCreateAction.one.fields.discount.value - 3)
                if(!this.selected){
                    if(this.selectedProp){
                        if(this.selectedProp === item.text){
                            this.selected = item.code;
                            this.html = html;
                            this.item = item.text;
                        }
                    }
                }
                return html
            },
            change(item) {
                this.html = this.generateHtml(item);
                this.item = item.text;
            },
            next(){
                if(this.html.length === 0){
                    this.error = true;
                    if(this.error) {
                        this.error = false;
                        setTimeout(()=>{
                            this.error = true;
                        },500)
                    }
                } else {
                    this.false = true;
                    this.$emit('change',this.item);
                    this.$store.commit('popups/close', {name: this.popups.originDiscount.name});
                }

            }
        },
        computed: {
            ...mapState({
                popups: state => state.popups.items,
                items: state => state.partnerActions.templates.modals.origin,
                stepsCreateAction: state => state.partnerActions.stepsCreateAction,
            }),
        },
        beforeDestroy(){
            this.$store.commit('popups/close', {name: this.popups.originDiscount.name});
        }
    }
</script>

<style scoped>
    [type="radio"] + label:before {
        top: 4px;
    }
    
    [type="radio"] + label:after {
        top: 8px;
    }
</style>