<template>
    <div ref="innDragZone" @mousemove="innDoDrag()">
        <div class="popup__header popup__header--dotted">
            <div class="title">Проверка ИНН {{ inn.text }}</div>
            <div class="subtitle" v-html="providerName"></div>
        </div>
        
        <div ref="inn" class="inn-img__num" @mousedown="innDragStart()" @mouseup="innDragStop()">
            ИНН <span>{{ inn.text }}</span>
        </div>


        <div class="popup__content">

            <div class="inn-img" v-if="inn.type.toLowerCase() === 'pdf'">
                <iframe :src="'//api.zemskidki.ru' + inn.file" frameborder="0"></iframe>
            </div>
            <div class="inn-img" v-else>
                <img :src="'//api.zemskidki.ru' + inn.file" alt="">
            </div>

            <div class="inn-check">
                <div class="text">ИНН проверен</div>

                <div class="c">
                    <input id="verification-inn-scan-check"
                           :class="{error: checkScanError}"
                           @change="checkScanError = false"
                           type="checkbox" v-model="checkScan"/>
                    <label for="verification-inn-scan-check">по скан-копии</label>
                </div>

                <div class="c">
                    <input id="verification-inn-scan-rosreestr"
                           :class="{error: checkRegError}"
                           @change="checkRegError = false"
                           type="checkbox" v-model="checkReg"/>
                    <label for="verification-inn-scan-rosreestr">через <a @click="copyTextToClipboard(inn.text)" href="https://egrul.nalog.ru/index.html" target="_blank">РосРеестр</a></label>
                </div>
            </div>


            <div class="text-center mt24">
                <submit-btn class="btn" @click.native="send" :type="btnType">Подтвердить</submit-btn>
            </div>
        </div>
    </div>
</template>

<script>
    import {mapState} from 'vuex';
    import SubmitBtn from '@/components/forms/SubmitBtn.vue';
    import Popup from '@/components/modal/Popup.vue';
    import { copyTextToClipboard } from "@/helpers/util/helper";


    export default {
        name: "VerificationInn",
        props: {
            providerId: {
                default: function () {
                    return ''
                }
            },
            providerName: {
                default: function () {
                    return ''
                }
            },
            inn: {
                default: function () {
                    return {
                        type: '',
                        file: '',
                        text: ''
                    }
                }
            },
            ogrn: {
                default: function () {
                    return {
                        type: '',
                        file: '',
                        text: ''
                    }
                }
            },
        },
        data: function () {
            return {
                checkScanError: false,
                checkRegError: false,
                checkScan: false,
                checkReg: false,
                btnType: 'normal',
                dragging: false,
            }
        },
        components: {
            SubmitBtn,
            Popup
        },
        methods: {
            send: function () {
                let isError = false;

                if (!this.checkScan) {
                    isError = true;
                    this.checkScanError = true;
                }

                if (!this.checkReg) {
                    isError = true;
                    this.checkRegError = true;
                }

                if(!isError){
                    this.btnType = 'load';
                    this.$store.dispatch('moderatorProviders/checkReq', {
                        type: 'inn',
                        id:  this.providerId
                    }).then(()=>{
                        this.btnType = 'ok';
                        this.$store.commit('popups/close', {name: this.popups.verificationInn.name});
                        this.$store.dispatch('moderatorProviders/getItem', {id: this.providerId});
                    });
                }
            },
            
            innDragStart: function() {
                this.$refs.inn.classList.add("dragging")
                this.dragging = true;
            },
            
            innDragStop: function() {
                this.$refs.inn.classList.remove("dragging")
                this.dragging = false;
            },
            
            innDoDrag: function() {
                if (this.dragging) {
                    this.$refs.inn.style.left = (event.clientX - this.$refs.innDragZone.getBoundingClientRect().left) + 'px';
                    this.$refs.inn.style.top = (event.clientY - this.$refs.innDragZone.getBoundingClientRect().top) + 'px';
                }
            },
            copyTextToClipboard(text){
                copyTextToClipboard(text)
            }
        },
        mounted() {
        },
        computed: {
            ...mapState(
                {
                    popups: state => state.popups.items,
                }),
        }
    }
</script>

<style scoped>
    .inn-img > iframe {
        width: 100%;
        min-height: 900px;
    }
</style>