<template>
    <div class="content">
        <div class="main-title">
            <div class="main-title__wrap main-title--nowrap">
                <div class="h1" v-if="pageType=='search'">Поиск «<span class="main-title__search-link">{{searchText}}</span>»</div>
                <div class="h1" v-if="pageType!='search'">{{title}}</div>
            </div>
            <div class="main-title__right">
                <span class="t text-color-grey text-bold" @click="goto(pageType)">{{switcher}}</span>
            </div>
        </div>

        <section  class="container">
            <div class="container__full">
                <div class="buh-partners-list" v-if="items.length > 0">
                    <div class="item">
                        <div class="item__title"><span @click="sort('name')" class="item item--name" :class="classObject('name')">
                                <span class="b">Название</span>
                            </span><span class="item item--doc" @click="sort('doc')" :class="classObject('doc')">
                                <span class="b">Ждём документы</span>
                            </span><span class="item item--debt" @click="sort('debt')" :class="classObject('debt')">
                                <span class="b">Долги по счетам</span>
                            </span><span class="item item--new" @click="sort('new')" :class="classObject('new')">
                                <span class="b">Новые купоны</span>
                            </span>
                        </div>
                    </div>
                    <div class="item"  v-for="item in items" :key="item.id">
                        <router-link :to="'/buh/id'+item.id+'/'" href="#" class="item__link">
                            <span class="item item--name">
                                <span class="t">{{item.trademark}}<span class="text-color-grey"> / {{item.name}}</span>
                                    <span v-if="item.isBanned" class="i i-stop i--dark-trans" style="transform: scale(0.75);"></span>
                                </span>
                            </span>
                            <span class="item item--doc">
                                <span class="t">{{(item.billsExposed>0)?item.billsExposed:''}}</span>
                            </span>
                            <span class="item item--debt">
                                <span class="t">{{item.totalBill}} <span class="rouble">{{ $store.getters['options/setRouble'] }}</span></span>
                            </span>
                            <span class="item item--new">
                                <span class="t">{{item.total}} <span class="rouble">{{ $store.getters['options/setRouble'] }}</span></span>
                            </span>
                        </router-link>
                    </div>
                    <div class="sect-blank" style="min-height: 100px" v-if="isLoad">
<div class="preloader"></div>
                    </div>
                </div>
                <div class="wrapper-content mt32" v-if="!isLoad && items.length === 0">
                    <div class="sect-blank">
                        <div class="wrap text-center">
                            <div class="text-size-l text-bolder text-color-grey mt16">Здесь пока ничего нет...</div>
                        </div>

                        <div class="mini-zems"></div>
                    </div>
                    <!-- end sect-blank -->
                </div>
            </div>
        </section>

    </div>
</template>
<script>
    import {mapState} from 'vuex';
    import Loader from '@/components/Loader.vue';
    export default {
        metaInfo() {
            return {
                title: this.title,
                titleTemplate: this.$store.state.options.titleTemplate
            }
        },
        components: {
            Loader
        },
        created() {},
        mounted() {
            window.addEventListener('scroll', this.handleScroll);
            if(this.$route.name=='buh-providers-list-blocked'){
                this.$store.commit('auditorProviders/setPageType', {pageType: 'blocked'});
            }
            if(this.$route.name=='buh-providers-list'){
                this.$store.commit('auditorProviders/setPageType', {pageType: 'default'});
            }
            this.fetchData();
        },
        beforeDestroy() {
            window.removeEventListener('scroll', this.handleScroll);
        },
        computed: {
            ...mapState(
            {
                isLoad: state => state.auditorProviders.isLoad,
                pageType: state => state.auditorProviders.pageType,
                searchText: state => state.auditorProviders.searchText,
                items: state => state.auditorProviders.items,
                sortList: state => state.auditorProviders.sortList,
            }),
            switcher(){
                return (this.pageType=='blocked')?'АКТИВНЫЕ ПОСТАВЩИКИ':'ЗАБЛОКИРОВАННЫЕ ПОСТАВЩИКИ';
            },
            title(){
                return (this.pageType=='blocked')?'Заблокированные поставщики':'Активные поставщики';
            },
            url(){
                return (this.pageType=='blocked')?'/buh/':'/buh/blocked/';
            }
        },
        methods: {
            sort(name){
                this.$store.dispatch('auditorProviders/setSort',{name: name});
            },
            classObject(name){
                return {
                    active: this.sortList.by == name,
                    up: this.sortList.by == name && this.sortList.order == 'up',
                    down: this.sortList.by == name && this.sortList.order == 'down',
                }
            },
            fetchData () {
                this.$store.dispatch('auditorProviders/getItems',{reload: true, blocked: (this.pageType=='blocked')?true:false});
            },
            goto(pageType){
                if(pageType=='blocked'){
                    this.$router.push('/buh/');
                    this.$store.commit('auditorProviders/setPageType', {pageType: 'blocked'});
                     this.$store.dispatch('auditorProviders/getItems',{reload: true});
                }
                else{
                    this.$router.push('/buh/blocked/');
                    this.$store.commit('auditorProviders/setPageType', {pageType: 'default'});
                    this.$store.dispatch('auditorProviders/getItems',{reload: true, blocked: true});
                }
            },
            /*blocked(){
                let blocked = (this.pageType=='blocked')?false:true;
                this.$store.dispatch('auditorProviders/getItems',{reload: true, blocked: blocked});
            },*/
            handleScroll: function () {
                let scroll = window.scrollY;
                let viewport = window.innerHeight;
                let bodyHeight = document.body.clientHeight;
                if(bodyHeight-80 <= scroll + viewport){
                    this.$store.dispatch('auditorProviders/getItems', {
                        status:this.currentStatus
                    })
                }
            },
        },
    }
</script>
