<template>
    <div>
        <div class="popup__content">
            <div class="h1 mb8">Позвонить...</div>


            <div class="row-b mt40">
                <div class="row-b__col elem-pos-fix" style="width: 296px; min-width: 296px; ">
                    <input-styled
                            name="phoneCall"
                            :mask="masks.phone"
                            :is-block-arrow-phone="true"
                            v-model="phone.value"
                            @focus="phone.error = false; phoneFocus = true"
                            @blur="phoneFocus = false"
                            :is-error="phone.error"
                            :placeholder="'По телефону...'"
                            :placeholder-input="''"></input-styled>
                </div>

                <div class="row-b__col elem-pos-fit" :class="{'--active': phoneFocus}">
                    <div class="construct-form__note" :class="{error:phone.error}">Для работы с ZemSkidki.ru лучше завести отдельный номер телефона,
                        <br> чтобы при звонке на него вы сразу понимали – это заказ от нас. Этот <br> номер будет доступен покупателю только после оформления купона.</div>
                </div>
            </div>
            <!-- end row-b -->


            <div class="row-b mt40">
                <div class="row-b__col elem-pos-fit">
                    <input-styled
                            name="operation"
                            v-model="operation.value"
                            @focus="operation.error = false"
                            :is-error="operation.error"
                            :placeholder="'Режим работы'"
                            :placeholder-input="'Например, «в любой будний день с 9:00 до 21:00»'"></input-styled>
                </div>
            </div>
            <!-- end row-b -->


            <div class="row-b mt40">
                <div class="row-b__col elem-pos-fit">
                    <input-styled
                            name="target"
                            v-model="target.value"
                            @focus="target.error = false"
                            :is-error="target.error"
                            :placeholder="'Суть звонка...'"
                            :placeholder-input="'Например, «заранее узнать стоимость» или «вызвать замерщика»...'"></input-styled>
                </div>
            </div>
            <!-- end row-b -->


            <div class="construct-use-preview">
                <div class="title">Проверьте, как считывается текст:</div>

                <div class="text">
                    <div class="text-bold">Чтобы воспользоваться скидкой, нужно:</div>

                    <ol>
                        <li>Оформить купон</li>
                        <li>Позвонить нам по телефону <span>{{ phone.value ? phone.value : '...'}}</span> <span>{{ operation.value ? ' ('+operation.value+')' : ''}}</span>, чтобы <span>{{ target.value ? target.value : '...'}}</span></li>
                    </ol>
                </div>
            </div>




            <div class="row-b mt32">
                <div class="row-b__col elem-pos-fix mr24">
                    <button @click="save" class="btn mr32">Продолжить</button>
                    <button @click="close" class="btn btn--grey">Отмена</button>
                </div>


                <div class="row-b__col elem-pos-fit">
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import {mapState} from 'vuex'
    import InputStyled from '@/components/forms/InputStyled.vue'
    import {isAnyPhone} from '@/helpers/validate.js';

    export default {
        name: "ConstructActionCallUs",
        components: {
            InputStyled,
        },
        props: {
            phoneProp: '',
            operationProp: '',
            targetProp: '',
        },
        data: function() {
            return {
                phone: {
                    value: '',
                    error: false,
                },
                operation: {
                    value: '',
                    error: false,
                },
                target: {
                    value: '',
                    error: false,
                },
                phoneFocus: false,
            }
        },
        created() {
            this.phone.value = this.phoneProp.value ? this.phoneProp.value : '';
            this.operation.value = this.operationProp.value ? this.operationProp.value : '';
            this.target.value = this.targetProp.value ? this.targetProp.value : '';
        },
        methods: {
            save() {
                this.phone.error = false;
                this.operation.error = false;
                this.target.error = false;

                let error = 0;
                if(!isAnyPhone(this.phone.value)){
                    setTimeout(()=>{
                        this.phone.error = true;
                    },150);

                    error++;
                }
                if(this.operation.value.length < 5){
                    setTimeout(()=>{
                        this.operation.error = true;
                    },150);

                    error++;
                }
                if(this.target.value.length < 5){
                    setTimeout(()=>{
                        this.target.error = true;
                    },150);

                    error++;
                }
                if(error === 0){
                    this.$emit('save',{
                        phone: this.phone,
                        operation: this.operation,
                        target: this.target
                    });
                    this.$store.commit('popups/close', {name: this.popups.constructActionCallUs.name});
                }
            },
            close() {
                this.$store.commit('popups/close', {name: this.popups.constructActionCallUs.name});
            }
        },
        computed: {
            ...mapState({
                popups: state => state.popups.items,
                masks: state => state.masks.items,
            }),
        },
        beforeDestroy(){
            this.$store.commit('popups/close', {name: this.popups.constructActionCallUs.name});
        }
    }
</script>

<style scoped>

</style>
