<template>
    <div class="content">
        <template v-if="isLoad">
            <div class="preloader-page">
                <div class="preloader"></div>
            </div>
        </template>
        <template v-else>
            <div class="main-title">
                <div class="main-title__wrap main-title--nowrap">
                    <div class="h1">{{provider.provider.ownership}}</div>
                </div>
                            <div class="main-title__right --icon">
                </div>
            </div>

            <section class="container">
                <tabs-outer @tabClick="tabClick" @selectedTab="tabClick"
                    class="coupon-details --wide"
                    tabsNavRight="">
                    <section :class="[rightBlockShow ? 'container__left' : 'container__full']">
                        <div>
                            <tab id="tab-about" name="О поставщике" :is-disabled="false">
                                    <provider-about class="coupon-details wrapper-content"
                                        :partner="provider.provider"
                                        :isModerator="true"
                                        :canEditText="false"
                                        :innOgrnCheck="false"
                                        :requisitesArr="provider.provider.requisitesArr" 
                                        :site="provider.provider.site" 
                                        :text="provider.provider.aboutText"></provider-about>
                            </tab>

                            <tab id="tab-action" name="Акции" :is-disabled="false">
                                <catalog-list-item v-for="coupon in provider.coupons"
                                                    :item="coupon"
                                                    :isModerator="true"
                                                    :key="coupon.id">
                                </catalog-list-item>
                            </tab>

                            <tab id="tab-request" name="Первичная заявка" :is-disabled="false">
                                <provider-request :item="provider"></provider-request>
                            </tab>

                            <tab id="tab-stat" name="Статистика" :is-disabled="false">
                                <provider-stat v-if="provider.stat.length>0" :item="provider"></provider-stat>
                            </tab>

                            <tab id="tab-history" name="История" :is-disabled="false">
                                <provider-history :item="provider"></provider-history>
                            </tab>

                            <tab id="tab-coupons" name="Новые купоны" :is-disabled="false">
                                <partner-repayment-list></partner-repayment-list>
                            </tab>

                            <tab id="tab-bills" name="Выставленные счета" :is-disabled="false">
                                <provider-bills></provider-bills>
                            </tab>
                        </div>
                    </section>
                    <aside v-show="rightBlockShow" class="aside aside--right">
                        <div class="aside__sticky js-sticky">
                            <div class="provider-request-contacts">
                                <div class="wrap">
                                    <div class="h2">Представитель</div>
                                    <div class="item">
                                        <div class="t" v-html="provider.request.position"></div>
                                        <div class="d text-bold">{{ providerName }}</div>
                                    </div>

                                    <div class="item">
                                        <div class="t">Телефон</div>
                                        <div class="d text-bold">
                                            <a v-if="provider.request.phone" class="text-bold" :href="'tel:'+provider.request.phone.replace(/[^\+0-9]/g, '')">{{ provider.request.phone }}</a>
                                            <a @click.prevent="openSendPopup" href="" class="d__send i i-mail2" title="Написать поставщику"></a>
                                        </div>
                                    </div>

                                    <div class="item">
                                        <div class="t">E-mail</div>
                                        <div class="d text-bold">
                                            {{provider.request.email}}
                                        </div>
                                    </div>

                                    <div class="h2">Бухгалтер</div>

                                    <div class="item">
                                        <div class="t">ФИО</div>
                                        <div class="d text-bold" v-if="provider.provider.bookerName" v-html="provider.provider.bookerName"></div>
                                        <div class="d text-bold" v-else>
                                            <span class="text-color-grey">Нет данных</span>
                                        </div>
                                    </div>

                                    <div class="item">
                                        <div class="t">Телефон</div>
                                        <div v-if="provider.provider.bookerPhone" class="d text-bold">
                                            <a class="text-bold" :href="'tel:'+provider.provider.bookerPhone.replace(/[^\+0-9]/g, '')">{{ provider.provider.bookerPhone }}</a>
                                            <a @click.prevent="openSendBuhPopup" href="" class="d__send i i-mail2" title="Написать бухгалтеру"></a>
                                        </div>
                                        <div v-else class="d text-bold">
                                            <span class="text-color-grey">Нет данных</span>
                                        </div>
                                    </div>

                                    <div class="item">
                                        <div class="t">E-mail</div>
                                        <div class="d text-bold" v-if="provider.provider.bookerEmail" v-html="provider.provider.bookerEmail"></div>
                                        <div class="d text-bold" v-else>
                                            <span class="text-color-grey">Нет данных</span>
                                        </div>
                                    </div>

                                </div>
                            </div>
                        </div>
                    </aside>
                </tabs-outer>
            </section>
        </template>
        <popup :is-set-styled="true" :show="popups.auditorBill.show" :name="popups.auditorBill.name">
            <auditor-bill></auditor-bill>
        </popup>
        <!-- Счёт успешно отправлен поставщику -->
        <popup :show="popups.billSendOk.show" :name="popups.billSendOk.name">
            <bill-send-ok></bill-send-ok>
        </popup>
        <!-- Пнуть поставщика -->
        <popup  :is-set-styled="true" :show="popups.kickPartner.show" :name="popups.kickPartner.name">
            <kick-partner></kick-partner>
        </popup>

        <!-- Заблокировать -->
        <popup v-if="!isLoad" :is-set-styled="true" :show="popups.auditorProviderBlock.show" :name="popups.auditorProviderBlock.name">
            <auditor-provider-block></auditor-provider-block>
        </popup>

        <!-- Разблокировать -->
        <popup v-if="!isLoad" :is-set-styled="true" :show="popups.auditorProviderCancelBlock.show" :name="popups.auditorProviderCancelBlock.name">
            <auditor-provider-cancel-block></auditor-provider-cancel-block>
        </popup>

                <!-- письмо поставщику -->
        <popup :is-set-styled="true" :show="popups.sendMessagePartner.show" :name="popups.sendMessagePartner.name">
            <send-message-partner type="buh" send-to="ouner" :id="provider.id"></send-message-partner>
        </popup>

        <!-- письмо бухгалтеру -->
        <popup :is-set-styled="true" :show="popups.sendMessagePartnerBuh.show" :name="popups.sendMessagePartnerBuh.name">
            <send-message-partner-buh type="buh" send-to="buh" :id="provider.id"></send-message-partner-buh>
        </popup>
    </div> 
</template>
<script>
    import {mapState} from 'vuex';
    import Popup from '@/components/modal/Popup.vue';
    import Tab from '@/components/tabs/Tab.vue';
    import TabsOuter from '@/components/tabs/TabsOuter.vue';
    import Loader from '@/components/Loader.vue';
    import PartnerRepaymentList from '@/components/auditor/PartnerRepaymentList.vue';
    import AuditorBill from '@/components/modal/AuditorBill.vue';
    import ProviderAbout from '@/components/moderator/ProviderAbout.vue';
    import ProviderBills from '@/components/auditor/ProviderBills.vue';
    import CatalogListItem from '@/components/catalog/CatalogListItem.vue'
    import ProviderRequest from '@/components/moderator/ProviderRequest.vue';  
    import ProviderStat from '@/components/moderator/ProviderStat.vue';    
    import ProviderHistory from '@/components/moderator/ProviderHistory.vue';   
    import BillSendOk from '@/components/modal/BillSendOk.vue';
    import KickPartner from '@/components/modal/KickPartner.vue';
    import AuditorProviderBlock from '@/components/modal/AuditorProviderBlock.vue';
    import AuditorProviderCancelBlock from '@/components/modal/AuditorProviderCancelBlock.vue';
    import expiringStorage from '@/helpers/tabsExpiringStorage.js';
    import SendMessagePartner from '@/components/modal/SendMessagePartner.vue';
    import SendMessagePartnerBuh from '@/components/modal/SendMessagePartnerBuh.vue';
    
     export default {
         metaInfo() {
             return {
                 title: this.title,
                 titleTemplate: this.$store.state.options.titleTemplate
             }
         },
         components: {
            TabsOuter,
            Tab,
            Loader,
            PartnerRepaymentList,
            Popup,
            AuditorBill,
            ProviderAbout,
            ProviderBills,
            CatalogListItem,
            ProviderRequest,
            ProviderStat,
            ProviderHistory,
            BillSendOk,
            KickPartner,
            AuditorProviderBlock,
            AuditorProviderCancelBlock,
            SendMessagePartner,
            SendMessagePartnerBuh,
         },
        mounted: function () {
           // this.$store.dispatch('moderatorCoupon/initDetailPage', {route: this.$route});
            this.$store.dispatch('auditorProviders/getItem', {id: this.$route.params.id, reload: true});
        },
        created() {},
        computed: {
            ...mapState({
                provider: state => state.auditorProviders.itemDetail,
                //items: state => state.auditorRepayments.items,
                isLoad: state => state.auditorProviders.isLoadDetail,
                popups: state => state.popups.items,
            }),
            providerName: function () {
                let result = this.provider.request.fio;
                if (this.provider.providerName) {
                    result = this.provider.providerName;
                }

                return result;
            },
            title(){
                return this.provider && this.provider.provider.ownership ? this.provider.provider.ownership : '';
            }
        },
        methods: {
            tabClick: function(tab){
                if(tab=='#tab-bills' || tab=='#tab-coupons'){
                    this.rightBlockShow = false;    
                }
                else{
                    this.rightBlockShow = true;    
                }
            },
            openSendPopup: function(){
                this.$store.dispatch('popups/open', {name: this.popups.sendMessagePartner.name});
            },
            openSendBuhPopup: function(){
                this.$store.dispatch('popups/open', {name: this.popups.sendMessagePartnerBuh.name});
            }
        },
        data() {
            return {
                rightBlockShow: true,
            }
        },
     }
</script>