<template>
    <div>
        <div class="popup__content">
            <div class="h1 mb8">Приехать...</div>
            
            
            <div class="row-b mt40">
                <div class="row-b__col elem-pos-fit">
                    <input-styled
                            name="where"
                            v-model="where.value"
                            :is-error="where.error"
                            @focus="where.error = false"
                            :placeholder="'Приехать к нам в...'"
                            :placeholder-input="'Например, «наш офис» или «любой из наших магазинов»'"></input-styled>
                </div>
                
            </div>
            <!-- end row-b -->
            
            
            <div class="row-b mt32" v-if="otherAddresses.length === 0">
                <div class="row-b__col elem-pos-fit">
                    <input-styled
                            name="address"
                            v-model="address.value"
                            :is-error="address.error"
                            @focus="address.error = false"
                            :placeholder="'По адресу...'"
                            :placeholder-input="'Укажите полный адрес'"></input-styled>
                </div>
            </div>
            <!-- end row-b -->
            
            
            <div class="row-b mt32" v-if="otherAddresses.length === 0">
                <div class="row-b__col elem-pos-fit">
                    <input-styled
                            name="operation"
                            v-model="operation.value"
                            :is-error="operation.error"
                            @focus="operation.error = false"
                            :placeholder="'Режим работы'"
                            :placeholder-input="'Например, «в любой будний день с 9:00 до 21:00»'"></input-styled>
                </div>
            </div>
            <!-- end row-b -->

            <div v-if="otherAddresses.length > 0" class="construct-use-address" style="position: relative;">
                <div class="el">
                    <input-styled
                            name="address"
                            v-model="address.value"
                            :is-error="address.error"
                            @focus="address.error = false"
                            :placeholder="'По адресу...'"
                            :placeholder-input="'Укажите полный адрес'"></input-styled>
                </div>

                <div class="el">
                    <input-styled
                            name="operation"
                            v-model="operation.value"
                            :is-error="operation.error"
                            @focus="operation.error = false"
                            :placeholder="'Режим работы'"
                            :placeholder-input="'Например, «в любой будний день с 9:00 до 21:00»'"></input-styled>
                </div>

                <div class="construct-use-address__l">
                    <span class="del" @click="delAddress()">
                        <span class="i i-close2"></span>
                    </span>
                </div>
            </div>
            
            
            <div class="construct-use-address" v-for="(item, index) in otherAddresses" style="position: relative;">
                <div class="el">
                    <input-styled
                            v-model="item.address.value"
                            :is-error="item.address.error"
                            @focus="item.address.error = false"
                            :placeholder="'По адресу...'"
                            :placeholder-input="'Укажите полный адрес'"></input-styled>
                </div>
                
                <div class="el">
                    <input-styled
                            v-model="item.operation.value"
                            :is-error="item.operation.error"
                            @focus="item.operation.error = false"
                            :placeholder="'Режим работы'"
                            :placeholder-input="'Например, «в любой будний день с 9:00 до 21:00»'"></input-styled>
                </div>
                
                <div class="construct-use-address__l">
                    <span class="del" @click="delOtherAddress(item.num)">
                        <span class="i i-close2"></span>
                    </span>
                </div>
                <a v-if="otherAddresses.length > 0 && +index + 1 === otherAddresses.length" href="javascript:void(0)" @click="addAddress" class="construct-use-add-address">Добавить ещё один адрес</a>
            </div>


            <div class="row-b mt32">
                <div class="row-b__col elem-pos-fit" style="position: relative; ">
                    <input-styled
                            v-model="target.value"
                            :is-error="target.error"
                            @focus="target.error = false"
                            :placeholder="'Чтобы...'"
                            :placeholder-input="'Например, «посмотреть образцы» или «заключить договор»'"></input-styled>
                      
                    <a v-if="otherAddresses.length === 0" href="javascript:void(0)" @click="addAddress" class="construct-use-add-address">Добавить ещё один адрес</a>
                </div>
            </div>
            <!-- end row-b -->
            
            
            <div class="construct-use-preview">
                <div class="title">Проверьте, как считывается текст:</div>
                
                <div class="text" v-if="otherAddresses.length === 0">
                    <div class="text-bold">Чтобы воспользоваться скидкой, нужно:</div>
                    
                    <ol>
                        <li>Оформить купон</li>
                        <li>
                            Приехать к нам в
                            <span v-if="where.value.length === 0">...</span>
                            <span v-else>{{ where.value }}</span>
                            по адресу
                            <span v-if="address.value.length === 0">...</span>
                            <span v-else>{{ address.value }} <span v-if="operation.value.length > 0">({{ operation.value }})</span></span>, чтобы
                            <span v-if="target.value.length === 0">...</span>
                            <span v-else>{{ target.value }}</span>
                        </li>
                    </ol>
                </div>
                
                <div class="text" v-else>
                    <div class="text-bold">Чтобы воспользоваться скидкой, нужно:</div>
                    
                    <ol>
                        <li>Оформить купон</li>
                        <li>
                            Приехать к нам в
                            <span v-if="where.value.length === 0">...</span>
                            <span v-else>{{ where.value }}</span>
                            по любому из указанных адресов, чтобы
                            <span v-if="target.value.length === 0">...</span>
                            <span v-else>{{ target.value }}</span>:<br>
                            
                            <ul class="no-disc">
                                <li v-if="address.value.length === 0">• ...</li>
                                <li v-else>• {{ address.value }} <span v-if="operation.value.length > 0">({{ operation.value }})</span></li>
                                <li v-for="otherAddress in otherAddresses">• {{ otherAddress.address.value }} ({{ otherAddress.operation.value }})</li>
                            </ul>
                        </li>
                    </ol>
                </div>
            </div>
            
            
            
            
            <div class="row-b mt32">
                <div class="row-b__col elem-pos-fix mr24">
                    <submit-btn tag="button"
                                class="btn mr32"
                                @click.native="save"
                                :type="buttonSave">
                        Продолжить
                    </submit-btn>
                    <button @click="close" class="btn btn--grey">Отмена</button>
                </div>
                
                
                <div class="row-b__col elem-pos-fit">
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import {mapState} from 'vuex'
    import InputStyled from '@/components/forms/InputStyled.vue'
    import SubmitBtn from '@/components/forms/SubmitBtn.vue'

    export default {
        name: "ConstructActionCome",
        components: {
            InputStyled,
            SubmitBtn,
        },
        props: {
            fields: {
                otherAddresses: [],
                where: '',
                address: '',
                operation: '',
                target: '',
            }
        },
        data: function() {
            return {
                otherAddresses: [],
                buttonSave: 'normal',
                where: {
                    value: '',
                    error: false,
                },
                address: {
                    value: '',
                    error: false,
                },
                operation: {
                    value: '',
                    error: false,
                },
                target: {
                    value: '',
                    error: false,
                },
            }
        },
        created() {
            for(let index in this.fields) {
                if(index === 'otherAddresses'){
                    this[index] = [];
                    for(let i in this.fields[index]){
                        this[index].push({
                            address:{...this.fields[index][i].address},
                            operation:{...this.fields[index][i].operation},
                            num:this.fields[index][i].num,
                            coord:{...this.fields[index][i].coord}
                        })
                    }

                } else if(this[index] !== undefined ) {
                    this[index].value = this.fields[index].value
                }
            }
        },
        methods: {
            addAddress() {
                let countNext = this.otherAddresses.length + 1;
                this.otherAddresses.push({
                    address: {
                        value: '',
                        error: false,
                    },
                    operation: {
                        value: '',
                        error: false,
                    },
                    num: countNext
                });
                this.$emit('resize');
            },
            save() {
                this.where.error = false;
                this.address.error = false;
                this.operation.error = false;
                this.target.error = false;
                for(let index in this.otherAddresses) {
                    this.otherAddresses[index].address.error = false;
                    this.otherAddresses[index].operation.error = false;
                }
                let error = 0;
                if(this.where.value.length < 3) {
                    setTimeout(()=>{
                        this.where.error = true;
                    },150);

                    error++;
                }
                if(this.address.value.length < 5) {
                    setTimeout(()=>{
                        this.address.error = true;
                    },150);

                    error++;
                }
                if(this.operation.value.length < 5) {
                    setTimeout(()=>{
                        this.operation.error = true;
                    },150);

                    error++;
                }
                if(this.target.value.length < 5) {
                    setTimeout(()=>{
                        this.target.error = true;
                    },150);

                    error++;
                }
                for(let index in this.otherAddresses) {
                    if(this.otherAddresses[index].address.value.length < 5) {
                        setTimeout(()=>{
                            this.otherAddresses[index].address.error = true;
                        },150);

                        error++;
                    }
                    if(this.otherAddresses[index].operation.value.length < 5) {
                        setTimeout(()=>{
                            this.otherAddresses[index].operation.error = true;
                        },150);

                        error++;
                    }
                }
                if(error === 0) {
                    this.buttonSave = 'load';
                    this.$store.dispatch('partnerActions/getCoord', {
                        address: this.address.value,
                        otherAddresses: this.otherAddresses
                    }).then((coords) => {
                        this.buttonSave = 'ok';
                        for(let index in this.otherAddresses) {
                            this.otherAddresses[index].coord = {value:coords.otherAddresses.shift()}
                        }
                        setTimeout(() => {
                            this.$emit('save',{
                                coord: {value:coords.address},
                                where: this.where,
                                address: this.address,
                                operation: this.operation,
                                target: this.target,
                                otherAddresses: this.otherAddresses
                            });
                            this.$store.commit('popups/close', {name: this.popups.constructActionComeUs.name});
                            this.buttonSave = 'normal';
                        }, 300);
                    }, () => {
                        this.buttonSave = 'normal';
                    })

                }
            },
            delAddress(){
                let item = this.otherAddresses.shift();
                this.address.value = item.address.value;
                this.operation.value = item.operation.value;
            },
            delOtherAddress(num) {
                for(let index in this.otherAddresses) {
                    if(this.otherAddresses[index].num === num) {
                        this.otherAddresses.splice(index, 1);
                    }
                }
                let number = 1;
                for(let index in this.otherAddresses) {
                    this.otherAddresses[index].num = number;
                    number++;
                }
                this.$emit('resize');
            },
            close() {
                this.$store.commit('popups/close', {name: this.popups.constructActionComeUs.name});
            }
        },
        computed: {
            ...mapState({
                popups: state => state.popups.items,
            }),
        }
    }
</script>

