<template>
    <div>
        <div class="text-bold">{{title}}</div>

        <div class="text" style="margin: 8px -8px -4px">
        <textarea v-model="text"
                  style="min-height: 96px;"
                  class="inp full"
                  :placeholder="placeholder"></textarea>
        </div>

        <div class="mt16">

            <submit-btn tag="span"
                        class="btn size-s"
                        @click.native="addItem"
                        :type="buttonState">
                Сохранить
            </submit-btn>
        </div>


        <div class="partner-info__notes" v-if="notes.length">
            <div v-for="note in notes" :key="note.id">
                <div class="t" v-html="note.text"></div>
                <div class="b">{{ note.date }} {{ note.moderator }}</div>
            </div>
        </div>
    </div>
</template>

<script>
    import {mapState} from 'vuex';
    import SubmitBtn from '@/components/forms/SubmitBtn.vue'

    export default {
        name: "Notes",
        components: {
            SubmitBtn
        },
        props: {
            entityId: {
                default: function () {
                    return ''
                }
            },
            type:{
                default: function () {
                    return 'provider'
                }   
            },
            title:{
                default: function () {
                    return 'Заметки'
                }   
            },
            placeholder:{
                default: function () {
                    return 'Например, «Напомнили про документы, ждем до 27 мая»'
                }       
            }
        },
        data: function () {
            return {
                text: ''
            }
        },
        methods: {
            setItems: function () {
                this.$store.dispatch('moderatorNotes/getItems', {entity_id: this.entityId, type: this.type});
            },
            addItem: function () {
                if(this.text.length>0){
                    this.$store.dispatch('moderatorNotes/addItem', {entity_id: this.entityId, type: this.type, text: this.text});
                    this.text = '';
                }
            },
        },
        created() {
            this.setItems();
        },
        computed: {
            ...mapState({
                notes: function (state) {
                    return state.moderatorNotes.items
                },
                noteFormLoading: state => state.moderatorNotes.isFormLoad,
            }),
            buttonState: function () {
                let result = 'normal';
                if (this.noteFormLoading) {
                    result = 'load';
                } else if (!this.text.length) {
                    result = 'disabled'
                }
                return result;
            }
        }
    }
</script>
