<template>
    <div>
        <div class="popup__header">
            <div class="title">Скрыть купоны</div>

            <div class="subtitle">Сотрудник: Сорокин Павел</div>
            <div class="text-color-white-trans mt16">
                Синэл-Электрика (Москва)
            </div>
        </div>


        <div class="popup__content">
            <h2>На сколько скрыть?</h2>

            <div class="mt16 time">
              <div class="input-radio mr24">
                <input type="radio" name="hours" id="h24" checked>
                <label for="h24">24 часа</label>
              </div>
              <div class="input-radio mr24">
                <input type="radio" name="hours" id="h48">
                <label for="h48">48 часов</label>
              </div>
              <div class="input-radio">
                <input type="radio" name="hours" id="h72">
                <label for="h72">72 часа</label>
              </div>
            </div>



            <div class="h2">Служебный комментарий</div>

            <div class="mt16">
                <textarea-field  :placeholder="text.placeholder"
                                 v-model="text.value"
                                 :auto-focus="true"
                                 :is-error="text.error"
                                 @focus="removeError(text.name)"
                                 :add-style="{height: 80 + 'px'}"
                ></textarea-field>
            </div>




            <div class="row-b mt32">
                <div class="row-b__col elem-pos-fix mr24">
                    <button class="btn" >
                        <span>Подтвердить</span>
                    </button>
                </div>


                <div class="row-b__col elem-pos-fit">
                    Напишите комментарий, чтобы впоследствии вспомнить,<br>о чём вы договорились с поставщиком.
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import {mapState} from 'vuex'
    import TextareaField from '@/components/forms/TextareaField.vue'

    export default {
        name: "HideCoupon",
        components: {
            TextareaField
        },
        props: {
            action: {
                default() {
                    return {}
                }
            }
        },
        data() {
            return {
                text: {
                    name: 'text',
                    value: '',
                    required: true,
                    error: false,
                    disabled: false,
                    placeholder: 'Например, «Поставщик поклялся обзвонить всех в течение суток...»',
                },
            }
        },
        methods: {
            removeError (field) {
                this[field].error = false;
            },
        },
        computed: {
            ...mapState({
                popups: state => state.popups.items,
            }),
        }
    }
</script>

<style scoped>

