<template>
    <div class="">
        <div class="buh-coupons mt0" v-if="items.length > 0">
            <div class="item">
                <div class="item__title"><span class="item__buyer">
                        Покупатель
                    </span><span class="item__paid"> 
                        Погашен
                    </span><span class="item__sum">
                        Сумма покупки
                    </span><span class="item__discount">
                        Скидка
                        </span><span class="item__transfer">
                        Поступило в кассу
                    </span><span class="item__percent">
                        Коммиссия 3%
                    </span>
                </div>
            </div>
            <div class="item"  v-for="item in items" :key="item.id">
                <div class="item__link">
                    <span class="item__buyer">{{item.phone}}</span>
                    <span class="item__paid">{{item.date.pay}}</span>
                    <span class="item__sum">{{item.price}} <span class="rouble">{{ $store.getters['options/setRouble'] }}</span></span>
                    <span class="item__discount">{{item.discount}} <span class="rouble">{{ $store.getters['options/setRouble'] }}</span></span>
                    <span class="item__transfer">{{item.transfer}} <span class="rouble">{{ $store.getters['options/setRouble'] }}</span></span>
                    <span class="item__percent">{{item.our}} <span class="rouble">{{ $store.getters['options/setRouble'] }}</span></span>
                </div>
            </div>
            
            
            <div class="item" v-if="items.length > 1">
                <div class="item__total">
                    Общая комиссия за <strong>{{ items.length }}</strong> {{declOfNumber(items.length,['купон','купона','купонов'])}}: <strong>{{ number_format(totalCommission) }} <span class="rouble">{{ $store.getters['options/setRouble'] }}</span></strong>
                </div>
            </div>
            <div class="item">
                <div class="item__total">
                    <a @click="openAuditorBill" class="btn">Выставить счет</a>
                </div>
            </div>
        </div>
        
        
        
        <div class="wrapper-content mt32" v-else>
            <div class="sect-blank">
                <div class="wrap text-center">
                    <loader v-if="isLoad"></loader>
                    <div class="text-size-l text-bolder text-color-grey mt16" v-else>У этого поставщика нет купонов, по которым ещё не выставлены счета...</div>
                </div>
                
                <div class="mini-zems"></div>
            </div>
            <!-- end sect-blank -->
        </div>
    </div>
</template>



<script>
    import {mapState} from 'vuex';
    import {number_format} from '@/helpers/formatted.js';
    import {declOfNumber} from '@/helpers/formatted.js';
    import Loader from '@/components/Loader.vue';
    export default {
        components: {
            Loader
        },
        mounted: function () {
            //this.$store.dispatch('auditorRepayments/getItems', {id: this.$route.params.id, reload: true});
        },
        created() {},
        computed: {
            ...mapState({
                items: state => state.auditorProviders.itemDetail.repayments,
                isLoad: state => state.auditorProviders.isLoadDetail,
                popups: state => state.popups.items,
            }),
            totalCommission () {
                //return 0;
                return this.$store.getters['auditorProviders/getTotalCommission'];
            },
        },
        methods: {
            number_format: function (v) {
                return number_format(v, 0, '', ' ')
            },
            openAuditorBill: function() {
                this.$store.dispatch('popups/open', {name: this.popups.auditorBill.name});
            },
            declOfNumber(number, titles){
                return declOfNumber(number, titles);
            },
        },
    }

</script>
