<template>
    <div>
        <div class="popup__header popup__header--dotted --bg-green">
            <div class="title">Опубликовать промо-страницу</div>
            <div class="subtitle" v-html="item.ownership"></div>
        </div>


        <div class="popup__content">
            <div class="h2 mb16">Служебный комментарий</div>

            <textarea-field @focus="removeError('commentError')" v-model="form.comment" :is-error="form.commentError"  :placeholder="'Прокомментируйте своё решение'" :add-style="{minHeight: 88 + 'px'}"></textarea-field>


            <div class="h2 mb16">СМС последнему редактору на {{this.form.sendPhone}}</div>

            <textarea-field :hasCounterSMS="true" @focus="removeError('smsTextError')" :is-error="form.smsTextError" v-model="form.smsText" ></textarea-field>



            <div class="h2 mb16">Письмо последнему редактору на {{this.form.sendEmail}}</div>

            <div class="mb8">
                <input type="text" class="inp size-s full" v-model="form.emailTitle">
            </div>

            <!--<textarea-field  :add-style="{minHeight: 333 + 'px'}" v-model="form.emailText"></textarea-field>-->
            <text-editor-min v-model="form.emailText" @click.native="removeError('emailTextError')" :error="form.emailTextError" :hide-toolbar="true" :value-default="form.emailText" />

            <div class="row-b mt32">
                <div class="row-b__col elem-pos-fix mr24">
                    <submit-btn class="btn--green" @click.native="send" :type="form.btnType">Подтвердить</submit-btn>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import TextareaField from '@/components/forms/TextareaField.vue'
    import {mapState} from 'vuex';
    import TextEditorMin from '@/components/editor/TextEditorMin.vue'
    import SubmitBtn from '@/components/forms/SubmitBtn.vue'

    export default {
        name: "ProviderApproveAbout",
        components: {
            TextareaField,
            TextEditorMin,
            SubmitBtn
        },
        methods: {
            send: function () {
                this.form.commentError = false;
                if (this.form.comment.length < 10) {
                    setTimeout(()=>{
                        this.form.commentError = true;
                    },150);
                } else {
                    if (!this.formLoad) {
                        this.formLoad = true;
                        this.form.commentError = false;
                        this.form.btnType = 'load';
                        this.$store.dispatch('moderatorProviders/sendForm', {
                            update: 'provider_approve_about',
                            id: this.item.id,
                            comment: this.form.comment,
                            smsText: this.form.smsText,
                            emailTitle: this.form.emailTitle,
                            emailText: this.form.emailText,
                            formLoad: false,
                            btnType: 'normal'
                        }).then(() => {
                            this.form.formLoad = false;
                                this.form.btnType = 'ok';
                            setTimeout(() => {
                                this.$store.commit('popups/close', {name: this.popups.providerApproveAbout.name});
                                if(this.$route.name=='provider-edit'){
                                    this.$store.dispatch('moderatorProviders/getItem', {id: this.item.id});
                                }
                                if(this.$route.name=='coupons-detail'){
                                    this.$store.dispatch('moderatorCoupon/initDetailPage', {route: this.$route});
                                }
                            }, 300);
                        });
                    }
                }
            },
            removeError(field) {
                this.form[field] = false;
            }
        },
        data: function () {
            return {
                form: {
                    sendPhone: '',
                    sendEmail: '',
                    comment: '',
                    commentError: false,
                    formLoad: false,
                    btnType: 'normal',
                    smsText: '',
                    emailTitle: '',
                    emailText: '',
                }
            }
        },
        mounted() {
            if(this.templatesCoupon.provider_approve_about_sms !== undefined){
                this.form.smsText = this.templatesCoupon.provider_approve_about_sms.text;
                this.form.emailTitle = this.templatesCoupon.provider_approve_about_email.title;
                this.form.emailText = this.templatesCoupon.provider_approve_about_email.text;
                this.form.sendPhone = this.templatesCoupon.provider_approve_about_sms.recipient;
                this.form.sendEmail= this.templatesCoupon.provider_approve_about_email.recipient;
            } else {
                this.form.smsText = this.templates.provider_approve_about_sms.text;
                this.form.emailTitle = this.templates.provider_approve_about_email.title;
                this.form.emailText = this.templates.provider_approve_about_email.text;
                this.form.sendPhone = this.templates.provider_approve_about_sms.recipient;
                this.form.sendEmail= this.templates.provider_approve_about_email.recipient;
            }
        },
        computed: {
            ...mapState({
                popups: state => state.popups.items, 
                templates: state => state.moderatorProviders.itemDetailTemplates,
                templatesCoupon: state => state.moderatorCoupon.itemDetailTemplates
            }),
            item(){
                if(this.$store.state.moderatorProviders.itemDetail.id === undefined){
                    return this.$store.state.moderatorCoupon.itemDetail.provider;
                }
                else{
                    return this.$store.state.moderatorProviders.itemDetail;
                }
            }
        }
    }
</script>