<template>
    <div class="content" :class="{'content-edit': isEdit}">
        <div class="main-title" v-if="!isEdit">
            <div class="main-title__wrap main-title--nowrap">
                <div class="h0" style="font-size: 48px">{{ partner.requestProviderName }}, здравствуйте!</div>

                <div class="text-size-l mt16">Чтобы активировать личный кабинет, заполните договор</div>
            </div>


            <div class="main-title__right">
            </div>
        </div>



        <section class="container" :class="{'container-edit': isEdit}">
            <div class="container__full">
                <div class="wrapper-content wrapper-content-inner contract mt32" :class="{'wrapper-content-nobg': isEdit}">
                    <div class="contract__title">
                        Агентский договор # {{ partner.contract.number }}
                    </div>


                    <div class="contract__top">
                        <div class="c">{{ partner.contract.city }}</div>

                        <div class="d">{{ partner.contract.date }}</div>
                    </div>


                    <div class="contract__appeal">
                        <tooltip style="margin-top: -3px; "
                                 :direction="'top-center-direction'"
                                 :add-class-wrap="'dib'"
                                 :add-style="{width: 337 + 'px'}">
                            <span slot="tooltipBtn" class="pseudo-link link-color">ИП Савкина Екатерина Николаевна,</span>

                            <div slot="tooltipContent">
                                <span class="text-size-s">Екатерина Николаевна – наш главный бухгалтер и&nbsp;владелец проекта с&nbsp;юридической точки зрения. Все договоры о&nbsp;сотрудничестве заключаются от её имени.</span>
                            </div>
                        </tooltip>
                        именуемая в дальнейшем «Агент», с одной стороны и

                    </div>

                    <div class="contract__form">
                        <div class="contract__form__item">
                            <div class="row-b">
                                <div class="row-b__col elem-pos-fix">
                                    <tooltip ref="tooltipOwnership"
                                             :direction="'bottom-left-direction'"
                                             :is-btn-close="false"
                                             :add-class-wrap="'dib'"
                                             :add-style="{width: 253 + 'px'}">
                                        <span slot="tooltipBtn" class="pseudo-select">{{ ownershipTypeTitle[company.ownership.value] }}</span>

                                        <div slot="tooltipContent">
                                            <div class="text-size-s text-bold">Какая у вас форма бизнеса?</div>
                                            <div class="text-size-s mt8">От этого зависит форма договора...</div>

                                            <div class="mt16">
                                                <button @click="setOwnership(ownershipType.ooo)" class="btn btn--white size-xm">ООО</button>
                                                <button @click="setOwnership(ownershipType.ip)" class="btn btn--white size-xm ml">ИП</button>
                                                <button @click="setOwnership(ownershipType.ao)" class="btn btn--white size-xm ml">АО</button>
                                            </div>
                                        </div>
                                    </tooltip>
                                </div>

                                <div class="row-b__col elem-pos-fit"  >
                                    <input-styled
                                             name="nameCompany"
                                             key="nameCompany"
                                            id="company-nameCompany"
                                            v-if="isLegalEntity(company.ownership.value)"
                                            v-model="company.nameCompany.value"
                                            :mask="companyMask"
                                            :is-error="company.nameCompany.error"
                                            @focus="focusCompanyName()"
                                            :class="{'error': company.nameCompany.error}"
                                            :class-btn="'click-outside-tooltip'"
                                            :placeholder="'Название компании'"
                                            :placeholder-input="''"></input-styled>
                                    <input-multi
                                            key="nameCompanyIP"
                                            id="company-nameIP"
                                            v-else
                                            @chunk="setPersonInputIP"
                                            :is-error="company.nameIP.error"
                                            v-model="company.nameIP.value"
                                            :fields="company.nameIP.fields"
                                            @focus="removeFieldError('nameIP')"
                                            :placeholder="'ФИО'"></input-multi>
                                </div>
                            </div>
                            <!-- end row-b -->



                            <div class="row-b" v-if="isLegalEntity(company.ownership.value)">
                                <div class="row-b__col elem-pos-fix" >
                                    <span class="note">в лице</span>
                                </div>

                                <div id="company-ceo" class="row-b__col elem-pos-fix" style="width: 392px; min-width: 392px;">
                                    <input-styled
                                             name="ceo"
                                             key="ceo"
                                            v-model="company.ceo.value"
                                            @focus="removeFieldError('ceo')"
                                            :is-error="company.ceo.error"
                                            :placeholder="'Должность (в родительном падеже)'"
                                            :placeholder-input="'Например, «Генерального директора»'"></input-styled>
                                </div>

                                <div class="row-b__col elem-pos-fit">
                                    <input-multi
                                            key="nameOOO"
                                            id="company-nameOOO"
                                            @chunk="setPersonInputOOO"
                                            :is-error="company.nameOOO.error"
                                            v-model="company.nameOOO.value"
                                            :fields="company.nameOOO.fields"
                                            @focus="removeFieldError('nameOOO')"
                                            :placeholder="'ФИО (в родительном падеже)'"></input-multi>
                                </div>
                            </div>
                            <!-- end row-b -->



                            <div class="row-b" v-if="isLegalEntity(company.ownership.value)">
                                <div class="row-b__col elem-pos-fix" >
                                    <span class="note">действующего на основании</span>
                                </div>

                                <div class="row-b__col elem-pos-fit">
                                    <input-styled
                                             key="credentials"
                                             name="credentials_skidki"
                                            id="company-credentials"
                                            :is-error="company.credentials.error"
                                            v-model="company.credentials.value"
                                            @focus="removeFieldError('credentials')"
                                            :placeholder="'Полномочия (в родительном падеже)'"
                                            :placeholder-input="'Например, «Устава» или «Доверености №15 от 18.02.12 и т.д.»'"></input-styled>
                                </div>
                            </div>
                            <!-- end row-b -->


                            <div class="mt32">
                                <div class="text-size-sl">
                                    <span v-if="isLegalEntity(company.ownership.value)">именуемое</span><span v-else>именуемый(ая)</span> в дальнейшем «Принципал», с другой стороны, а совместно именуемые «Стороны», заключили<br v-if="isLegalEntity(company.ownership.value)">
                                    настоящий договор (далее «Договор») о нижеследующем:
                                </div>
                            </div>
                        </div>
                        <!-- end contract__form__item -->
                    </div>

                    <div class="contract__info">
                        <item-show  v-for="clause in partner.contract.clauses" :key="clause.id" :prop-title="clause.name">
                            <p v-html="clause.text"></p>
                        </item-show>
                        <div class="item open --open-always">
                            <div class="item__title" style="cursor: default;">
                                <span>{{ partner.contract.requisites.title }}</span>
                            </div>
                            <div class="contract__title">
                                Агент <span class="text-color-grey-light">(Это мы)</span>
                            </div>

                            <div class="contract__requisites" v-html="partner.contract.requisites.text"></div>

                            <div class="contract__title">
                                Принципал <span class="text-color-grey-light">(Это вы)</span>
                            </div>

                            <div class="contract__form__item" id="company-inn">
                                <div class="row-b">
                                    <div class="row-b__col elem-pos-fix" style="width: 378px; min-width: 378px; ">
                                        <input-styled
                                                 name="innIp"
                                                v-if="company.ownership.value == ownershipType.ip"
                                                v-model="company.inn.value"
                                                :mask="innIpMask"
                                                key="innIp"
                                                @change="validateLength('inn')"
                                                @focus="removeFieldError('inn')"
                                                :is-error="company.inn.error"
                                                :class="{error: company.inn.error}"
                                                :placeholder="'ИНН'"
                                                :placeholder-input="'12 цифр'"></input-styled>
                                        <input-styled
                                                 name="inn"
                                                v-else
                                                key="innOOO"
                                                :placeholder="'ИНН'"
                                                v-model="company.inn.value"
                                                :is-error="company.inn.error"
                                                :mask="innMask"
                                                @focus="removeFieldError('inn')"
                                                :class="{error: company.inn.error}"
                                                :placeholder-input="'10 цифр'"></input-styled>
                                    </div>
                                </div>
                                <!-- end row-b -->


                                <div class="row-b" id="company-ogrn">
                                    <div class="row-b__col elem-pos-fix" style="width: 378px; min-width: 378px; ">
                                        <input-styled
                                                 name="ogrnip"
                                                v-if="company.ownership.value == ownershipType.ip"
                                                :mask="ogrnIpMask"
                                                :is-error="company.ogrnip.error"
                                                v-model="company.ogrnip.value"
                                                @focus="removeFieldError('ogrnip')"
                                                key="ogrnip"
                                                :placeholder="'ОГРНИП'"
                                                :placeholder-input="'15 цифр'"></input-styled>
                                        <input-styled
                                                 name="ogrn"
                                                v-else
                                                :mask="ogrnMask"
                                                :is-error="company.ogrn.error"
                                                v-model="company.ogrn.value"
                                                @focus="removeFieldError('ogrn')"
                                                key="ogrn"
                                                :placeholder="'ОГРН'"
                                                :placeholder-input="'13 цифр'"></input-styled>
                                    </div>
                                </div>
                                <!-- end row-b -->


                                <div class="row-b mt32">
                                    <div class="row-b__col" style="margin-right: 21px">
                                        <div>
                                            <div class="row-b mt0">
                                                <file-upload
                                                        v-if="!existsFiles.inn"
                                                        v-model="company.innFile.file"
                                                        key="innFile"
                                                        :formParams="{IS_EDIT: this.isEdit, PROVIDER_ID: this.$route.params.id}"
                                                        :error="company.innFile.error"
                                                        @change="company.innFile.error = false"
                                                        :field-name="'innFile'"
                                                        action-send="partner/sendContractFile"
                                                        action-delete="partner/deleteContractFile"
                                                        @error="checkError"
                                                        :btn-text="'Загрузить скан ИНН'"
                                                        hint="(jpg, png или pdf, до 10Мб)"
                                                        :accept-type="'image/jpeg,image/png,application/pdf'">
                                                </file-upload>
                                                <div v-else class="row-b__col elem-pos-fix --has-file"
                                                     style="width: 240px;">
                                                    <span class="added-file-text">
                                                        <span
                                                        class="t">{{ partner.contract.innFileName }}</span>
                                                        <span @click="clearExistFile('inn')" class="del"></span>
                                                    </span>
                                                </div>
                                            </div>


                                            <div class="row-b" style="margin-top: 23px">
                                                <file-upload
                                                        v-if="!existsFiles.ogrn"
                                                        v-model="company.ogrnFile.file"
                                                        key="ogrnFile"
                                                        :formParams="{IS_EDIT: this.isEdit, PROVIDER_ID: this.$route.params.id}"
                                                        :error="company.ogrnFile.error"
                                                        @change="company.ogrnFile.error = false"
                                                        action-send="partner/sendContractFile"
                                                        action-delete="partner/deleteContractFile"
                                                        :field-name="'ogrnFile'"
                                                        @error="checkError"
                                                        :btn-text="isLegalEntity(company.ownership.value) ? 'Загрузить скан ОГРН' : 'Загрузить скан ОГРНИП'"
                                                        hint="(jpg, png или pdf, до 10Мб)"
                                                        :accept-type="'image/jpeg,image/png,application/pdf'">
                                                </file-upload>
                                                <div v-else class="row-b__col elem-pos-fix --has-file"
                                                     style="width: 240px;">
                                                    <span class="added-file-text">
                                                        <span
                                                                class="t">{{ partner.contract.ogrnFileName }}</span>
                                                        <span @click="clearExistFile('ogrn')" class="del"></span>
                                                    </span>
                                                </div>
                                            </div>
                                        </div>
                                    </div>


                                    <div class="row-b__col">
                                        <span class="text-size-m text-color-grey">Если у вас документы нового образца, то загрузите скан листа записи два раза.<br> Допускаются форматы jpg, png или pdf, максимальный вес — до 10Мб</span>
                                    </div>
                                </div>



                                <div id="company-kpp" class="row-b" v-if="isLegalEntity(company.ownership.value)">
                                    <div class="row-b__col elem-pos-fix"
                                         style="width: 378px; min-width: 378px; ">
                                        <input-styled
                                                 key="kpp"
                                                 name="kpp"
                                                 :mask="bikMask"
                                                v-model="company.kpp.value"
                                                @focus="removeFieldError('kpp')"
                                                :is-error="company.kpp.error"
                                                :placeholder="'КПП'"
                                                :placeholder-input="'9 цифр'"></input-styled>
                                    </div>

                                </div>

                                <div id="company-settlementAccount" class="row-b" v-if="company.ownership.value == ownershipType.ip">
                                    <div class="row-b__col elem-pos-fix"
                                         style="width: 378px; min-width: 378px; ">
                                        <input-styled
                                                 name="settlementAccount"
                                                v-if="company.settlementAccount.notUse"
                                                key="settlementAccountIPnotUse"
                                                :disabled="true"
                                                value="нет"
                                                :placeholder="'Расчётный счет'"
                                                :placeholder-input="'20 цифр'"></input-styled>
                                        <input-styled
                                                 name="settlementAccount"
                                                v-else
                                                key="settlementAccountIP"
                                                :mask="ksMask"
                                                v-model="company.settlementAccount.value"
                                                @focus="removeFieldError('settlementAccount')"
                                                :is-error="company.settlementAccount.error"
                                                :placeholder="'Расчётный счет'"
                                                :placeholder-input="'20 цифр'"></input-styled>

                                    </div>

                                    <div class="row-b__col elem-pos-fit" v-if="company.settlementAccount.error">
                                        <div class="error text-size-s ">Перепроверьте рассчетный счет и БИК банка. Эти цифры связаны и где-то здесь есть ошибка.</div>
                                    </div>
                                    <div class="row-b__col elem-pos-fit" v-else>
                                        <input id="provider-lk-create-company-input-settlement-account-use" v-model="company.settlementAccount.notUse"
                                               type="checkbox"
                                               class="opinion-rate__like"/>
                                        <label for="provider-lk-create-company-input-settlement-account-use" class="text-size-s text-color-grey">
                                            Я работаю без расчётного счёта
                                        </label>
                                    </div>

                                </div>

                                <div id="company-settlementAccount" class="row-b" v-else>
                                    <div class="row-b__col elem-pos-fix"
                                         style="width: 378px; min-width: 378px; ">
                                        <input-styled
                                                 name="settlementAccount"
                                                key="settlementAccountOOO"
                                                 :mask="ksMask"
                                                v-model="company.settlementAccount.value"
                                                @focus="removeFieldError('settlementAccount')"
                                                :is-error="company.settlementAccount.error"
                                                :placeholder="'Расчётный счет'"
                                                :placeholder-input="'20 цифр'"></input-styled>
                                    </div>
                                    <div class="row-b__col elem-pos-fit" v-if="company.settlementAccount.error">
                                        <div class="error text-size-s ">Перепроверьте рассчетный счет и БИК банка. Эти цифры связаны и где-то здесь есть ошибка.</div>
                                    </div>
                                </div>

                                <div class="row-b" v-if="company.ownership.value == ownershipType.ip">
                                    <div  id="company-regAddress" class="row-b__col full">
                                        <input-styled
                                                name="regAddress"
                                                key="regAddress"
                                                v-model.trim="company.regAddress.value"
                                                :is-error="company.regAddress.error"
                                                @focus="removeFieldError('regAddress')"
                                                :placeholder="'Адрес регистрации'"
                                                :placeholder-input="'Укажите полный адрес, включая почтовый индекс'"></input-styled>
                                    </div>
                                </div>
                                <!-- end row-b -->

                                <div class="row-b" v-if="company.ownership.value == ownershipType.ip">
                                    <div  id="company-postAddress" class="row-b__col full">
                                        <input-styled
                                                 key="postAddressIp"
                                                 name="postAddress"
                                                v-model.trim="company.postAddress.value"
                                                :is-error="company.postAddress.error"
                                                @focus="removeFieldError('postAddress')"
                                                :placeholder="'Почтовый адрес'"
                                                :placeholder-input="'Адрес для почтовых отправлений, включая индекс. Может совпадать с адресом регистрации'"></input-styled>
                                    </div>
                                </div>
                                <!-- end row-b -->



                                <div  id="company-settlementAccountBank" class="row-b" v-if="company.ownership.value == ownershipType.ip && !company.settlementAccount.notUse || isLegalEntity(company.ownership.value)">
                                    <div class="row-b__col full">
                                        <input-styled
                                                key="settlementAccountBank"
                                                 name="settlementAccountBank"
                                                :placeholder="'Ваш банк'"
                                                v-model="company.settlementAccountBank.value"
                                                :is-error="company.settlementAccountBank.error"
                                                @focus="removeFieldError('settlementAccountBank')"
                                                :placeholder-input="'Полное наименование банка и его форма бизнеса, Например, «ПАО “Сбербанк”...»'"></input-styled>
                                    </div>

                                    <div class="row-b__col elem-pos-fit">

                                    </div>
                                </div>
                                <!-- end row-b -->


                                <div id="company-settlementAccountBankBik" class="row-b" v-if="company.ownership.value == ownershipType.ip && !company.settlementAccount.notUse || isLegalEntity(company.ownership.value)">
                                    <div class="row-b__col elem-pos-fix" style="width: 378px; min-width: 378px; ">
                                        <input-styled
                                                key="bikMask"
                                                 name="settlementAccountBankBik"
                                                :mask="bikMask"
                                                :placeholder="'БИК банка'"
                                                v-model="company.settlementAccountBankBik.value"
                                                :is-error="company.settlementAccountBankBik.error"
                                                @focus="removeFieldError('settlementAccountBankBik')"
                                                :placeholder-input="'9 цифр'"></input-styled>
                                    </div>
                                </div>
                                <!-- end row-b -->


                                <div  id="company-settlementAccountBankCor" class="row-b" v-if="company.ownership.value == ownershipType.ip && !company.settlementAccount.notUse || isLegalEntity(company.ownership.value)">
                                    <div class="row-b__col elem-pos-fix" style="width: 378px; min-width: 378px; ">
                                        <input-styled
                                                key="settlementAccountBankCor"
                                                 name="settlementAccountBankCor"
                                                :placeholder="'к/с'"
                                                :mask="ksMask"
                                                v-model="company.settlementAccountBankCor.value"
                                                :is-error="company.settlementAccountBankCor.error"
                                                @change="validateLength('settlementAccountBankCor')"
                                                @focus="removeFieldError('settlementAccountBankCor')"
                                                :placeholder-input="'20 цифр'"></input-styled>
                                    </div>

                                    <div class="row-b__col elem-pos-fit" v-if="company.settlementAccountBankCor.error && company.settlementAccountBankCor.value.length === company.settlementAccountBankCor.length">
                                        <div class="error text-size-s ">Перепроверьте корр. счет и БИК банка. Эти цифры связаны и где-то здесь есть ошибка.</div>
                                    </div>
                                    <div class="row-b__col elem-pos-fit" v-if="company.settlementAccountBankCor.error && company.settlementAccountBankCor.value.length > company.settlementAccountBankCor.length">
                                        <div class="error text-size-s ">должно быть {{ company.settlementAccountBankCor.length }} цифр</div>
                                    </div>
                                </div>
                                <!-- end row-b -->

                                <div id="company-urAddress" class="row-b" v-if="isLegalEntity(company.ownership.value)">
                                    <div class="row-b__col full">
                                        <input-styled
                                                key="urAddress"
                                                 name="urAddress"
                                                v-model.trim="company.urAddress.value"
                                                :is-error="company.urAddress.error"
                                                @focus="removeFieldError('urAddress')"
                                                :placeholder="'Юридический адрес'"
                                                :placeholder-input="'Укажите полный адрес, включая почтовый индекс'"></input-styled>
                                    </div>
                                </div>
                                <!-- end row-b -->




                                <div  id="company-postAddress" class="row-b" v-if="isLegalEntity(company.ownership.value)">
                                    <div class="row-b__col full">
                                        <input-styled
                                                 key="postAddress"
                                                 name="postAddress"
                                                v-model.trim="company.postAddress.value"
                                                :is-error="company.postAddress.error"
                                                @focus="removeFieldError('postAddress')"
                                                :placeholder="'Почтовый адрес'"
                                                :placeholder-input="'Адрес для почтовых отправлений, включая индекс. Может совпадать с адресом регистрации'"></input-styled>
                                    </div>
                                </div>
                                <!-- end row-b -->
                            </div>
                            <!-- end contract__form__item -->

                            <div class="contract__title">
                                Бухгалтерия
                            </div>

                            <div class="mt24 text-size-sl" v-if="company.ownership.value == ownershipType.ip">
                                Укажите ФИО, телефон и почту вашего главного бухгалтера. Если у вас его нет, укажите контакты<br>
                                лица, отвечающего за оплату счетов и другие бухгалтерские штуки.
                            </div>
                            <div class="mt24 text-size-sl" v-else>Укажите телефон и почту вашего бухгалтера. Если они совпадают с телефоном и почтой владельца<br> компании, укажите их повторно...</div>



                            <div class="contract__form__item mt44">

                                <div class="row-b">
                                    <div class="row-b__col elem-pos-fit">
                                        <input-multi
                                                id="company-bookerName"
                                                @chunk="setPersonInputBooker"
                                                :is-error="company.bookerName.error"
                                                v-model="company.bookerName.value"
                                                :fields="company.bookerName.fields"
                                                @focus="removeFieldError('bookerName')"
                                                :placeholder="'ФИО главного бухгалтера'"></input-multi>
                                    </div>
                                </div>
                                <!-- end row-b -->


                                <div id="company-bookerPhone" class="row-b">
                                    <div class="row-b__col elem-pos-fix" style="width: 378px; min-width: 378px; ">
                                        <input-styled
                                                key="bookerPhone"
                                                 name="bookerPhone"
                                                v-model="company.bookerPhone.value"
                                                :mask="phoneMask"
                                                :is-block-arrow-phone="true"
                                                :is-error="company.bookerPhone.error"
                                                @focus="focus('bookerPhone')"
                                                @blur="blur('bookerPhone')"
                                                :placeholder="'Телефон бухгалтера'"
                                                :placeholder-input="'+7(___)___-__-__'"></input-styled>
                                    </div>

                                    <div class="row-b__col elem-pos-fit"
                                         :class="{'--active':company.bookerPhone.focus}">
                                        <span class="text-size-s construct-form__note">На этот номер будут приходить уведомления о выставленных счетах</span>
                                    </div>
                                </div>
                                <!-- end row-b -->



                                <div  id="company-bookerEmail" class="row-b">
                                    <div class="row-b__col elem-pos-fix" style="width: 378px; min-width: 378px; ">
                                        <input-styled
                                                key="pochta"
                                                 name="pochta"
                                                v-model="company.bookerEmail.value"
                                                @focus="focus('bookerEmail')"
                                                @blur="blur('bookerEmail')"
                                                :is-error="company.bookerEmail.error"
                                                :placeholder="'E-mail бухгалтера'"
                                                :placeholder-input="'info@domain.com'"></input-styled>
                                    </div>

                                    <div class="row-b__col elem-pos-fit"
                                         :class="{'--active':company.bookerEmail.focus}">
                                        <span class="text-size-s construct-form__note">На этот адрес будут приходить сами счета</span>
                                    </div>
                                </div>
                                <!-- end row-b -->
                            </div>
                            <!-- end contract__form__item -->



                        </div>
                    </div>








                </div>
                <div class="mt40 edit-btn-block" v-if="isEdit">
                    <submit-btn tag="span" class="btn btn--green mr24" @click.native="finish" :type="buttons.send.state">Сохранить</submit-btn>
                    <a href="#" @click.prevent="close" class="pseudo-link grey h-editor-link">Отмена</a>
                </div>
                <div class="mt40" v-else>
                    <submit-btn tag="span" class="btn" @click.native="finish" :type="buttons.send.state">Продолжить</submit-btn>
                </div>
            </div>
        </section>
    </div>
</template>

<script>
    import {mapState} from 'vuex';
    import ItemShow from '@/components/ItemShow.vue';
    import {scrollTo, scrollToModal} from '@/helpers/animate.js';
    import {
        validateBik,
        validateInn,
        validateKpp,
        validateKs,
        validateOgrn,
        validateOgrnip,
        validateRs,
        validateSnils,
        isEmpty
    } from '@/helpers/validate.js';
    import Tooltip from '@/components/Tooltip.vue';
    import FileUpload from '@/components/forms/FileUpload.vue';
    import InputStyled from '@/components/forms/InputStyled.vue'
    import InputMulti from '@/components/forms/InputMulti.vue';
    import SubmitBtn from '@/components/forms/SubmitBtn.vue'

    export default {
        name: "Contract",
        components: {
            ItemShow,
            Tooltip,
            FileUpload,
            InputStyled,
            InputMulti,
            SubmitBtn,
        },
        props: {
            isEdit: {
                default: function () {
                    return false
                }
            },
        },
        mounted: function() {
            if (this.isEdit) {
                this.existsFiles.inn = this.partner.contract.innFileName !== '';
                this.existsFiles.ogrn = this.partner.contract.ogrnFileName !== '';


                //owenership

                if (this.partner.contract.ownership) {
                    this.setOwnership(this.partner.contract.ownership);
                    this.$set(this.company.ownership, 'isShowFirst', false);
                }


                //IP FORM
                if (this.partner.contract.ownership === this.ownershipType.ip) {
                    //ip name
                    this.company.nameIP.value = this.partner.contract.ipSecondName + ' ' + this.partner.contract.ipName + ' ' + this.partner.contract.ipLastName;
                    this.$nextTick(function () {

                        let event = document.createEvent('HTMLEvents'),
                            el = document.getElementById('company-nameIP').querySelectorAll('input')[0];
                        event.initEvent('input', true, false);
                        el.dispatchEvent(event);
                    });


                    //ip settlementAccount notUse
                    if (this.company.settlementAccount.value === '') {
                        this.$nextTick(function () {
                            let event = document.createEvent('HTMLEvents'),
                                el = document.getElementById('provider-lk-create-company-input-settlement-account-use');
                            el.click();
                        });
                    }

                    //ip bookerName
                    this.company.bookerName.value = this.partner.contract.bookerSecondName + ' ' + this.partner.contract.bookerName + ' ' + this.partner.contract.bookerLastName;
                    this.$nextTick(function () {
                        let event = document.createEvent('HTMLEvents'),
                            el = document.getElementById('company-bookerName').querySelectorAll('input')[0];
                        event.initEvent('input', true, false);
                        el.dispatchEvent(event);
                    });

                    //other fields
                    this.company.inn.value = this.partner.contract.inn;
                    this.company.ogrnip.value = this.partner.contract.ogrnip;
                    this.company.regAddress.value = this.partner.contract.regAddress;
                    this.company.postAddress.value = this.partner.contract.postAddress;
                    this.company.settlementAccount.value = this.partner.contract.settlementAccount;
                    this.company.settlementAccountBank.value = this.partner.contract.settlementAccountBank;
                    this.company.settlementAccountBankBik.value = this.partner.contract.settlementAccountBankBik;
                    this.company.settlementAccountBankCor.value = this.partner.contract.settlementAccountBankCor;
                    this.company.bookerPhone.value = this.partner.contract.bookerPhone;
                    this.company.bookerEmail.value = this.partner.contract.bookerEmail;

                }

                //OOO FORM
                if (this.isLegalEntity(this.partner.contract.ownership)) {
                    //ooo name
                    this.company.nameOOO.value = this.partner.contract.oooSecondName + ' ' + this.partner.contract.oooName + ' ' + this.partner.contract.oooLastName;
                    this.$nextTick(function () {

                        let event = document.createEvent('HTMLEvents'),
                            el = document.getElementById('company-nameOOO').querySelectorAll('input')[0];
                        event.initEvent('input', true, false);
                        el.dispatchEvent(event);
                    });

                    //ooo bookerName
                    this.company.bookerName.value = this.partner.contract.bookerSecondName + ' ' + this.partner.contract.bookerName + ' ' + this.partner.contract.bookerLastName;
                    this.$nextTick(function () {
                        let event = document.createEvent('HTMLEvents'),
                            el = document.getElementById('company-bookerName').querySelectorAll('input')[0];
                        event.initEvent('input', true, false);
                        el.dispatchEvent(event);
                    });

                    //other fields
                    this.company.nameCompany.value = this.partner.contract.companyName;
                    this.company.ceo.value = this.partner.contract.ceo;
                    this.company.credentials.value = this.partner.contract.credentials;
                    this.company.inn.value = this.partner.contract.inn;
                    this.company.ogrn.value = this.partner.contract.ogrn;
                    this.company.kpp.value = this.partner.contract.kpp;
                    this.company.regAddress.value = this.partner.contract.regAddress;
                    this.company.postAddress.value = this.partner.contract.postAddress;
                    this.company.urAddress.value = this.partner.contract.urAddress;
                    this.company.settlementAccount.value = this.partner.contract.settlementAccount;
                    this.company.settlementAccountBank.value = this.partner.contract.settlementAccountBank;
                    this.company.settlementAccountBankBik.value = this.partner.contract.settlementAccountBankBik;
                    this.company.settlementAccountBankCor.value = this.partner.contract.settlementAccountBankCor;
                    this.company.bookerPhone.value = this.partner.contract.bookerPhone;
                    this.company.bookerEmail.value = this.partner.contract.bookerEmail;
                }
            }
        },
        computed: {
            ...mapState({
                popups: state => state.popups.items,
                user: state => state.users.user,
                cities: state => state.cities.items,
                partner: state => state.partner.current,
            }),
        },
        data() {
            return {
                buttons: {
                    send: {
                        state: 'normal'
                    },
                },
                phoneMask: {
                    mask: "+7(999)999-99-99",
                    showMaskOnHover: false,
                },
                companyMask: {
                    definitions: {
                        "i": {
                            validator: ".",
                            definitionSymbol: "*"
                        }
                    },
                    placeholder: '',
                    mask: "«i{1,}»",
                    showMaskOnHover: false,
                },
                bikMask: {
                    mask: "999999999",
                    showMaskOnHover: false,
                    placeholder: ''
                },
                ksMask: {
                    mask: "99999999999999999999",
                    showMaskOnHover: false,
                    placeholder: ''
                },
                innMask: {
                    mask: "9999999999",
                    showMaskOnHover: false,
                    placeholder: ''
                },
                innIpMask: {
                    mask: "999999999999",
                    showMaskOnHover: false,
                    placeholder: ''
                },
                ogrnMask: {
                    mask: "9999999999999",
                    showMaskOnHover: false,
                    placeholder: ''
                },
                ogrnIpMask: {
                    mask: "999999999999999",
                    showMaskOnHover: false,
                    placeholder: ''
                },
                ownershipType: {
                    ooo: '32',
                    ip: '33',
                    ao: '76'

                },
                ownershipTypeTitle: {
                    '32': 'ООО',
                    '33': 'ИП',
                    '76': 'АО',
                },
                company: {
                    ownership: {
                        disabled: false,
                        required: true,
                        value: '32',
                        error: false,
                        errorMessage: '',
                        type: 'text',
                        isShowFirst: true,
                    },
                    city: {
                        required: false,
                        value: false,
                        type: 'text',
                    },
                    nameCompany: {
                        disabled: false,
                        required: true,
                        value: '',
                        error: false,
                        errorMessage: '',
                        type: 'text',
                        ownership: '32',
                        scrollTo: '#company-nameCompany',
                    },
                    ceo: {
                        disabled: false,
                        required: true,
                        value: '',
                        error: false,
                        errorMessage: '',
                        type: 'text',
                        ownership: '32',
                        scrollTo: '#company-ceo',
                    },
                    credentials: {
                        disabled: false,
                        required: true,
                        value: '',
                        error: false,
                        errorMessage: '',
                        type: 'text',
                        ownership: '32',
                        scrollTo: '#company-credentials',
                    },
                    nameIP: {
                        disabled: false,
                        required: true,
                        value: '',
                        error: false,
                        errorMessage: '',
                        type: 'multi',
                        ownership: '33',
                        scrollTo: '#company-nameIP',
                        fields: {
                            secondNameIP: {
                                name: 'secondNameIP',
                                value: '',
                                delimiter: ' ',
                                placeholder: 'Фамилия',
                            },
                            nameIP: {
                                name: 'nameIP',
                                value: '',
                                delimiter: ' ',
                                placeholder: 'Имя',
                            },
                            lastNameIP: {
                                name: 'lastNameIP',
                                value: '',
                                delimiter: '',
                                placeholder: 'Отчество',
                            },
                        }
                    },
                    nameOOO: {
                        disabled: false,
                        required: true,
                        value: '',
                        error: false,
                        errorMessage: '',
                        type: 'multi',
                        ownership: '32',
                        scrollTo: '#company-ceo',
                        fields: {
                            secondNameOOO: {
                                name: 'secondNameOOO',
                                value: '',
                                delimiter: ' ',
                                placeholder: 'Фамилия',
                            },
                            nameOOO: {
                                name: 'nameOOO',
                                value: '',
                                delimiter: ' ',
                                placeholder: 'Имя',
                            },
                            lastNameOOO: {
                                name: 'lastNameOOO',
                                value: '',
                                delimiter: '',
                                placeholder: 'Отчество',
                            },
                        }
                    },
                    inn: {
                        disabled: false,
                        required: true,
                        length: 10,
                        value: '',
                        error: false,
                        errorMessage: '',
                        type: 'inn',
                        scrollTo: '#company-inn',
                    },
                    innFile: {
                        disabled: false,
                        required: true,
                        error: false,
                        file: {},
                        type: 'file',
                        errorMessage: '',
                        scrollTo: '#company-inn',
                    },

                    ogrn: {
                        disabled: false,
                        required: true,
                        length: 13,
                        value: '',
                        error: false,
                        errorMessage: '',
                        type: 'ogrn',
                        scrollTo: '#company-ogrn',
                    },
                    ogrnFile: {
                        disabled: false,
                        required: true,
                        error: false,
                        errorMessage: '',
                        file: {},
                        type: 'file',
                        scrollTo: '#company-ogrn',
                    },
                    ogrnip: {
                        disabled: false,
                        required: true,
                        value: '',
                        error: false,
                        errorMessage: '',
                        type: 'ogrnip',
                        scrollTo: '#company-ogrn',
                    },
                    kpp: {
                        disabled: false,
                        required: true,
                        length: 9,
                        value: '',
                        error: false,
                        errorMessage: '',
                        type: 'kpp',
                        scrollTo: '#company-kpp',
                    },
                    settlementAccount: {
                        disabled: false,
                        required: true,
                        length: 20,
                        errorMessage: '',
                        value: '',
                        error: false,
                        notUse: false,
                        type: 'rs',
                        scrollTo: '#company-settlementAccount',
                    },
                    settlementAccountBank: {
                        disabled: false,
                        required: true,
                        value: '',
                        error: false,
                        errorMessage: '',
                        type: 'settlement',
                        scrollTo: '#company-settlementAccountBank',
                    },
                    settlementAccountBankBik: {
                        disabled: false,
                        required: true,
                        length: 9,
                        value: '',
                        error: false,
                        errorMessage: '',
                        type: 'bik',
                        scrollTo: '#company-settlementAccountBankBik',
                    },
                    settlementAccountBankCor: {
                        disabled: false,
                        required: true,
                        length: 20,
                        value: '',
                        error: false,
                        errorMessage: '',
                        type: 'ks',
                        scrollTo: '#company-settlementAccountBankCor',
                    },
                    urAddress: {
                        disabled: false,
                        required: true,
                        value: '',
                        error: false,
                        errorMessage: '',
                        type: 'text',
                        ownership: '32',
                        scrollTo: '#company-urAddress',
                    },
                    regAddress: {
                        disabled: false,
                        required: true,
                        value: '',
                        error: false,
                        errorMessage: '',
                        type: 'text',
                        ownership: '33',
                        scrollTo: '#company-regAddress',
                    },
                    postAddress: {
                        disabled: false,
                        required: true,
                        value: '',
                        error: false,
                        errorMessage: '',
                        type: 'text',
                        scrollTo: '#company-postAddress',
                    },

                    bookerName: {
                        disabled: false,
                        required: true,
                        value: '',
                        error: false,
                        errorMessage: '',
                        type: 'multi',
                        scrollTo: '#company-bookerName',
                        fields:{
                            bookerSecondName: {
                                name: 'bookerSecondName',
                                value: '',
                                delimiter: ' ',
                                placeholder: 'Фамилия',
                            },
                            bookerName: {
                                name: 'bookerName',
                                value: '',
                                delimiter: ' ',
                                placeholder: 'Имя',
                            },
                            bookerLastName: {
                                name: 'bookerLastName',
                                value: '',
                                delimiter: '',
                                placeholder: 'Отчество',
                            },
                        }
                    },
                    bookerPhone: {
                        disabled: false,
                        required: true,
                        value: '',
                        focus: false,
                        error: false,
                        errorMessage: '',
                        type: 'phone',
                        scrollTo: '#company-bookerPhone',
                    },
                    bookerEmail: {
                        disabled: false,
                        required: true,
                        focus: false,
                        value: '',
                        error: false,
                        errorMessage: '',
                        type: 'email',
                        scrollTo: '#company-bookerEmail',
                    },
                },
                existsFiles: {
                    inn: false,
                    ogrn: false
                }
            }
        },
        methods: {
            close() {
                this.$emit('close');
            },
            clearName(){
                this.company.nameCompany.value = this.company.nameCompany.value.replace(/«»/ig,'')
            },
            clearExistFile(code) {
                this.existsFiles[code] = false;
            },
            focus(field){
                this.removeFieldError(field);
                this.company[field].focus = true;
            },
            blur(field){
                this.company[field].focus = false;
            },
            focusCompanyName (){
                this.removeFieldError('nameCompany');
                if(this.company.ownership.isShowFirst){
                    this.$set(this.company.ownership, 'isShowFirst', false);
                    this.$refs.tooltipOwnership.tooltipShow();
                }
            },
            getCityName(xmlId){
                let city = this.$store.getters['cities/getByXmlId'](xmlId);
                return city ? 'г. ' + city.name : '';
            },
            scrollTo(element) {
                if(this.isEdit) {
                    scrollToModal(element, true, 0, 'contract-modal');
                } else {
                    scrollTo(element);
                }
            },
            setOwnership(value){
                this.company.ownership.value = value;
                this.$refs.tooltipOwnership.tooltipClose();
                this.$set(this.company.ownership, 'isShowFirst', false);
                this.$set(this.company.settlementAccount, 'notUse', false);
            },
            checkError: function (error) {
                switch (error) {

                    case 'authorized':
                        window.location.href = '/partner/';
                        break;
                }
            },
            isLegalEntity: function (value) {
                return ((value == this.ownershipType.ooo) || (value == this.ownershipType.ao))? true: false;
            },
            checkField: function (field) {
                let isCheck = true;
                let result;
                this.$set(field, 'error', false);

                if(field.ownership == this.company.ownership.value || field.ownership === undefined) {

                    switch (field.type) {

                        case 'phone':

                            let phone = field.value.replace(/[^\+0-9]/g, '');

                            if (phone.length < 12) {
                                setTimeout(()=>{
                                    this.$set(field, 'error', true);
                                },150);

                                isCheck = false;
                            } else if (phone[2] != '9') {
                                isCheck = false;
                                setTimeout(()=>{
                                    this.$set(field, 'error', 1); //если 1, то текст тоже красится
                                },150);

                            } else {
                                this.$set(field, 'error', false);
                            }
                            break;

                        case 'email':

                            let r = /^[\w\.\d-_]+@[\w\.\d-_]+\.\w{2,99}$/i;
                            if (!r.test(field.value)) {
                                setTimeout(()=>{
                                    this.$set(field, 'error', true);
                                },150);

                                isCheck = false;
                            } else {
                                this.$set(field, 'error', false);
                            }
                            break;

                        case 'checkbox':

                            if (!field.value) {
                                setTimeout(()=>{
                                    this.$set(field, 'error', true);
                                },150);

                                isCheck = false;
                            } else {
                                this.$set(field, 'error', false);
                            }
                            break;

                        case 'text':
                            if (field.value === undefined || field.value !== undefined && field.value.length === 0) {
                                if (field.notUse !== true) {
                                    setTimeout(()=>{
                                        this.$set(field, 'error', true);
                                    },150);

                                    isCheck = false;
                                } else {
                                    this.$set(field, 'error', false);
                                }
                            }

                            break;

                        case 'multi':
                                for(let fieldIndexMulti in field.fields) {
                                    let fieldMulti = field.fields[fieldIndexMulti];
                                    if(fieldMulti.value.length === 0){
                                        setTimeout(()=>{
                                            this.$set(field, 'error', true);
                                        },150);

                                        isCheck = false;
                                    }
                                }
                            break;

                        case 'inn':
                            result = validateInn(field.value, this.company.ownership.value === this.ownershipType.ip);
                            if (result !== true && field.notUse !== true) {
                                setTimeout(()=>{
                                    this.$set(field, 'error', true);
                                },150);

                                isCheck = false;
                            } else {
                                this.$set(field, 'error', false);
                            }
                            break;
                        case 'ip':
                            let lenIp = 1;
                            if (field.value === undefined && this.company.ownership.value === this.ownershipType.ip || field.value !== undefined && field.value.length < lenIp && this.company.ownership.value === this.ownershipType.ip) {
                                setTimeout(()=>{
                                    this.$set(field, 'error', true);
                                },150);

                                isCheck = false;
                            } else {
                                this.$set(field, 'error', false);
                            }
                            break;
                        case 'ogrn':
                            result = validateOgrn(field.value);
                            if (result !== true && field.notUse !== true && this.isLegalEntity(this.company.ownership.value)) {
                                setTimeout(()=>{
                                    this.$set(field, 'error', true);
                                },150);

                                isCheck = false;
                            } else {
                                this.$set(field, 'error', false);
                            }
                            break;
                        case 'ogrnip':
                            result = validateOgrnip(field.value);
                            if (result !== true && field.notUse !== true && this.company.ownership.value === this.ownershipType.ip) {
                                setTimeout(()=>{
                                    this.$set(field, 'error', true);
                                },150);

                                isCheck = false;
                            } else {
                                this.$set(field, 'error', false);
                            }
                            break;
                        case 'kpp':
                            result = validateKpp(field.value);
                            if (result !== true && field.notUse !== true && this.isLegalEntity(this.company.ownership.value)) {
                                setTimeout(()=>{
                                    this.$set(field, 'error', true);
                                },150);

                                isCheck = false;
                            } else {
                                this.$set(field, 'error', false);
                            }
                            break;
                        case 'rs':
                            result = validateRs(field.value, this.company.settlementAccountBankBik.value);
                            if (result !== true && field.notUse !== true && this.company.settlementAccountBankBik.value.length > 0 || field.notUse !== true && this.company.settlementAccountBankBik.value.length === 0) {
                                setTimeout(()=>{
                                    this.$set(field, 'error', true);
                                },150);

                                isCheck = false;
                            } else {
                                this.$set(field, 'error', false);
                            }
                            break;
                        case 'bik':
                            result = validateBik(field.value);
                            if (result !== true && this.company.settlementAccount.notUse !== true) {
                                setTimeout(()=>{
                                    this.$set(field, 'error', true);
                                },150);

                                isCheck = false;
                            } else {
                                this.$set(field, 'error', false);
                            }
                            break;
                        case 'ks':
                            result = validateKs(field.value, this.company.settlementAccountBankBik.value);
                            if (result !== true && this.company.settlementAccount.notUse !== true) {
                                setTimeout(()=>{
                                    this.$set(field, 'error', true);
                                },150);

                                isCheck = false;
                            } else {
                                this.$set(field, 'error', false);
                            }
                            break;
                        case 'ooo':
                            let lenOOO = 1;
                            if (field.value === undefined && this.isLegalEntity(this.company.ownership.value) || field.value !== undefined && field.value.length < lenOOO && this.isLegalEntity(this.company.ownership.value)) {
                                setTimeout(()=>{
                                    this.$set(field, 'error', true);
                                },150);

                                isCheck = false;
                            } else {
                                this.$set(field, 'error', false);
                            }
                            break;
                        case 'file':

                            if (isEmpty(field.file)) {
                                setTimeout(()=>{
                                    this.$set(field, 'error', true);
                                },150);

                                isCheck = false;
                            } else {
                                this.$set(field, 'error', false);
                            }

                            break;
                        case 'fileOOO':

                            if (isEmpty(field.file) && this.isLegalEntity(this.company.ownership.value)) {
                                setTimeout(()=>{
                                    this.$set(field, 'error', true);
                                },150);

                                isCheck = false;
                            } else {
                                this.$set(field, 'error', false);
                            }

                            break;
                        case 'fileIP':

                            if (isEmpty(field.file) && this.company.ownership.value === this.ownershipType.ip) {
                                setTimeout(()=>{
                                    this.$set(field, 'error', true);
                                },150);

                            isCheck = false;
                        } else {
                            this.$set(field, 'error', false);
                        }
                        break;
                    case 'settlement':

                        if (this.isLegalEntity(this.company.ownership.value)) {
                            if (field.value === undefined || field.value !== undefined && field.value.length === 0) {
                                setTimeout(()=>{
                                    this.$set(field, 'error', true);
                                },150);

                                isCheck = false;
                            } else {
                                this.$set(field, 'error', false);
                            }
                        }
                        if (this.company.ownership.value === this.ownershipType.ip) {
                            if (field.value === undefined && !this.company.settlementAccount.notUse || field.value !== undefined && field.value.length === 0 && !this.company.settlementAccount.notUse) {
                                setTimeout(()=>{
                                    this.$set(field, 'error', true);
                                },150);

                                isCheck = false;
                            } else {
                                this.$set(field, 'error', false);
                            }
                        }

                            break;

                        default:
                            break;
                    }
                } else {
                    this.$set(field, 'error', false);
                }

                if (result !== undefined && result.message !== undefined) {
                    field.errorMessage = result.message;
                }

                return isCheck;
            },
            checkFields: function (group, isEdit) {
                let isCheckedFields = true;
                if (this[group] !== undefined) {
                    for (let index in this[group]) {
                        let isCheck = false;
                        if (this[group][index].required === true) {
                            isCheck = this.checkField(this[group][index]);

                            if (!isCheck) {
                                if (!(this[group][index].type === 'file' && isEdit)) {
                                    isCheckedFields = false;
                                }
                            }

                            if (!this.errorScrollTo && !isCheck && !isCheckedFields && this[group][index].scrollTo !== undefined) {
                                this.errorScrollTo = this[group][index].scrollTo;
                            }
                        }
                    }
                }
                if (this.errorScrollTo) {
                    if(document.querySelector(this.errorScrollTo) ){
                        this.scrollTo(this.errorScrollTo);
                    }
                }

                return isCheckedFields;
            },
            finish: function () {
                if (!this.sendForm) {
                    this.errorScrollTo = false;
                    let check = this.checkFields('company', this.isEdit);

                    if (check) {
                        let data = {};

                        for (let indexOrder in this.company) {
                            if(this.company.settlementAccount.notUse && indexOrder === 'settlementAccount') {
                                data[indexOrder] = 'Нет';
                            } else if(this.company[indexOrder].type ==='multi'){
                                for(let fieldIndex in this.company[indexOrder].fields) {
                                    let field = this.company[indexOrder].fields[fieldIndex];
                                    data[fieldIndex] = field.value
                                }
                            } else {
                                data[indexOrder] = this.company[indexOrder].value;
                            }
                        }
                        if(this.company.settlementAccount.notUse){
                            data['settlementAccountBank'] = "";
                            data['settlementAccountBankBik'] = "";
                            data['settlementAccountBankCor'] = "";
                        }
                        this.buttons.send.state = 'load';

                        if (this.isEdit) {
                            data['IS_EDIT'] = this.isEdit;
                            data['PROVIDER_ID'] = this.$route.params.id;
                        }

                        this.$store.dispatch('partner/sendContract',data).then(() => {
                            if (data['IS_EDIT'])  {
                                let self = this;
                                this.$store.dispatch('moderatorProviders/getItem', {id: this.$route.params.id}).then(() => {
                                    this.buttons.send.state = 'ok';
                                    this.$store.commit('popups/close', {name: this.popups.contractEdit.name});
                                });
                            } else {
                                this.$store.dispatch('partner/getCurrent').then(()=>{
                                    this.buttons.send.state = 'ok';
                                    this.$store.dispatch('popups/open', {name: this.popups.partnerFirstEntry.name});
                                });
                            }

                        });

                    }
                }
            },
            validateLength: function (field) {
                if (this.company.hasOwnProperty(field)) {
                    if (this.company[field].value > this.company[field].length) {
                        this.$set(this.company[field], 'error', true);
                    }
                }
            },
            removeFieldError: function (field) {
                if (this.company.hasOwnProperty(field)) {
                    this.$set(this.company[field], 'error', false);
                }
            },
            setPersonInputIP: function (field) {
                if(field.value !== undefined && field.name !== undefined){
                    this.company.nameIP.fields[field.name].value = field.value;
                }
            },
            setPersonInputOOO: function (field) {
                if(field.value !== undefined && field.name !== undefined){
                    this.company.nameOOO.fields[field.name].value = field.value;
                }
            },
            setPersonInputBooker: function (field) {
                if(field.value !== undefined && field.name !== undefined){
                    this.company.bookerName.fields[field.name].value = field.value;
                }
            }
        }
    }
</script>

