<template>
    <div>
        <div class="popup__content">
            <div class="h1 text-center">Что нужно сделать<br> для получения скидки?</div>
            
            
            <div class="construct-form__how-use-var mt32">
                <div :class="{disabled:stepsCreateAction.four.fields.callUsCheckBox.value}"
                     class="item"
                     @click="openConstructActionCallUs">
                    <div class="icon">
                        <img src="@/assets/img/icons/phone.svg" alt="">
                    </div>
                    
                    <div class="t">Позвонить...</div>
                    
                    <div class="s">Уже используется</div>
                </div>
                
                <div :class="{disabled:stepsCreateAction.four.fields.comeUsCheckBox.value}"
                     class="item"
                     @click="openConstructActionComeUs">
                    <div class="icon">
                        <img src="@/assets/img/icons/marker.svg" alt="">
                    </div>
                    
                    <div class="t">Приехать...</div>
                    
                    <div class="s">Уже используется</div>
                </div>
                
                <div :class="{disabled:stepsCreateAction.four.fields.goLinkCheckBox.value}"
                     class="item"
                     @click="openConstructActionGoLink">
                    <div class="icon">
                        <img src="@/assets/img/icons/cart.svg" alt="">
                    </div>
                    
                    <div class="t">Заказать по ссылке...</div>
                    
                    <div class="s">Уже используется</div>
                </div>
                
            </div>
            
            <div class="mt32 text-center">
                Выберите один из вариантов. Если нужно выбрать несколько – добавьте их по очереди...
            </div>
            
        </div>
    </div>
</template>

<script>
    import {mapState} from 'vuex';

    export default {
        name: "ConstructHowUseVars",
        props: {
        },
        data: function() {
            return {
            }
        },
        methods: {
            openConstructActionCallUs: function () {
                if(!this.stepsCreateAction.four.fields.callUsCheckBox.value){
                    this.$store.commit('popups/close', {name: this.popups.constructHowUseVars.name});
                    this.$store.dispatch('popups/open', {name: this.popups.constructActionCallUs.name});
                }

            },
            openConstructActionComeUs: function () {
                if(!this.stepsCreateAction.four.fields.comeUsCheckBox.value){
                    this.$store.commit('popups/close', {name: this.popups.constructHowUseVars.name});
                    this.$store.dispatch('popups/open', {name: this.popups.constructActionComeUs.name});
                }

            },
            openConstructActionGoLink: function () {
                if(!this.stepsCreateAction.four.fields.goLinkCheckBox.value){
                    this.$store.commit('popups/close', {name: this.popups.constructHowUseVars.name});
                    this.$store.dispatch('popups/open', {name: this.popups.constructActionGoLink.name});
                }

            },
        },
        computed: {
            ...mapState({
                popups: state => state.popups.items,
                stepsCreateAction: state => state.partnerActions.stepsCreateAction,
            }),
        },
    }
</script>

