<template>
    <div>
        <div class="tab-content">
            <div class="content-title mb0">
                <div class="content-title__wrap">
                    <div class="h2" ><span v-html="partner.name"></span>&nbsp;<a @click.prevent="clickEditText()" v-if="canEditText" href="#" class="link-icon dif"><span class="i i-pencil2"></span></a></div>
                </div>
                <div v-if="partner.site" class="content-title__right">
                    <a :href="'http://' + partner.site"
                       target="_blank" rel="noopener"
                       class="content-title__pointer">
                        <span class="i i-link"></span>
                        <span class="t" v-html="partner.site"></span>
                    </a>
                </div>

                <div class="content-title__info" v-if="(partner.status === 'verify' || partner.statusCode === 'verify') && partner.aboutText.length > 0">
                    <tooltip :direction="'top-left-direction'"
                             :add-class-wrap="'l-icon'"
                             :add-class-btn="tooltipModerateAboutIconClass + ' i hover'"
                             :add-class="'tooltip--stat'"
                             :is-padding="false"
                             :isBtnClose="false">
                        <span slot="tooltipBtn" class=""></span>
                        
                        <div slot="tooltipContent">
                            <div class="nowrap dif">
                                <span class="text-size-m pr8">Редактор: {{ partner.lastEditor.fio }} <a class="text-bold" v-if="partner.lastEditor.phone" :href="'tel:'+partner.lastEditor.phone.replace(/[^\+0-9]/g, '')">{{ partner.lastEditor.phone }}</a>&nbsp;<span v-if="partner.datePublicAbout && !partner.dateEditAbout">Опубликовано {{partner.datePublicAbout}}</span></span>

                                <button @click="openProviderApproveAbout" class="btn btn--green btn--ico --no-shadow size-s" v-if="partner.dateEditAbout || !partner.datePublicAbout">
                                    <span class="i i-ok2 mt-1"></span>
                                    <span class="ml4">Опубликовать промо-страницу</span>
                                </button>
                            </div>
                        </div>
                    </tooltip>
                </div>
            </div>
            
            <!-- <div class="sect-blank" style="min-height: 215px">
                <div class="wrap text-center">
                    <div class="text-size-l text-bolder text-color-grey mt16">Поставщик пока не запилил промо-страницу</div>
                </div>
            </div> -->

            <partner-about-main-text v-if="partner.aboutText" class="mt16 mtb"  :partner="partner"></partner-about-main-text>
        </div>

        <div v-if="requisitesArr.length === 0" class="tab-content-bottom">
            <div class="sect-blank" style="min-height: 226px">
                <div class="wrap text-center">
                    <div class="text-size-l text-bolder text-color-grey mt16">Реквизиты не заполнены</div>
                </div>

                <div class="mini-zems"></div>
            </div>
            <!-- end sect-blank -->
        </div>



        <div v-else class="tab-content-bottom">
            <h2 id="req-provider-detail">Реквизиты <!--<a @click.prevent="clickEditRequisites()" v-if="canEditRequisites" href="#" class="link-icon"><span class="i i-pencil2"></span></a>--></h2>

            <div class="requisites">
                <div>
                    <p v-for="(req, index) in requisitesArr">
                        <span v-html="req"></span>
                        <span v-if="index === 'INN' && !inn.checked && innOgrnCheck">
                            <a :class="{error:inn.error}" @click.prevent="openVerifyINN" href="#" class="text-size-s ml8" title="Проверить">Проверить</a>
                        </span>
                        <span v-if="index === 'OGRN' && !ogrn.checked && innOgrnCheck">
                            <a :class="{error:ogrn.error}" @click.prevent="openVerifyOGRN" href="#" class="text-size-s ml8" title="Проверить">Проверить</a>
                        </span>
                        <span v-if="index === 'OGRNIP' && !ogrnip.checked && innOgrnCheck">
                            <a :class="{error:ogrnip.error}" @click.prevent="openVerifyOGRNIP" href="#" class="text-size-s ml8" title="Проверить">Проверить</a>
                        </span>
                        <span v-if="index === 'INN' && inn.checked && innOgrnCheck">
                            <span class="ml8 i i-ok2 i--blue"></span>
                        </span>
                        <span v-if="index === 'OGRN' && ogrn.checked && innOgrnCheck">
                            <span class="ml8 i i-ok2 i--blue"></span>
                        </span>
                        <span v-if="index === 'OGRNIP' && ogrnip.checked && innOgrnCheck">
                            <span class="ml8 i i-ok2 i--blue"></span>
                        </span>
                    </p>
                </div>
                <div v-if="partner.logo && partner.logo.icon !== null && partner.logo.icon !== ''" class="requisites__logo">
                    <img :src="'//api.zemskidki.ru'+partner.logo.icon" alt="">
                </div>
            </div>


        </div>


        <!-- проверка ИНН -->
        <popup :add-class="'popup--inn'" :is-set-styled="true" :show="popups.verificationInn.show" :name="popups.verificationInn.name">
            <verification-inn :providerId="providerId" :providerName="providerName" :inn="inn"></verification-inn>
        </popup>


        <!-- проверка ОГРН -->
        <popup :add-class="'popup--inn'" :is-set-styled="true" :show="popups.verificationOgrn.show" :name="popups.verificationOgrn.name">
            <verification-ogrn :providerId="providerId" :ogrn="ogrn" :providerName="providerName"></verification-ogrn>
        </popup>


        <!-- проверка ОГРНИП -->
        <popup :add-class="'popup--inn'" :is-set-styled="true" :show="popups.verificationOgrnIp.show" :name="popups.verificationOgrnIp.name">
            <verification-ogrn-ip :providerId="providerId" :ogrnip="ogrnip" :providerName="providerName"></verification-ogrn-ip>
        </popup>

        <!-- предварительно одобрить поставщика -->
        <popup :is-set-styled="true" :show="popups.providerApproveAbout.show" :name="popups.providerApproveAbout.name">
            <provider-approve-about></provider-approve-about>
        </popup>
    </div>

</template>

<script>
    import {mapState} from 'vuex';
    import VerificationInn from '@/components/modal/VerificationInn.vue';
    import VerificationOgrn from '@/components/modal/VerificationOgrn.vue';
    import VerificationOgrnIp from '@/components/modal/VerificationOgrnIp.vue';
    import ProviderApproveAbout from '@/components/modal/ProviderApproveAbout.vue';
    import PartnerAboutMainText from '@/components/partner/PartnerAboutMainText.vue';
    import Popup from '@/components/modal/Popup.vue';
    import Tooltip from '@/components/Tooltip.vue';

    export default {
        name: "ProviderAbout",
        components: {
            VerificationInn,
            VerificationOgrn,
            VerificationOgrnIp,
            Popup,
            PartnerAboutMainText,
            Tooltip,
            ProviderApproveAbout
        },
        props: {
            inCoupons: {
                default: function () {
                    return false
                }
            },
            site: {
                default: function () {
                    return ''
                }
            },
            innOgrnCheck: {
                default: function () {
                    return false
                }
            },
            partner: {
                default: function () {
                    return {}
                }
            },
            requisites: {
                default: function () {
                    return ''
                }
            },
            requisitesArr: {
                default: function () {
                    return []
                }
            },
            canEditRequisites: {
                default(){
                    return false;
                }
            },
            isModerator: {
                default(){
                    return false;
                }
            },
            isProviders: {
                default(){
                    return false;
                }
            },
            canEditText: {
                default(){
                    return false;
                }
            },
            providerId: {
                default(){
                    return '';
                }
            },
            providerName: {
                default(){
                    return '';
                }
            },
            inn: {
                default: function () {
                    return {
                        type: '',
                        file: '',
                        text: ''
                    }
                }
            },
            ogrn: {
                default: function () {
                    return {
                        type: '',
                        file: '',
                        text: ''
                    }
                }
            },
            ogrnip: {
                default: function () {
                    return {
                        type: '',
                        file: '',
                        text: ''
                    }
                }
            },
        },
        data: function () {
            return {
                search: ''
            }
        },
        computed: {
            ...mapState(
                {
                    isLoad: state => state.moderatorProviders.isLoad,
                    menuList: state => state.menus.itemsAction,
                    items: state => state.moderatorProviders.items,
                    popups: state => state.popups.items,
                }),
            defaultStatus() {
                return this.menuList['providers'][0]['id'];
            },
            pageTitle() {
                let action = this.$route.params.action ? this.$route.params.action : this.defaultStatus,
                    currentIndex = this.getIndexItem(action),
                    title = '';

                if (typeof (this.menuList['providers']) !== 'undefined') {
                    title = this.menuList['providers'][currentIndex]['title']
                }
                return title;
            },
            currentStatus() {
                return this.$route.params.action ? this.$route.params.action : this.defaultStatus;
            },
            tooltipModerateAboutIconClass(){
                let classTooltip = 'i--green i-ok3';
                if((+this.partner.countActive > 0 || +this.partner.countModerate > 0) && this.partner.datePublicAbout.length === 0){
                    classTooltip = 'i--orange i-info3';
                } else if(this.partner.dateEditAbout || !this.partner.datePublicAbout){
                    classTooltip = 'i--yellow i-info3';
                }
                return classTooltip;
            }
        },
        methods: {
            fetchData() {
                this.$store.dispatch('moderatorProviders/getItems', {
                    status: this.currentStatus,
                    search: this.search,
                    reload: true
                });
            },
            getIndexItem(action) {
                let menuList = this.menuList['providers'],
                    result;

                for (let index in menuList) {
                    if (menuList[index].id == action) {
                        result = index;
                        break;
                    }
                }

                return result;
            },
            handleScroll: function () {
                let scroll = window.scrollY;
                let viewport = window.innerHeight;
                let bodyHeight = document.body.clientHeight;
                if (bodyHeight - 80 <= scroll + viewport) {
                    this.$store.dispatch('moderatorProviders/getItems', {
                        status: this.currentStatus,
                        page: 2
                    });
                }
            },
            clickEditText: function () {
                if(this.isProviders){
                    this.$router.push({path: '/moderator/providers/about'+this.$route.params.id+'/'});
                } else {
                    this.$router.push({path: '/moderator/coupons/about'+this.$route.params.id+'/'});
                }
            },
            clickEditRequisites: function () {

            },
            openVerifyINN: function () {
                this.$store.dispatch('popups/open', {name: this.popups.verificationInn.name});
            },
            openVerifyOGRN: function () {
                this.$store.dispatch('popups/open', {name: this.popups.verificationOgrn.name});
            },
            openVerifyOGRNIP: function () {
                this.$store.dispatch('popups/open', {name: this.popups.verificationOgrnIp.name});
            },
            openProviderApproveAbout: function () {
                this.$store.dispatch('popups/open', {name: this.popups.providerApproveAbout.name});
            },
        },
        watch: {
            '$route': 'fetchData'
        },
    }
</script>
<style scoped>
    .content-title {
        position: relative;
    }

    .content-title__info {
        position: absolute;
        top: 3px;
        left: -17px;
    }
</style>

<style>
    .content-title__info .i-ok3{
        transform: scale(0.75);
    }
</style>