            removeError(field, step) {
                this.$store.commit('partnerActions/setFormCreateError', {name: field, step: step, value: false})
            },

            fieldBlur(field, step) {
                this.checkStep(step)
                this.$store.commit('partnerActions/setFormCreateFocus', {name: field, step: step, value: false})
            },
            fieldFocus(field, step) {
                this.removeError(field, step)
                this.$store.commit('partnerActions/setFormCreateFocus', {name: field, step: step, value: true})
                if(field === 'discount'){
                    this.$store.commit('partnerActions/setFormCreateError', {name: 'onenessDiscount', step: step, value: false});
                    this.$store.commit('partnerActions/setFormCreateError', {name: 'originDiscount', step: step, value: false});
                }
                this.$store.commit('partnerActions/setFormCreateBtnHide', {
                    name: 'oneness',
                    value: false,
                    step: 'one',
                });
                this.$store.commit('partnerActions/setFormCreateBtnClick', {
                    name: 'oneness',
                    value: 0,
                    step: 'one',
                });
                this.$store.commit('partnerActions/setFormCreateBtnHide', {
                    name: 'origin',
                    value: false,
                    step: 'one',
                });
                this.$store.commit('partnerActions/setFormCreateBtnClick', {
                    name: 'origin',
                    value: 0,
                    step: 'one',
                });

            },
            inputText(field, step, value) {
                this.$store.commit('partnerActions/setFormCreateField', {
                    name: field,
                    value: value,
                    step: step,
                })
            },

            openOriginDiscount: function () {
                let self = this;
                if (this.stepsCreateAction.one.buttons.origin.hide === false) {
                    if (this.stepsCreateAction.one.buttons.origin.state !== 'disable') {
                        this.$store.dispatch('popups/open', {name: this.popups.originDiscount.name});
                    } else {
                        /*if (this.stepsCreateAction.one.buttons.origin.clicks < 2) {
                            this.$store.commit('partnerActions/setFormCreateBtnClick', {
                                name: 'origin',
                                value: this.stepsCreateAction.one.buttons.origin.clicks + 1,
                                step: 'one',
                            })
                        } else {*/
                            this.$store.commit('partnerActions/setFormCreateBtnHide', {
                                name: 'origin',
                                value: true,
                                step: 'one',
                            })
                            setTimeout(function () {
                                self.$store.commit('partnerActions/setFormCreateBtnHide', {
                                    name: 'origin',
                                    value: false,
                                    step: 'one',
                                })
                                self.$store.commit('partnerActions/setFormCreateBtnClick', {
                                    name: 'origin',
                                    value: 0,
                                    step: 'one',
                                })
                            }, 2500);

                        //}
                    }
                }


            },
            openConfirmDiscount: function () {
                let self = this;
                if (this.stepsCreateAction.one.buttons.oneness.hide === false) {
                    if (this.stepsCreateAction.one.buttons.oneness.state !== 'disable') {
                        this.$store.dispatch('popups/open', {name: this.popups.confirmDiscount.name});
                    } else {
                        if (this.stepsCreateAction.one.buttons.oneness.clicks < 2) {
                            this.$store.commit('partnerActions/setFormCreateBtnClick', {
                                name: 'oneness',
                                value: this.stepsCreateAction.one.buttons.oneness.clicks + 1,
                                step: 'one',
                            })
                        } else {
                            this.$store.commit('partnerActions/setFormCreateBtnHide', {
                                name: 'oneness',
                                value: true,
                                step: 'one',
                            })
                            setTimeout(function () {
                                self.$store.commit('partnerActions/setFormCreateBtnHide', {
                                    name: 'oneness',
                                    value: false,
                                    step: 'one',
                                })
                                self.$store.commit('partnerActions/setFormCreateBtnClick', {
                                    name: 'oneness',
                                    value: 0,
                                    step: 'one',
                                })
                            }, 2500);

                        }

                    }
                }

            },
            prevConfirmDiscount() {
                this.$store.commit('popups/close', {name: this.popups.correctText.name});
                this.$store.commit('popups/close', {name: this.popups.correctTextNoDiscount.name});
                this.$store.dispatch('popups/open', {name: this.popups.confirmDiscount.name});
            },
            hideConfirmDiscount() {
                this.$store.commit('popups/close', {name: this.popups.correctText.name});
                this.$store.commit('popups/close', {name: this.popups.correctTextNoDiscount.name});
            },
            nextStep() {
                if (this.currentStep !== 'finish') {
                    this.$store.dispatch('partnerActions/nextStep');
                } else {
                    this.buttons.save.state = 'load';
                    let id,
                        codes = Object.getOwnPropertyNames(this.statuses);

                    if (this.$route.params.id === 'new') {
                        id = this.tmpId;
                    } else {
                        id = this.$route.params.id;

                    }
                    this.$store.dispatch('partnerActions/saveAction', {
                        id: this.$route.params.id,
                        isModerator: this.isModerator
                    }).then(() => {
                        this.buttons.save.state = 'ok';
                        setTimeout(() => {
                            let code = codes[0];

                            /*if (this.$route.params.id !== 'new') {

                                for(let c in this.statuses) {
                                    if (this.detail.status === this.statuses[c]) {
                                        code = c;
                                        break;
                                    }
                                }

                            }*/

                            if (this.isModerator) {
                                this.$router.push('/moderator/coupons/' + code + '/id' + id);
                            } else {
                                this.$router.push('/partner/coupons/' + code + '/id' + id);
                            }
                            this.buttons.save.state = 'normal';
                        }, 300);
                    }, () => {
                        this.buttons.save.state = 'normal';
                    })
                }

            },
            originDiscountChange(value) {
                this.$store.commit('partnerActions/setFormCreateField', {
                    name: 'originDiscount',
                    value: value,
                    step: 'one',
                })
            },
