                this.$store.dispatch('popups/open', {name: this.popups.constructActionComeUs.name});
            },
            openConstructActionGoLink: function () {
                this.$store.dispatch('popups/open', {name: this.popups.constructActionGoLink.name});
            },
            delCity: function (city) {
                this.$store.commit('partnerActions/delCity', {
                    city: city,
                })
                if (city === 'all') {
                    this.$store.commit('partnerActions/setFormCreateField', {
                        name: 'cities',
                        value: [this.cityPartner],
                        step: 'five',
                    })
                }
                this.changeCitySelect()
            },
            openConstructActionAllCity: function () {
                this.$store.dispatch('popups/open', {name: this.popups.constructActionAllCity.name});
            },
            openConstructActionCity: function () {
                this.$store.dispatch('popups/open', {name: this.popups.constructActionCity.name});
            },
            getCityByXmlId(xmlId) {
                return this.$store.getters['cities/getByXmlId'](xmlId);
            },
            cityChange: function (data) {
                let cities = [];
                for (let r in data.regions) {
                    for (let c in data.regions[r].cities) {
                        if (data.regions[r].cities[c].isChecked) {
                            cities.push({...data.regions[r].cities[c]});
                        }
                    }
                }
                this.removeError('cities', 'five')
                this.$store.commit('partnerActions/setFormCreateField', {
                    name: 'cities',
                    value: cities,
                    step: 'five',
                })
            },
            changeCitySelect: function (bReset) {
                if (this.cities.size > 0) {
                    let citySelectOptions = {};
                    let count = 0;
                    let cities = [...this.cities];
                    for (let cityIndex in cities) {
                        if (cities[cityIndex].regionCode && !citySelectOptions.hasOwnProperty(cities[cityIndex].regionCode)) {
                            let mainIndex = 0;
                            if(cities[cityIndex].region === 'Московская область'){
                                mainIndex = 2;
                            }
                            if(cities[cityIndex].region === 'Ленинградская область'){
                                mainIndex = 1;
                            }
                            citySelectOptions[cities[cityIndex].regionCode] = {
                                name: cities[cityIndex].region,
                                id: cities[cityIndex].regionCode,
                                isOpen: false,
                                isChecked: 0,
                                cities: [],
                                isMain: mainIndex,
                                isDelimiter: cities[cityIndex].region === 'Ленинградская область'
                            }
                        }
                        if (cities[cityIndex].regionCode) {
                            let isChecked = this.stepsCreateAction.five.fields.cities.value.find(city => city.xmlId === cities[cityIndex].xmlId);
                            citySelectOptions[cities[cityIndex].regionCode].cities.push({
                                isChecked: isChecked !== undefined,
                                xmlId: cities[cityIndex].xmlId,
                                name: cities[cityIndex].name,
                                isCapital: cities[cityIndex].isCapital,
                            });
                            if (isChecked !== undefined) {
                                //citySelectOptions[cities[cityIndex].regionCode].isOpen = true;
                                count++;
                            }
                        }
                    }
                    for (let index in citySelectOptions) {
                        let sortable = [];
                        for (let city in citySelectOptions[index].cities) {
                            sortable.push(citySelectOptions[index].cities[city]);
                        }

                        sortable.sort(function (a, b) {
                            if (a.name > b.name) {
                                return 1;
                            }
                            if (a.name < b.name) {
                                return -1;
                            }
                            if (a.name == b.name) {
                                return 0;
                            }
                        });
                        sortable.sort(function (a, b) {
                            if (a.isCapital < b.isCapital) {
                                return 1;
                            }
                            if (a.isCapital > b.isCapital) {
                                return -1;
                            }
                            if (a.isCapital == b.isCapital) {
                                return 0;
                            }
                        });
                        citySelectOptions[index].cities = sortable;
                    }
                    let sortable = [];
                    for (let index in citySelectOptions) {
                        sortable.push(citySelectOptions[index]);
                    }
                    sortable.sort(function (a, b) {
                        if (a.name > b.name) {
                            return 1;
                        }
                        if (a.name < b.name) {
                            return -1;
                        }
                        if (a.name == b.name) {
                            return 0;
                        }
                    });
                    sortable.sort(function (a, b) {
                        if (a.isMain < b.isMain) {
                            return 1;
                        }
                        if (a.isMain > b.isMain) {
                            return -1;
                        }
                        if (a.isMain == b.isMain) {
                            return 0;
                        }
                    });
                    this.citySelectOptions = sortable;
                }
            },
            checkStep(step) {
                if (step !== this.currentStep && this.currentStep !== 'one') {
                    this.$store.dispatch('partnerActions/checkStep', {step: step})
                }
            },
            pageUp() {
                if (this.$route.params.id !== 'new') {
                    window.scroll(0, 0);
                }
            },

            openVideoAdd: function () {
                this.$store.dispatch('popups/open', {name: this.popups.constructVideoAdd.name});
            },

            number_format(result) {
                return number_format(result, 0, '', ' ')
            },
            closePartnerModerateErrorPercent: function () {
                this.$store.commit('popups/close', {name: this.popups.partnerModerateErrorPercent.name});
                this.setFocus('projectDiscount')
            },
            closePartnerModerateErrorSum: function () {
                this.$store.commit('popups/close', {name: this.popups.partnerModerateErrorSum.name});
            },
            closePartnerModerateErrorProfit: function () {
                this.$store.commit('popups/close', {name: this.popups.cooperationCancelSmallPriceOne.name});
            },
            setFocus: function(ref) {
                if(!!this.$refs[ref]){
                    let input = this.$refs[ref].$el.getElementsByClassName('inp-s')[0];
                    if(input){

                        input.focus()
                        setTimeout(function () {
                            input.select()
                        },20)
                    }
                }
            },
            confirmDiscountChange(val){
                this.$store.commit('partnerActions/setFormCreateField', {
                    name: 'discountUser',
                    value: val,
                    step: 'one',
                });
            },
        }
    }
</script>

