<template>
    <div class="full">
        <div v-if="isLoad && items.length==0" class="coupon-stat">
            <div class="coupon-stat__wrap">
                <catalog-ghost :show-flag="false"></catalog-ghost>
            </div>
            <div class="coupon-stat__control"></div>
        </div>


        <div class="wrapper-content" v-if="!isLoad && items.length===0"><div class="sect-blank"><div class="wrap text-center"><div class="text-size-l text-bolder text-color-grey mt16">Здесь пока ничего нет...</div></div><div class="mini-zems"></div></div></div>
        <div class="coupon-list mt0">
            <div class="coupon-stat"  v-for="item in items" :key="item.id">
                <div class="coupon-stat__wrap">
                    <div class="coupon">
                        <tooltip
                             :direction="'left-center-direction'"
                             :add-class-wrap="'coupon__bookmark-wrap'"
                             :add-style="{width: 248 + 'px', 'top': '67px', 'right': '126px'}">
                            <div slot="tooltipBtn" class="coupon__bookmark">
                                <div class="wrap">
                                    <span class="d">−{{ item.discountFrom }}%</span>
                                    <span class="p">
                                        ≈{{ item.realSavingsFormat }}&nbsp;<span class="rouble">{{ $store.getters['options/setRouble'] }}</span>
                                    </span>
                                </div>
                            </div>

                            <div slot="tooltipContent" style="font-size: 16px; ">
                                При обращении к этому<br>
                                поставщику средний чек<br>
                                составляет <strong>{{ item.averageCheckFormat }}&nbsp;<span class="rouble">{{ $store.getters['options/setRouble'] }}</span></strong>,<br>
                                а экономия – около
                                <strong>{{ item.realSavingsFormat }}&nbsp;<span class="rouble">{{ $store.getters['options/setRouble'] }}</span></strong>
                            </div>
                        </tooltip>

                        <router-link :to="'/moderator/coupons/'+currentStatus+'/id'+item.id+'/'" :href="'/moderator/coupons/'+currentStatus+'/id'+item.id+'/'" class="coupon__link"></router-link>

                        <div class="coupon__wrap">
                            <div class="coupon__img" v-if="item.image" :style="{ backgroundImage: 'url(//api.zemskidki.ru/'+item.image+')' }"></div>

                            <div v-else class="coupon__img --empty">
                                <span class="coupon__img__empty">
                                    <span class="icon">
                                        <span class="i i-photo"></span>
                                    </span>
                                    <span class="t">нет фото</span>
                                </span>
                            </div>


                            <div class="coupon__content">
                                <div class="coupon__stat">
                                    <span class="elem --views" v-if="item.showCounter>0">
                                        <span class="i i-eye"></span>
                                        <span class="t">{{ item.showCounterFormat }}</span>
                                    </span>

                                    <span class="elem --buy" v-if="item.statistics.sold > 0">
                                        <span class="i i-cart"></span>
                                        <span class="t">{{ item.statistics.sold }}</span>
                                    </span>

                                    <span class="elem --comment" v-if="item.countOpinions > 0">
                                        <span class="i i-comment"></span>
                                        <span class="t">{{  item.countOpinions }}</span>
                                    </span>
                                </div>
                                
                                <div class="coupon__name">{{item.name}}</div>

                                <div class="coupon__company">{{item.providerName}}</div>

                                <div class="w">
                                    <div class="coupon__economy">
                                        <div class="t">
                                            Экономия
                                        </div>

                                        <div class="p" v-if="item.realSavingsFormat">
                                            ≈{{item.realSavingsFormat}}
                                            <span class="rouble">{{ $store.getters['options/setRouble'] }}</span>
                                        </div>
                                        <div class="p" v-else>
                                            ?
                                        </div>
                                    </div>


                                    <div class="coupon__info" v-if="item.averageCheckFormat">
                                        Средний чек
                                        {{  item.averageCheckFormat }} <span class="rouble">{{ $store.getters['options/setRouble'] }}</span>,
                                        скидка
                                        <span class="n">{{ item.discountFrom }}%</span>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>



                <div class="coupon-stat__control">
                    <div class="stat"  v-if="item.statusModerator==status.publick">
                        <div class="stat__icon">
                            <span class="i i-play6" style="transform: scale(1.2)"></span>
                        </div>

                        <span class="t">
                            Активна {{item.dateDiffStatus}}
                            <span class="text-color-grey text-size-s text-normal">(с {{item.statusDate}})</span>
                        </span>
                    </div>

                    <div class="stat" v-if="item.statusModerator==status.blocked">
                        <div class="stat__icon">
                            <span class="i i-stop3"></span>
                        </div>
                        
                        <span class="t">
                            Заблокирована {{item.dateDiffStatus}}
                            <span class="text-color-grey text-size-s text-normal">(с {{item.statusDate}})</span>
                        </span>
                    </div>
                  
                    <!-- <div class="stat">
                        <div class="stat__icon">
                            <span class="i i-stop3"></span>
                        </div>

                        <span class="t">
                            Заблокирована 756 дн.
                            <span class="text-color-grey text-size-s text-normal">(с 04.12.2019)</span>
                        </span>
                    </div> -->
                    
                    <!--<div class="stat" v-if="currentStatus == defaultStatus">Ждёт {{item.dateDiffStatus}}</div>
                    <template v-else>
                        <div class="stat">{{item.status_name}}</div>
                        <div class="stat-sub" v-if="item.statusDate">{{statusDate(item)}}</div>
                    </template>-->
                    
                    <div class="link">
                        <a href="#" @click.prevent="openCategoryChoice(item)" class="link-icon">
                            <span class="w">
                                <span class="i i-book2"></span>
                            </span>
                            <span v-if="item.sections.length" class="t">Изменить привязку к каталогу</span>
                            <span v-else class="t">Привязать к каталогу</span>
                        </a>
                    </div>
                    
                    <div class="text">
                        <div class="text__icon" v-if="item.lk_status_publicate=='NEW' || item.lk_status_publicate=='CHECK'">
                            <span class="icon">
                                <span class="i i-info i--yellow"></span>
                            </span>
                            <span class="t">
                                <span class="t__title text-size-m text-color-yellow">Ждёт проверки {{item.diff_lk_status_publicate_date}}</span>
                            </span>
                        </div>
                    </div>
                    
                    <div v-if="!item.already_moderated" class="stat-info --red">
                        <span class="w">
                            <span class="i i-warning"></span>
                        </span>

                        <span class="t">Документы не получены</span>
                    </div>


                    <div class="text">
                        <div class="text__icon">
                            <span class="icon">
                                <span class="i i-marker"></span>
                            </span>

                            <span class="t">
                                <span v-if="item.city.length>1" class="t__title text-normal">Городов: {{item.city.length}}</span>
                                <span v-else class="t__title text-normal">{{item.city_name}}</span>

                                <!-- <span class="t__note">{{item.moderator_comment}}</span> -->
                            </span>
                        </div>
                    </div>


                    <div v-if="item.moderator_comment" class="text">
                        <div class="text__icon">
                            <span class="icon">
                                <span class="i i-doc3"></span>
                            </span>

                            <span class="t">
                                <span class="t__note">{{item.moderator_comment}}</span>
                            </span>
                        </div>
                    </div>
                </div>
            </div>
            <div class="wrap text-center mt32" v-if="isLoad && items.length>0">
                <div class="coupon-stat">
                    <div class="coupon-stat__wrap">
                        <catalog-ghost :show-flag="false"></catalog-ghost>
                    </div>
                    <div class="coupon-stat__control"></div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import {mapState} from 'vuex';
import Tooltip from '@/components/Tooltip.vue';
import Loader from '@/components/Loader.vue';
import CategoryChoice from '@/components/forms/CategoryChoice.vue';
import CatalogGhost from '@/components/catalog/CatalogGhost.vue'

export default {
    components: {
        Tooltip,
        Loader,
        CatalogGhost
    },
    created() {},
    mounted() {
        this.fetchData();
        window.addEventListener('scroll', this.handleScroll);
    },
    beforeDestroy() {
        window.removeEventListener('scroll', this.handleScroll);
    },
    computed: {
        ...mapState({
            isLoad: state => state.moderatorCoupon.isLoad,
            menuList: state => state.menus.itemsAction,
            items: state => state.moderatorCoupon.items,
            popups: state => state.popups.items,
            status: state => state.moderatorCoupon.status,
            scrollTop: state => state.moderatorCoupon.backData.scrollTop,
            pageCount: state => state.moderatorCoupon.backData.pageCount,
        }),
        defaultStatus() {
            return this.menuList['coupons'][0]['code'];
        },
        currentStatus() {
            return this.$route.params.action ? this.$route.params.action : this.defaultStatus;
        }
    },
    methods: {
        fetchData () {
            this.$store.dispatch('moderatorCoupon/getItems', {
                status: this.currentStatus,
                reload: true
            }).then(() => {
                setTimeout(() => {
                    if (this.scrollTop) {
                        document.body.scrollTop = document.documentElement.scrollTop = this.scrollTop;
                        this.$store.commit('moderatorCoupon/setNumPage', this.pageCount + 1);
                    }
                    this.$store.commit('moderatorCoupon/setBackData', {
                        scrollTop: 0,
                        pageCount: 0
                    });
                }, 50);
            });

            this.$store.dispatch('moderatorCoupon/setListStatus', {
                listStatus: this.currentStatus
            });
        },
        handleScroll: function () {
            let scroll = window.scrollY;
            let viewport = window.innerHeight;
            let bodyHeight = document.body.clientHeight;
            if(bodyHeight-80 <= scroll + viewport){
                this.$store.dispatch('moderatorCoupon/getItems', {
                    status:this.currentStatus
                })
            }
        },
        openCategoryChoice: function(item){
            this.$store.commit('moderatorCategories/setItem', {
                id: item.id,
                name: item.name
            });
            this.$store.dispatch('popups/open', {name: this.popups.categoryChoice.name});
            this.$nextTick(()=>{
                document.getElementsByTagName('body')[0].classList.add("city-select");
            }
            );
        },
        statusDate(item){
            let res = "";
            res += item.dateDiffStatus +'<span class="text-color-grey text-size-s text-normal"> (c '+item.statusDate+')</span>';
            return res;
        },
    },
    watch: {
        '$route': 'fetchData'
    },
}
</script>
