<template>
    <div class="">
        <div class="buh-invoices mt0" v-if="item.bills.length > 0">
            <div class="item">
                <div class="item__title">
                    <span class="item__num">номер</span>
                    <span class="item__date">выставлен</span>
                    <span class="item__sum">сумма</span>
                    <span class="item__stat">статус</span>
                </div>
                <div class="item" v-for="bill in item.bills">
                    <a class="item__link" href="javascript:void(0);" @click="openBuhPartnerBill(bill)">
                        <span class="item__num">{{ bill.number }}</span>
                        <span class="item__date">{{ bill.dateExposed }}</span>
                        <span class="item__sum">{{ bill.price }} <span class="rouble">{{ $store.getters['options/setRouble'] }}</span></span>
                        <span class="item__stat" :class="['--']+bill.color">
                            <span class="i" :class="['i-']+bill.icon"></span>
                            <span class="t">{{ bill.status }}</span>
                        </span>
                    </a>
                </div>
            </div>
        </div>   
        <div class="wrapper-content mt32" v-else>
            <div class="sect-blank">
                <div class="wrap text-center">
                    <div class="text-size-l text-bolder text-color-grey mt16">У поставщика нет купонов, по которым ещё не выставлены счета...</div>
                </div>

                <div class="mini-zems"></div>
            </div>
            <!-- end sect-blank -->
        </div>
        <popup :is-set-styled="true" :show="popups.buhPartnerBill.show" :name="popups.buhPartnerBill.name" :width="992">
            <auditor-partner-bill :bill="currentBill"></auditor-partner-bill>
        </popup>
    </div>
</template>
<script>
    import {mapState} from 'vuex';
    import Popup from '@/components/modal/Popup.vue';
    import AuditorPartnerBill from '@/components/modal/AuditorPartnerBill.vue';

    export default {
        components: {
            AuditorPartnerBill,
            Popup 
        },
        created() {},
        computed: {
            ...mapState({
                popups: state => state.popups.items,
                item: state => state.auditorProviders.itemDetail,
            })
        },
        data: function() {
            return {
                currentBill: {}
            }
        },
        methods: {
            openBuhPartnerBill(currentBill){
                this.currentBill = currentBill;
                this.$store.dispatch('popups/open', {name: this.popups.buhPartnerBill.name});
            }
        },
    }
</script>