<template>
    <div class="content">
        <template v-if="!repaymentLoading">
            <div class="main-title" v-if="Object.keys(items).length > 0">
                <div class="main-title__wrap main-title--nowrap">
                    <div class="h1">Все купоны покупателя</div>
                </div>
                <div class="main-title__right">
                </div>
            </div>
            <section class="container" v-if="Object.keys(items).length > 0">
                <section class="container__left">
                    <div class="coupon-list mt0">
                        <div v-for="item in items" :key="item.id" :id="'coupon_' + item.id" class="coupon-list__item">
                            <div class="coupon">
                                <tooltip
                                     :direction="'left-center-direction'"
                                     :add-class-wrap="'coupon__bookmark-wrap'"
                                     :add-style="{width: 248 + 'px', 'top': '67px', 'right': '126px'}">
                                    <div slot="tooltipBtn" class="coupon__bookmark">
                                        <div class="wrap">
                                            <span class="d">−{{ item.action.discountFrom }}%</span>
                                            <span class="p">
                                                ≈{{ item.action.realSavingsFormat }}&nbsp;<span class="rouble">{{ $store.getters['options/setRouble'] }}</span>
                                            </span>
                                        </div>
                                    </div>

                                    <div slot="tooltipContent" style="font-size: 16px; ">
                                        При обращении к этому<br> 
                                        поставщику средний чек<br> 
                                        составляет <strong>{{ item.action.averageCheckFormat }}&nbsp;<span class="rouble">{{ $store.getters['options/setRouble'] }}</span></strong>,<br>
                                        а экономия – около
                                        <strong>{{ item.action.realSavingsFormat }}&nbsp;<span class="rouble">{{ $store.getters['options/setRouble'] }}</span></strong>
                                    </div>
                                </tooltip>
                                

                                <span v-if="item.is_cheating" class="coupon__list-mini-status">
                                    <span v-if="item.is_cheating" class="coupon__mini-status" :class="{'--has-pointer': item.id === detail.id}">Нас кинули {{item.moderatorChangeDate}}</span>
                                </span>
                                <span v-else class="coupon__list-mini-status">
                                    <span v-if="item.is_extend && item.statusName == 'Активен'" class="coupon__mini-status" :class="{'--has-pointer': item.id === detail.id}">Продлён {{item.dateDiffModeratorExtended}}</span>
                                    <span v-if="item.status == 8 || item.status == 7 || item.moderator_action == 2" class="coupon__mini-status" :class="{'--has-pointer': item.id === detail.id && !item.is_extend}">{{item.statusName}} {{item.statusDate}}</span>
                                    <span v-else class="coupon__mini-status" :class="{'--has-pointer': item.id === detail.id}">{{item.statusName}} {{item.dateDiffStatus}}</span>
                                    <span v-if="item.is_checked" class="coupon__mini-status">Проверен {{item.couponCheckDate}}</span>
                                </span>

                                <a target="_blank" :href="'/moderator/coupons/id'+item.action.id+'/'"
                                   class="coupon__link"></a>
                                   
                                   
                                <div class="coupon__wrap">
                                    <div class="coupon__img" v-if="item.action.image"
                                         :style="{ backgroundImage: 'url(//api.zemskidki.ru/'+item.action.image.src+')' }"></div>
                                    <div v-else class="coupon__img --empty">
                                        <span class="coupon__img__empty">
                                            <span class="icon">
                                                <span class="i i-photo"></span>
                                            </span>
                                            <span class="t">нет фото</span>
                                        </span>
                                    </div>


                                    <div class="coupon__content">
                                        <div class="coupon__stat">
                                            <span class="elem --views">
                                                <span class="i i-eye"></span>
                                                <span class="t">{{ item.action.showCounterFormat }}</span>
                                            </span>

                                            <span class="elem --buy" v-if="item.action.statistics.sold > 0">
                                                <span class="i i-cart"></span>
                                                <span class="t">{{ item.action.statistics.sold }}</span>
                                            </span>
                                            <span class="elem --comment" v-if=" item.action.countOpinions > 0">
                                                <span class="i i-comment"></span>
                                                <span class="t">{{  item.action.countOpinions }}</span>
                                            </span>
                                        </div>

                                        <div class="coupon__name">{{item.action.name}}</div>

                                        <div class="coupon__company">{{item.action.providerName}}</div>


                                        <div class="w">
                                            <div class="coupon__economy">
                                                <div class="t">
                                                    Экономия
                                                </div>

                                                <div class="p" v-if="item.action.realSavingsFormat">
                                                    ≈{{item.action.realSavingsFormat}}
                                                    <span class="rouble">{{ $store.getters['options/setRouble'] }}</span>
                                                </div>
                                                <div class="p" v-else>
                                                    ?
                                                </div>
                                            </div>


                                            <div class="coupon__info" v-if="item.action.averageCheckFormat">
                                                Средний чек
                                                {{ item.action.averageCheckFormat }} <span class="rouble">{{ $store.getters['options/setRouble'] }}</span>,
                                                скидка
                                                <span class="n">{{ item.action.discountFrom }}%</span>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>


                            <div class="wrapper-content wrapper-content-inner mt8">
                                <template>

                                    <div v-for="comment in item.history" class="mt16">
                                        <div v-html="comment.text"></div>
                                        <div class="mt8 text-color-grey text-size-xs">{{comment.date}}, {{comment.group}}:
                                            {{comment.user}}
                                        </div>
                                    </div>
                                    <div class="mt16">
                                        <div>
                                            <strong>Оформлен:</strong> {{item.city_name}}
                                        </div>
                                        <div class="mt8 text-color-grey text-size-xs">{{item.order_date}}, Покупатель:
                                            {{item.user.fio}}
                                        </div>
                                    </div>

                                    <div class="mt24" v-if="haveButtons(item)">

                                        <button @click="openCouponCheck(item.id)" v-if="item.operations.check" class="btn size-xm btn--ico mr16">
                                            <span class="i i-ok2 mt-1"></span>
                                            <span class="t text-size-s ml4">Отметить проверенным</span>
                                        </button>

                                        <button @click="openCouponExtend(item.id)" v-if="item.operations.extend" class="btn size-xm btn--ico mr16">
                                            <span class="i i-clock2 mt-1"></span>
                                            <span class="t text-size-s ml4">Продлить</span>
                                        </button>

                                        <button @click="openCouponAnnul(item.id)" v-if="item.operations.annul" class="btn size-xm btn--ico mr16">
                                            <span class="i i-close2 mt-1"></span>
                                            <span class="t text-size-s ml4">Аннулировать</span>
                                        </button>

                                        <button @click="openCouponCheating(item.id)" v-if="item.operations.cheating" class="btn size-xm btn--ico mr16">
                                            <span class="i i-warning mt-1"></span>
                                            <span class="t text-size-s ml4">Нас кинули!</span>
                                        </button>
                                    </div>
                                </template>
                            </div>
                        </div>
                        <!-- end coupon-list__item -->

                    </div>

                    <!--                    <div class="wrapper-content wrapper-content-inner mt24">
                                            <div class="h2">История</div>

                                            <template v-if="item.history.length>0">
                                            <div v-for="history in item.history" :key="history.id" class="mt16 mb24">
                                                <div v-html="history.text">
                                                    &lt;!&ndash;<strong>Аннулирован:</strong> Оформил купон ради сиреневой плитки, а её сняли с производства&ndash;&gt;
                                                </div>
                                                <div class="mt8 text-color-grey text-size-xs">{{history.date}}, {{(history.user)?history.user:'Пользователь не найден'}}</div>
                                            </div>

                                            </template>
                                            <template v-else>
                                                <div class="mt16 text-color-grey">Здесь пока ничего нет...</div>
                                            </template>
                                        </div>-->


                </section>


                <aside class="aside aside--right">
                    <div class="aside__sticky">
                        <div class="provider-request-contacts" v-if="detail.user">
                            <div class="wrap" style="padding-bottom: 20px; ">
                                <div class="item">
                                    <div class="t">
                                        <span class="t__t">Регион</span>
                                        <span class="t__d" :class="{'text-color-red': !detail.city_is_worktime}">{{detail.city_time}}</span>
                                    </div>

                                    <div class="d text-bold">{{detail.city_name}}</div>
                                </div>
                            </div>

                            <div class="wrap">
                                <div class="item" v-if="detail.user.phone">
                                    <div class="t">Покупатель</div>

                                    <div class="d mb8 text-bold" v-if="detail.user.fio">{{detail.user.fio}}</div>

                                    <div class="d text-bold" v-if="detail.user.phone">
                                        <a class="text-bold" :href="'tel:'+detail.user.phone.replace(/[^\+0-9]/g, '')">{{
                                            detail.user.phone }}</a>
                                        <a href="" @click.prevent="openSendPopup" class="d__send i i-mail2" title="Написать покупателю"></a>
                                    </div>
                                </div>

                                <!-- По идее теперь это лишнее
                                <div class="item" v-if="item.user.email">
                                    <div class="t">E-mail</div>
                                    <div class="d text-bold">{{item.user.email}}</div>
                                </div> -->
                            </div>


                            <div class="wrap pt16">
                                <div class="text-bold">Статистика</div>

                                <div class="coupon-details-mini__data mt16">
                                    <div class="row-dots">
                                        <span class="l">Всего купонов</span>
                                        <span class="r">{{stat.sold}}</span>
                                    </div>

                                    <div class="row-dots">
                                        <span class="l">Активные</span>
                                        <span class="r">{{stat.valid}}</span>
                                    </div>

                                    <div class="row-dots">
                                        <span class="l">Погашены</span>
                                        <span class="r">{{stat.ext}}</span>
                                    </div>

                                    <div class="row-dots">
                                        <span class="l">Отменены</span>
                                        <span class="r">{{stat.withdrawn}}</span>
                                    </div>

                                    <div class="row-dots">
                                        <span class="l">Аннулированы нами</span>
                                        <span class="r">{{stat.canceledModerator}}</span>
                                    </div>

                                    <div class="row-dots">
                                        <span class="l">Аннулированы поставщиком</span>
                                        <span class="r">{{stat.canceledProvider}}</span>
                                    </div>
                                </div>
                            </div>


                            <div class="wrap">
                                <notes v-if="detail.user.id"
                                       :title="'Заметки о покупателе'"
                                       type="user"
                                       :entityId="detail.user.id"
                                       placeholder="Например, «Очень лояльный, смотрит все наши видео...»"
                                ></notes>
                                <template v-if="!detail.user.id">
                                    <div class="text-color-grey-light text-size-s">Ошибка, не привязан пользователь!
                                    </div>
                                </template>
                            </div>
                        </div>
                    </div>
                </aside>
                <!-- end aside aside--right -->
            </section>

        </template>

        <template v-else>
            <div class="main-title">
                <div class="main-title__wrap main-title--nowrap">
                    <div class="h1">Все купоны покупателя</div>
                </div>
                <div class="main-title__right">
                </div>
            </div>
            <div class="coupon-stat">
                <div class="coupon-stat__wrap">
                    <catalog-ghost :show-flag="false"></catalog-ghost>
                </div>
                <div class="coupon-stat__control"></div>
            </div>
        </template>


        <template v-if="items.length" v-for="item in items">
            <!-- Нас кинули -->
            <popup :is-set-styled="true" :show="getPopupParams(item.id, 'cheating')" @close="closeCouponCheating(item.id)">
                <coupon-cheating @close="closeCouponCheating(item.id, $event)" :name="item.action.name" :id="item.id"></coupon-cheating>
            </popup>

            <!-- Продлить -->
            <popup :is-set-styled="true" :show="getPopupParams(item.id, 'extend')" @close="closeCouponExtend(item.id)">
                <coupon-extend @close="closeCouponExtend(item.id, $event)" :name="item.action.name" :annul="item.autoAnnulDays" :id="item.id"></coupon-extend>
            </popup>

            <!-- Аннулировать -->
            <popup :is-set-styled="true" :show="getPopupParams(item.id, 'annul')" @close="closeCouponAnnul(item.id)">
                <coupon-annul @close="closeCouponAnnul(item.id, $event)" :name="item.action.name" :id="item.id"></coupon-annul>
            </popup>

            <!-- Отметить проверенным -->
            <popup :is-set-styled="true" :show="getPopupParams(item.id, 'check')" @close="closeCouponCheck(item.id)">
                <coupon-check @close="closeCouponCheck(item.id, $event)" :name="item.action.name" :id="item.id"></coupon-check>
            </popup>
        </template>

        <!-- письмо покупателю -->
        <popup :is-set-styled="true" :show="popups.sendMessageBuyer.show" :name="popups.sendMessageBuyer.name">
            <send-message-buyer type="buyer" send-to="buyer" :id="detail.id"></send-message-buyer>
        </popup>
    </div>
</template>
<script>

    import {mapState} from 'vuex';
    import Tooltip from '@/components/Tooltip.vue';
    import Popup from '@/components/modal/Popup.vue';
    import CouponCheating from '@/components/modal/CouponCheating.vue';
    import CouponExtend from '@/components/modal/CouponExtend.vue';
    import CouponAnnul from '@/components/modal/CouponAnnul.vue';
    import CouponCheck from '@/components/modal/CouponCheck.vue';
    import SendMessageBuyer from '@/components/modal/SendMessageBuyer.vue';
    import Notes from '@/components/moderator/Notes.vue';
    import {scrollTo} from '@/helpers/animate.js';
    import CatalogGhost from '@/components/catalog/CatalogGhost.vue'

    export default {
        data: function () {
            return {
                popupsDetail: {}
            }
        },
        metaInfo() {
            return {
                //title: this.item && this.item.action ? this.item.action.name : '',
                //titleTemplate: this.$store.state.options.titleTemplate
            }
        },
        components: {
            Tooltip,
            Popup,
            CouponCheating,
            CouponExtend,
            CouponAnnul,
            CouponCheck,
            SendMessageBuyer,
            Notes,
            CatalogGhost,
        },
        mounted: function () {
            this.$store.dispatch('moderatorRepayment/initDetailPage', {route: this.$route}).then(() => {
                this.initPopups();

                if (this.items[0].id !== this.detail.id) {
                    this.$nextTick(() => {
                        scrollTo('#coupon_' + this.detail.id, true, 0);
                    })
                }
            });
        },
        beforeDestroy() {
            this.$store.commit('moderatorRepayment/setItemDetailEmpty');
        },
        computed: {
            ...mapState({
                detail: state => state.moderatorRepayment.itemDetail,
                stat: state => state.moderatorRepayment.stat,
                items: state => state.moderatorRepayment.detailItems,
                repaymentLoading: state => state.moderatorRepayment.isLoadDetail,
                repaymentListStatus: state => state.moderatorRepayment.listStatus,
                menuList: state => state.menus.itemsAction,
                popups: state => state.popups.items,
                user: state => state.users.user,
                actionMenuList: state => state.menus.itemsAction,
            }),
            repaymentBackId() {
                let statusId = this.repaymentListStatus ? this.repaymentListStatus : this.detail['moderator_action'];
                return statusId;
            },
            repaymentBackCode() {
                let statusId = this.repaymentListStatus ? this.repaymentListStatus : this.detail['moderator_action'];

                for (let itemIndex in this.actionMenuList['repayment']) {
                    if (this.actionMenuList['repayment'][itemIndex]['id'] == statusId) {
                        return this.actionMenuList['repayment'][itemIndex]['code'];
                    }
                }
            },
            showCouponLink(){
                let result = true;
                if(this.user.permissionModerator !== undefined && this.user.permissionModerator.length > 0){
                    if(this.user.permissionModerator.indexOf(19) !== -1){
                        result = false;
                    }
                }
                return result
            },
        },
        methods: {
            haveButtons: function(item) {
                return item.operations.check ||
                    item.operations.extend ||
                    item.operations.annul ||
                    item.operations.cheating;
            },
            getPopupParams: function (id, code) {
                let state = false;

                if (typeof (this.popupsDetail[id]) !== 'undefined') {
                    state = this.popupsDetail[id][code].open;
                }


                return state;
            },
            initPopups: function () {
                for (let ind in this.items) {
                    this.$set(this.popupsDetail, this.items[ind].id, {
                        cheating: {open: false},
                        extend: {open: false},
                        annul: {open: false},
                        check: {open: false},
                    });
                }
            },
            openSendPopup() {
                this.$store.dispatch('popups/open', {name: this.popups.sendMessageBuyer.name});
            },
            openCouponCheck: function (id) {
                setTimeout(()=>{
                    this.$set(this.popupsDetail[id].check, 'open', true);
                },10);
            },
            openCouponCheating: function (id) {
                setTimeout(()=>{
                    this.$set(this.popupsDetail[id].cheating, 'open', true);
                },10);
            },
            openCouponExtend: function (id) {
                setTimeout(()=>{
                    this.$set(this.popupsDetail[id].extend, 'open', true);
                },10);
            },
            openCouponAnnul: function (id) {
                setTimeout(()=>{
                    this.$set(this.popupsDetail[id].annul, 'open', true);
                },10);
            },
            closeCouponCheck: function (id, event) {
                this.$set(this.popupsDetail[id].check, 'open', false);
                if (typeof(event) !== 'undefined' && event.apply) {
                    this.$router.push('/moderator/repayment/' + this.repaymentBackCode + '/');
                }
            },
            closeCouponCheating: function (id, event) {
                this.$set(this.popupsDetail[id].cheating, 'open', false);
                if (typeof(event) !== 'undefined' && event.apply) {
                    this.$router.push('/moderator/repayment/' + this.repaymentBackCode + '/');
                }
            },
            closeCouponExtend: function (id, event) {
                this.$set(this.popupsDetail[id].extend, 'open', false);
                if (typeof(event) !== 'undefined' && event.apply) {
                    this.$router.push('/moderator/repayment/' + this.repaymentBackCode + '/');
                }
            },
            closeCouponAnnul: function (id, event) {
                this.$set(this.popupsDetail[id].annul, 'open', false);
                if (typeof(event) !== 'undefined' && event.apply) {
                    this.$router.push('/moderator/repayment/' + this.repaymentBackCode + '/');
                }
            },
        }
    }

</script>

<style scoped>

</style>
