<template>
    <div>
        <div class="popup__header popup__header--dotted">
            <div class="title">Аннулировать купон</div>
            <div class="subtitle">{{name}}</div>
        </div>
        
        
        <div class="popup__content">
            <div class="h2">Прокомментируйте своё решение</div>
            
            <div class="mt16">
                <textarea-field :is-error="form.comment.error" @focus="form.comment.error = false "
                                v-model="form.comment.text"
                                :auto-focus="true"
                                :placeholder="'Например, «Оформил купон ради сиреневой плитки, а её сняли с производства...»'"
                                :add-style="{minHeight: 65 + 'px', 'height': 'auto'}"
                ></textarea-field>
            </div>
            
            
            <div class="row-b mt32">
                <div class="row-b__col elem-pos-fix mr24">
                    <submit-btn @click.native="send" :type="form.btnType">Подтвердить</submit-btn>
                </div>
                
                
                <div class="row-b__col elem-pos-fit">
                    <div class="text-color-grey">
                        Внимание! Для аннулирования купона нужна<br>
                        веская причина. Убедитесь, что она у вас есть.
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import TextareaField from '@/components/forms/TextareaField.vue'
    import {mapState} from 'vuex';
    import SubmitBtn from '@/components/forms/SubmitBtn.vue';

    export default {
        name: "CouponAnnul",
        components: {
            TextareaField,
            SubmitBtn
        },
        props: ['id', 'name'],
        data: function(){
            return {
                form:{
                    btnType: 'normal',
                    type: 'annul',
                    comment:{
                        text: '',
                        error: false
                    }
                }
            }
        },
        mounted() {

        },
        computed: {
            ...mapState({
                popups: state => state.popups.items,
                items: state => state.moderatorRepayment.detailItems,
                //item: state => state.moderatorRepayment.itemDetail,
            }),
        },
        methods: {
            send: function(){
                let error = false;
                this.form.comment.error = false;
                if(this.form.comment.text.length < 10){
                    error = true;
                    setTimeout(()=>{
                        this.form.comment.error = true;
                    },150);
                }
                if(!error){
                    this.form.btnType = 'load';
                    this.$store.dispatch('moderatorRepayment/sendForm', {
                        type: this.form.type,
                        id: this.id,
                        comment: this.form.comment.text,
                    }).then(()=>{
                            this.form.btnType = 'ok';

                            let apply = true;
                            for (let ind in this.items) { 
                                if(this.items[ind].status == 4 && this.items[ind].is_extend!=true && this.id!=this.items[ind].id){
                                    apply = false;
                                }
                            }
                            setTimeout(()=>{
                                this.$store.dispatch('moderatorRepayment/initDetailPage',{route: this.$route});
                                this.$emit('close', {apply: apply});
                            }, 300);
                    });
                }
            }
        },
        beforeDestroy() {
            this.$store.commit('popups/close', {name: this.popups.couponAnnul.name});
        }
    }
</script>