<template>
    <div class="item" :class="{open:opened}">
        <div  class="item__title" @click="opened = !opened">
            <a class="link" href="javascript:void(0)">{{ propTitle }}</a>
        </div>
        <transition
                @enter="enterSlideUp"
                @leave="leaveSlideUp"
                :css="false">
            <div v-show="opened" class="item__hidden">
                <div class="item__content">
                    <slot></slot>
                </div>
            </div>
        </transition>
    </div>
    <!-- end item -->
</template>

<script>
    import Velocity from 'velocity-animate';

    export default {
        name: "ItemShow",
        props: {
            open: {default: false},
            propTitle: {default: ''}
        },

        data: function () {
            return {
                opened: false,
            }
        },

        computed: {},

        created: function () {
            this.$set(this, 'opened', !!this.open);
        },

        mounted: function () {

        },

        methods: {
            enterSlideUp: function (el, done) {
                Velocity(el, "finish");
                Velocity(el,"slideDown", { duration: 300 }).then(function(e) { done(); });
            },

            leaveSlideUp: function (el, done) {
                Velocity(el, "finish");
                Velocity(el,"slideUp", { duration: 300 }).then(function(e) { done(); });
            },
        }
    }
</script>

