<template>
    <div>
        <div class="popup__header popup__header--dotted ">
            <div class="title">Написать поставщику</div>
            <div class="subtitle">{{providerData.name}}</div>
            <div class="text-color-white-trans mt16">Получатель: {{providerData.fio}} ({{providerData.position}})</div>
        </div>
        
        
        <div class="popup__content">
            
            <div class="row-b mb16">
                <div class="row-b__col elem-pos-fit">
                    <div class="h2">СМС на {{providerData.phone}}</div>
                </div>
                
                <div class="row-b__col elem-pos-fix">
                    <div v-if="templates.length>0" class="text-size-s">
                        Шаблоны: 
                        <template v-for="(template, index) in templates ">
                            <a @click="setTemplateText(index)" :key="index" href="#">{{template.NAME}}</a>
                            <template v-if="templates.length-1 != index">,&nbsp;</template>
                        </template>
                    </div>
                </div>
            </div>
            
            <textarea-field :is-error="form.sms.error" @focus="form.sms.error = false " v-model="form.sms.text" :hasCounterSMS="true" :placeholder="'Текст СМС'" :add-style="{minHeight: 80 + 'px'}"></textarea-field>
            
            <div class="h2 mb16">Письмо на {{providerData.email}}</div>
            
            <div class="mb8">
                <input @focus="form.emailTitle.error=false" :class="{error:form.emailTitle.error}" v-model="form.emailTitle.text" type="text" class="inp size-s full" placeholder="Тема письма">
            </div>
            
            <!--<textarea-field v-model="form.emailText.text"  :add-style="{minHeight: 333 + 'px'}" :placeholder="'Текст письма'"></textarea-field>-->
            <text-editor-min @error="form.emailText.error=false" :error="form.emailText.error" :hide-toolbar="true" v-model="form.emailText.text" :placeholder="'Текст письма'"/>
            
            
            <div class="row-b mt32">
                <submit-btn class="" @click.native="send" :type="form.btnType">Отправить</submit-btn>
                <div class="row-b__col elem-pos-fit">
                    <div class="text-color-grey">Если вы не хотите отправлять письмо или СМС,<br>
                    просто оставьте эти поля пустыми</div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import {mapState} from 'vuex'
    import TextareaField from '@/components/forms/TextareaField.vue'
    import TextEditorMin from '@/components/editor/TextEditorMin.vue'
    import SubmitBtn from '@/components/forms/SubmitBtn.vue'

    export default {
        name: "SendMessagePartner",
        props: ['type','id','sendTo'],
        components: {
            TextareaField,
            TextEditorMin,
            SubmitBtn
        },
        mounted() {
            this.$store.dispatch('moderatorTemplates/getTemplates',{code: 'MODAL_SEND_LETTER'});
        },
        data: function(){
            return {
                form: {
                    sms: {text:'', error: false},
                    emailTitle: {
                        text: '',
                        error: false,
                    },
                    emailText: {
                        text: '',
                        error: false
                    },
                    btnType: 'normal'
                }
            }
        },
        methods: {
            close: function (popup) {
                this.$store.commit('popups/close', {name: popup});
            },
            setTemplateText: function(index){
                let fio = this.providerData.fio.split(' '),
                    text = this.templates[index]['~DETAIL_TEXT'];
                text = text.replace('#NAME#', fio[1]);
                this.form.sms.text = text;
            },
            send: function(){
                let error = false;
                this.form.emailTitle.error = false;
                this.form.emailText.error = false;
                this.form.sms.error = false;

                /*if((this.form.emailTitle.text.length == 0) &&
                (this.form.emailText.text.length == 0) &&
                this.form.sms.text.length == 0){
                    this.form.emailTitle.error = true;
                    this.form.emailText.error = true;
                    this.form.sms.error = true;
                    error = true;     
                }*/
                if((this.form.emailTitle.text.length == 0) && this.form.emailText.text.length > 0){
                    setTimeout(()=>{
                        this.form.emailTitle.error = true;
                    },150);

                    error = true;  
                }
                if((this.form.emailTitle.text.length > 0) && this.form.emailText.text.length == 0){
                    setTimeout(()=>{
                        this.form.emailText.error = true;
                    },150);

                    error = true;
                }
                if(!error){
                    this.form.btnType = 'load';
                    this.$store.dispatch('moderatorProviders/sendLetter', {
                        type: this.type,
                        id: this.id,
                        sendTo: this.sendTo,
                        smsText: this.form.sms.text,
                        emailText: this.form.emailText.text,
                        emailTitle: this.form.emailTitle.text, 
                    }).then(()=>{
                            this.form.btnType = 'ok';
                            setTimeout(()=>{
                                this.$store.commit('popups/close', {name: this.popups.sendMessagePartner.name});
                            }, 300);
                    });
                }
            }
        },
        computed: {
            ...mapState({
                popups: state => state.popups.items,
                templates: state => state.moderatorTemplates.templates,
                item: state => state.moderatorCooperation.itemDetail,
            }),
            providerData(){
                let data = {};
                if(this.type == "cooperation"){
                    data.name = this.$store.state.moderatorCooperation.itemDetail.name;
                    data.position = this.$store.state.moderatorCooperation.itemDetail.position;
                    data.fio = this.$store.state.moderatorCooperation.itemDetail.fio;
                    data.phone = this.$store.state.moderatorCooperation.itemDetail.phone;
                    data.email = this.$store.state.moderatorCooperation.itemDetail.email;
                }
                if(this.type == "partner"){
                    data.name = this.$store.state.moderatorProviders.itemDetail.name;
                    data.position = this.$store.state.moderatorProviders.itemDetail.position;
                    data.fio = this.$store.state.moderatorProviders.itemDetail.requestFIO;
                    data.phone = this.$store.state.moderatorProviders.itemDetail.phone;
                    data.email = this.$store.state.moderatorProviders.itemDetail.email;
                }
                if(this.type == "buh"){
                    data.name = this.$store.state.auditorProviders.itemDetail.provider.name;
                    data.position = this.$store.state.auditorProviders.itemDetail.request.position;
                    data.fio =  this.providerName;  
                    data.phone = this.$store.state.auditorProviders.itemDetail.bookerPhone;
                    data.email = this.$store.state.auditorProviders.itemDetail.bookerEmail;
                }
                return data;
            },
            removeError(){

            },
            providerName: function () {
                let result = this.$store.state.auditorProviders.itemDetail.request.fio;
                if (this.$store.state.auditorProviders.itemDetail.providerName) {
                    result = this.$store.state.auditorProviders.itemDetail.providerName;
                }

                return result;
            }

        },
        beforeDestroy(){
            this.$store.commit('popups/close', {name: this.popups.sendMessagePartner.name});
        }
    }
</script>

