<template>
    <partner-about-editor :is-moderator="true" v-if="init"></partner-about-editor>
</template>



<script>
    import {mapState} from 'vuex';
    import PartnerAboutEditor from '@/components/partner/PartnerAboutEditor.vue';

    export default {
        metaInfo() {
            return {
                title: 'Конструктор страницы «Поставщик»',
                titleTemplate: this.$store.state.options.titleTemplate
            }
        },
        name: "CouponsAboutProviderEdit",
        components: {
            PartnerAboutEditor,
        },
        data: function() {
            return {
                init: false
            }
        },
        mounted: function () {
            if(this.$route.name === 'providers-about-edit'){
                this.$store.dispatch('partner/getByID',{id: this.$route.params.id}).then(()=>{
                    this.init = true;
                });
            } else {
                this.$store.dispatch('partner/getByActionID',{action: this.$route.params.id}).then(()=>{
                    this.init = true;
                });
            }

        },
        beforeDestroy(){
            this.$store.commit('partner/partnerClear');
        },
        computed: {

        },
        methods: {

        },
    }
</script>