<template>
    <div>
        <div class="popup__content">
            <div class="h1 mb8">Заказать по ссылке...</div>


            <div class="row-b mt40">
                <div class="row-b__col elem-pos-fix" style="width: 463px; min-width: 463px; ">
                    <input-styled
                            :isClearBtn="true"
                            name="phoneCall"
                            v-model="link.value"
                            @focus="link.error = false; linkFocus = true"
                            @blur="linkFocus = false"
                            :is-error="link.error"
                            :placeholder="'Ссылка на оформление заказа'"
                            :placeholder-input="'yourshop.ru/dump'"></input-styled>
                </div>

                <div class="row-b__col elem-pos-fit" :class="{'--active': linkFocus}">
                    <div class="construct-form__note" :class="{error:link.error}">Ссылка будет доступна покупателю <br>
                        только после оформления купона</div>
                </div>
            </div>
            <!-- end row-b -->


            <div class="construct-use-preview">
                <div class="title">Проверьте, как считывается текст:</div>

                <div class="text">
                    <div class="text-bold">Чтобы воспользоваться скидкой, нужно:</div>

                    <ol>
                        <li>Оформить купон</li>
                        <li>Сделать заказ по <a v-if="link.value" target="_blank" :href="link.value">ссылке</a><span v-else>ссылке</span>. В описании обязательно укажите кодовое слово «ЗЕМСКИДКИ»</li>
                    </ol>
                </div>
            </div>




            <div class="row-b mt32">
                <div class="row-b__col elem-pos-fix mr24">
                    <button @click="save" class="btn mr32">Продолжить</button>
                    <button @click="close" class="btn btn--grey">Отмена</button>
                </div>


                <div class="row-b__col elem-pos-fit">
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import {mapState} from 'vuex'
    import InputStyled from '@/components/forms/InputStyled.vue'
    import {isAnyPhone} from '@/helpers/validate.js';

    export default {
        name: "ConstructActionLink",
        components: {
            InputStyled,
        },
        props: {
            linkProp: '',
        },
        data: function() {
            return {
                link: {
                    value: '',
                    error: false,
                },
                linkFocus: false
            }
        },
        created() {
            this.link.value = this.linkProp.value ? this.linkProp.value : '';
        },
        methods: {
            save() {
                let error = 0;
                this.link.error = false;
                if(this.link.value.length < 5){
                    setTimeout(()=>{
                        this.link.error = true;
                    },150);
                    error++;
                }
                if(error === 0){
                    this.$emit('save',{
                        link: this.link,
                    });
                    this.$store.commit('popups/close', {name: this.popups.constructActionGoLink.name});
                }
            },
            close() {
                this.$store.commit('popups/close', {name: this.popups.constructActionGoLink.name});
            }
        },
        computed: {
            ...mapState({
                popups: state => state.popups.items,
            }),
        },
        beforeDestroy(){
            this.$store.commit('popups/close', {name: this.popups.constructActionGoLink.name});
        }
    }
</script>

<style scoped>

