<template>
    <div class="content">
        <div class="nav-top" v-if="Object.keys(action).length > 0">
            <div class="nav-top__l">
                <div class="nav-top__category mr16" v-if="action.statusModerator==status.publick">
                    <span class="i i-play6" style="transform: scale(0.9)"></span>
                    <span class="t">Активна {{action.diff_publicate_date}} (с {{action.publicate_date}})</span>
                </div>

                <div class="nav-top__category mr16" v-if="action.lk_status_publicate=='NEW' || action.lk_status_publicate=='CHECK'">
                    <span class="i i-info i--yellow" style="transform: scale(0.8)"></span>
                    <span class="t">Ждёт проверки {{action.diff_lk_status_publicate_date}}</span>
                </div>
                <div class="nav-top__category mr16" v-if="action.statusModerator==status.blocked">
                    <span class="i i-stop3" style="transform: scale(0.75)"></span>
                    <span class="t">Заблокирована {{action.dateDiff}} (c {{action.statusDate}})</span>
                </div>
                <!--

                <div class="nav-top__category mr16">
                    <span class="i i-info i--yellow" style="transform: scale(0.8)"></span>
                    <span class="t">Ждёт проверки 2 дн.</span>
                </div>

                <div class="nav-top__category mr16">
                    <span class="i i-gear" style="transform: scale(0.75)"></span>
                    <span class="t">В разработке 99 дн.</span>
                </div>

                <div class="nav-top__category mr16">
                    <span class="i i-trash" style="transform: scale(0.75)"></span>
                    <span class="t">Удалена 99 дн.</span>
                </div>

                <div class="nav-top__category mr16">
                    <span class="i i-pause3" style="transform: scale(0.75)"></span>
                    <span class="t">Приостановлена 99 дн.</span>
                </div> -->


                <!-- <span class="text-size-s text-color-grey"> -->

                <!--<span class="text-size-s text-color-grey">
                
                    <template v-if="action.status_moderator == defaultStatus">Ждёт {{action.dateDiffStatus}}</template>
                    <template v-else>
                        {{statusDate(action)}}
                    </template>
                </span>-->
            </div>
            <div class="nav-top__r"></div>
        </div>
        <div class="main-title" v-if="Object.keys(action).length > 0">
            <div class="main-title__wrap main-title--nowrap">
                <div v-if="action.name" class="h1" v-html="action.name"></div>
                <div v-else class="h1 text-color-red">Название акции не указано</div>
            </div>

            <div class="main-title__right">
            </div>
        </div>


        <section class="container" v-if="Object.keys(action).length > 0">
            <lk-action-detail @changed="changeTab"  v-if="Object.keys(action).length > 0" :action="action">

                <!-- <provider-about
                    :inCoupons="true"
                    :partner="provider"
                    :canEditText="true"
                    :innOgrnCheck="false"
                    :requisitesArr="provider.requisitesArr"
                    :site="provider.site"></provider-about> -->

                <tab id="tab-supplier" name="Поставщик" :suffix="moderateAboutSuffix">
                    <provider-about
                        :inCoupons="true"
                        :partner="provider"
                        :canEditText="true"
                        :innOgrnCheck="false"
                        :requisitesArr="provider.requisitesArr"
                        :site="provider.site"></provider-about>
                </tab>


                <!-- <statistics-detail :action="action"></statistics-detail> -->
                <!-- <opinion-list :action-id="action.id" v-if="loadOpinion"></opinion-list> -->
            </lk-action-detail>



        <aside-sticky :margin-top="72">


            <aside class="aside aside--right">
                <div class="aside__sticky js-sticky" id="aside-sticky">
                    <div class="coupon-details-mini">
                        <div class="coupon-details-mini__supplier">
                            <div class="title">Поставщик</div>

                            <div class="l">
                                <a v-if="isTabSupplier" href="javascript:void(0);" @click="setTabSupplier()">
                                    {{action.provider.name}}
                                </a>

                                <span v-else>
                                    {{action.provider.name}}
                                </span>
                            </div>
                        </div>

                        <div class="coupon-details-mini__sep">
                            <div></div><div></div><div></div><div></div><div></div>
                        </div>


                        <div class="coupon-details-mini__discount">
                            <div class="title">Скидка</div>
                            <div class="t">{{ action.discountFrom }}%</div>
                        </div>


                        <div class="coupon-details-mini__price" v-if="action.realSavingsFormat">
                            <tooltip
                                 :direction="'left-center-direction'"
                                 :add-class-wrap="'dib'"
                                 :add-class-btn="'dib'"
                                 :add-style="{width: 248 + 'px',}">
                                <span slot="tooltipBtn" class="t">
                                    ≈{{ action.realSavingsFormat }}
                                    <span class="rouble">{{ $store.getters['options/setRouble'] }}</span>
                                </span>

                                <div slot="tooltipContent" style="font-size: 16px; ">
                                    При обращении к этому<br>
                                    поставщику средний чек<br>
                                    составляет <strong>{{ action.averageCheckFormat }} <span class="rouble">{{ $store.getters['options/setRouble'] }}</span></strong>,<br>
                                    а экономия – около
                                    <strong>{{ action.realSavingsFormat }}
                                    <span class="rouble">{{ $store.getters['options/setRouble'] }}</span></strong>
                                </div>
                            </tooltip>
                        </div>

                        <div v-else class="coupon-details-mini__price text-color-red">
                            ?
                        </div>

                        <!-- <div class="coupon-details-mini__img" v-if="action.image">
                            <img :src="'//api.zemskidki.ru/'+action.image" :alt="action.name">
                        </div> -->

                        <div class="coupon-details-mini__btn disabled-content" style="opacity: 0.2">
                            <button class="btn full">
                                <span class="t">Оформить бесплатно</span>
                            </button>
                        </div>

                        <div class="coupon-details-mini__stat">
                            <div class="t">
                                <span class='i i-eye'></span>
                                <span class="t__t" v-html="action.showCounterFormat"></span>
                            </div>


                            <div class="t" v-if="action.statistics.sold > 0">
                                <span class='i i-cart'></span>
                                <span class="t__t" v-html="+action.statistics.sold"></span>
                            </div>

                            <!--<div class="t" v-if="action.countOpinionsFormat > 0">
                                <span class='i i-comment'></span>
                                <span class="t__t" >{{ action.countOpinionsFormat }}</span>
                            </div>-->
                        </div>
                    </div>
                    <!-- end coupon-details-mini -->


                    <div class="mt32 text-size-s text-center text-color-grey" v-if="action.last_editor">
                        <div class="">Редактор:</div>
                        <div class="text-bold">{{ action.last_editor.fio }}</div>
                        <div class="text-bold">
                            <a class="text-bold" v-if="action.last_editor && action.last_editor.phone" :href="'tel:'+action.last_editor.phone.toString().replace(/[^\+0-9]/g, '')">{{action.last_editor.phone}}</a>
                        </div>
                    </div>



                    <div class="mt32">
                        <div class="nav-moderator nav-moderator--city unactive">
                            <div class="title-city">
                                <span>
                                    <span class="icon"><span class="i"></span></span>
                                    <span class="t">Городов: {{action.city.length}}</span>
                                </span>
                            </div>

                            <a @click.prevent="openCategoryChoice(action)" v-if="action.sections.length == 0" class="nav-wrapper__link-catalog" href="#"></a>

                            <div v-if="action.sections.length == 0" class="nav-wrapper sect-blank">
                                <div class="wrap text-center">
                                    <div class="text-bolder" style="font-size: 19px; line-height: 23px; color: rgba(0, 0, 0, 0.12);">Акция не привязана<br> к&nbsp;каталогу</div>
                                </div>
                                <div class="mini-zems-u"></div>
                            </div>

                            <div v-else class="nav-wrapper">
                                <categories-coupon :firstLevel="true" :selected="action.sections" :categories="categories"></categories-coupon>
                            </div>
                        </div>
                    </div>
                </div>
            </aside>
            <!-- end aside aside--right -->
          </aside-sticky>
        </section>


        <section class="container" v-if="isLoading">
            <div class="preloader-page">
                <div class="preloader"></div>
            </div>
        </section>


        <section class="container" v-if="!isLoading && Object.keys(action).length === 0">
            <div class="h1 text-color-red">Акция не найдена</div>
        </section>
        <!-- end container -->


        <popup :is-set-styled="true" :show="popups.moderatorCouponApprove.show" :name="popups.moderatorCouponApprove.name">
            <moderator-coupon-approve/>
        </popup>


        <popup :is-set-styled="true" :show="popups.moderatorCouponReject.show" :name="popups.moderatorCouponReject.name">
            <moderator-coupon-reject/>
        </popup>

        <popup  :is-set-styled="true" :show="popups.moderatorCouponApproveEdit.show" :name="popups.moderatorCouponApproveEdit.name">
            <moderator-coupon-approve-edit/>
        </popup>

        <popup :is-set-styled="true" :show="popups.moderatorCouponRejectEdit.show" :name="popups.moderatorCouponRejectEdit.name">
            <moderator-coupon-reject-edit/>
        </popup>

        <popup :is-set-styled="true" :show="popups.moderatorCouponBlock.show" :name="popups.moderatorCouponBlock.name">
            <moderator-coupon-ban/>
        </popup>


        <popup :is-set-styled="true" :show="popups.moderatorCouponUnBlock.show" :name="popups.moderatorCouponUnBlock.name">
            <moderator-coupon-unblock/>
        </popup>


        <popup :show="popups.moderatorCouponAlert.show" :name="popups.moderatorCouponAlert.name" :width="640">
            <div class="popup__header mb16">
                <div class="title">Акция не привязана к каталогу</div>
            </div>

            <div class="popup__content">
                <div>Присвойте категорию, после этого вы сможете опубликовать акцию...</div>

                <div class="mt32">
                    <button class="btn" @click="openCategoryChoice(action)">Присвоить категорию</button>
                </div>
            </div>
        </popup>


        <popup v-if="action" :show="popups.supplierInfo.show" :name="popups.supplierInfo.name" :width="704" :add-class="'popup-supplier'" >
            <div class="popup-partner-info">
                <provider-about
                        :inCoupons="true"
                        :partner="provider"
                        :canEditText="true"
                        :innOgrnCheck="false"
                        :requisitesArr="provider.requisitesArr"
                        :site="provider.site"></provider-about>
            </div>
        </popup>
    </div>
</template>
<script>
    import {mapState} from 'vuex';
    import LkActionDetail from '@/components/LkActionDetail.vue';
    import Tooltip from '@/components/Tooltip.vue';
    import Loader from '@/components/Loader.vue';
    import Popup from '@/components/modal/Popup.vue';
    import Tab from '@/components/tabs/Tab.vue';
    import SubmitBtn from '@/components/forms/SubmitBtn.vue';
    import ModeratorCouponApprove from '@/components/modal/ModeratorCouponApprove.vue';
    import ModeratorCouponReject from '@/components/modal/ModeratorCouponReject.vue';
    import ModeratorCouponApproveEdit from '@/components/modal/ModeratorCouponApproveEdit.vue';
    import ModeratorCouponRejectEdit from '@/components/modal/ModeratorCouponRejectEdit.vue';
    import ModeratorCouponBan from '@/components/modal/ModeratorCouponBan.vue';
    import ModeratorCouponUnblock from '@/components/modal/ModeratorCouponUnblock.vue';
    import ProviderAbout from '@/components/moderator/ProviderAbout.vue';
    import OpinionList from '@/components/OpinionList.vue';
    import StatisticsDetail from '@/components/catalog/StatisticsDetail.vue'
    import CategoriesCoupon from '@/components/moderator/CategoriesCoupon.vue'
    import AsideSticky from '@/components/AsideSticky.vue'

    export default {
        metaInfo() {
            return {
                title: this.action.name?this.action.name:'',
                titleTemplate: this.$store.state.options.titleTemplate
            }
        },
        components: {
            LkActionDetail,
            Tooltip,
            Loader,
            Popup,
            Tab,
            SubmitBtn,
            ModeratorCouponApprove,
            ModeratorCouponReject,
            ModeratorCouponApproveEdit,
            ModeratorCouponRejectEdit,
            ModeratorCouponBan,
            ModeratorCouponUnblock,
            ProviderAbout,
            OpinionList,
            StatisticsDetail,
            CategoriesCoupon,
            AsideSticky,
        },
        data: function () {
            return {
                loadOpinion: false,
                currentTab: '',
                unsubscribe: '',
                isTabSupplier: false,
            }
        },
        mounted: function () {
            this.$store.dispatch('moderatorCoupon/initDetailPage', {route: this.$route}).then(()=>{
                if(window.location.hash === '#open_catalog_detail'){
                    this.openCategoryChoice(this.action)
                }
            });
            this.unsubscribe = this.$store.subscribe((mutation, state) => {
                if(mutation.type=='moderatorCategories/setClose'){
                    if(state.moderatorCategories.itemSaves){
                        this.$store.dispatch('moderatorCoupon/refreshSections', {id: this.$route.params.id});
                    }
                }
            });
        },
        beforeDestroy: function(){
            this.$store.commit('moderatorCoupon/setCouponDetail', {item:{}});
            this.unsubscribe();
        },
        computed: {
            ...mapState({
                action: state => state.moderatorCoupon.itemDetail,
                categories: state => state.moderatorCoupon.categories,
                provider: state => state.moderatorCoupon.provider,
                popups: state => state.popups.items,
                menuList: state => state.menus.itemsAction,
                isLoading: state => state.moderatorCoupon.isLoadDetail,
                status: state => state.moderatorCoupon.status,
            }),
            defaultStatus() {
                if(this.menuList['coupons']){
                    return this.menuList['coupons'][0]['id'];
                }
            },
            moderateAboutSuffix(){
                let suffix;
                if((+this.provider.countActive > 0 || +this.provider.countModerate > 0) && this.provider.datePublicAbout.length ===0 && this.provider.status === 'verify'){
                    suffix = "<sup class='--suffix i i--orange i-info3'></sup>";
                } else if(this.provider.dateEditAbout && this.provider.status === 'verify'){
                    suffix = "<sup class='--suffix i i--yellow i-info3'></sup>";
                }
                return suffix;


            }
        },
        methods: {
            changeTab(data) {
                this.currentTab = data.tab.hash;
                if(this.currentTab === '#tab-opinions' && this.action.id){
                    this.loadOpinion = true;
                }

                this.isTabSupplier = (this.currentTab === '#tab-supplier') ? false : true;
            },
            statusDate(item){
                let res = "";
                res = item.status_name+" ";
                if(item.statusModerator != 60 && item.statusModerator != 61){
                    res += "с ";
                }
                res += item.statusDate;
                return res;
            },
            openCategoryChoice: function(item){
                this.$store.commit('moderatorCategories/setItem', {
                    id: item.id,
                    name: item.name
                });
                this.$store.commit('popups/close', {name: this.popups.moderatorCouponAlert.name});
                this.$store.dispatch('popups/open', {name: this.popups.categoryChoice.name});
                document.getElementsByTagName('body')[0].classList.add("city-select");
            },
            setTabSupplier() {
                document.querySelector("[aria-controls='#tab-supplier']").click();
            },
        }
    }
</script>
<style>
    .tabs-nav__btn.active .--suffix{
        display: none;
    }
</style>
