<template>
    <div class="popup-info">
        <div>
            <img src="@/assets/img/icon-ok-big.svg" alt="">
        </div>

        <div class="text mt32">
            <div class="text-size-xxl text-color-green text-bolder">Акция поступила на модерацию!</div>

            <p class="mt24">
                Сейчас она находится на срочной проверке<br>и будет опубликована в ближайшее время.
            </p>
        </div>

        <div class="mt32">
            <button class="btn btn--white-green" @click="closeActionToModaretionConfirm()">Степенно жду!</button>
        </div>
    </div>
</template>

<script>
    import {mapState} from 'vuex'
    import Popup from '@/components/modal/Popup.vue'

    export default {
        name: "couponBuyOk",
        components: {
            Popup
        },
        methods: {
            closeActionToModaretionConfirm: function () {
                this.$store.commit('popups/close', {name: this.popups.actionToModaretionConfirm.name});
            }
        },
        computed: {
            ...mapState({
                popups: state => state.popups.items,
                user: state => state.users.user,
            }),
        }
    }
</script>

