<template>
    <div class="content">
        <div class="main-title">
            <div class="main-title__wrap main-title--nowrap">
                <div class="h1">Продвижение акций</div>
            </div>
            <div class="main-title__right">
              <span class="i i-info mr4"></span>
              Эта страница помогает оценить, какие акции нуждаются в продвижении
            </div>
        </div>
        <tabs
            @changed="changeTab"
            :options="{
                useUrlFragment: false,
                defaultTabHash: 'tab-all',
            }"
            class="tabs3-0"
        >
            <tab v-for="tab in tabs" :id="tab.id" :name="tab.name" :key="tab.id">
                <div class="rating-table rating-table-wide rating-table-promotion">
                    <div class="r-table-head">
                        <div  class="text-color-grey text-bold text-size-xs head-item">
                            Название
                        </div>
                        <div class="text-color-grey text-bold text-size-xs head-item"> <!-- :class="{active: sort.order === 'sum','up': sort.by === 'asc'}" @click="setSort('sum')"  -->
                            Охват
                        </div>
                        <div class="sort__link down head-item"
                        :class="{active: sort.order === 'view','up': sort.by === 'asc'}" @click="setSort('view')">
                            Просмотры
                        </div>
                        <div class="sort__link down head-item"
                        :class="{active: sort.order === 'coupons','up': sort.by === 'asc'}" @click="setSort('coupons')">
                            Оформления
                        </div>
                        <div class="sort__link down head-item"
                        :class="{active: sort.order === 'repay','up': sort.by === 'asc'}" @click="setSort('repay')">
                            Погашения
                        </div>
                        <div class="sort__link down head-item"
                        :class="{active: sort.order === 'sum','up': sort.by === 'asc'}" @click="setSort('sum')">
                            Доход
                        </div>
                    </div>
                    <div class="r-table">
                        <div class="r-table-row"  v-for="(item, index) of items" :id="'row' + index">
                            <tooltip
                                    :direction="'bottom-left-direction'"
                                    :add-class-wrap="'rating-tooltip'"
                                    :isBtnClose="true"
                                    :add-style="{width: 231 + 'px'}">
                        <span slot="tooltipBtn">

                            <div @click="loadActionProvider(item.provider.id)" class="info info-bg" v-if="item.provider.actionsCount > 0">{{item.provider.actionsCount}}</div>
                            <div class="info" v-else>
                                <span class="i i-info5"></span>
                            </div>
                        </span>

                                <div slot="tooltipContent">
                                    <div class="text-bold text-size-m no-wrap" v-if="item.provider.actionsCount == 0">
                                        Поставщик <a target="_blank" :href="'/moderator/providers/'+item.provider.status+'/id'+item.provider.id+'/'" class="link">{{item.provider.name}}</a>
                                    </div>
                                    <div v-else-if="itemsProvider[item.provider.id]">
                                        <div class="other text-bold text-size-m">
                                            Другие акции поставщика <a target="_blank" :href="'/moderator/providers/'+item.provider.status+'/id'+item.provider.id+'/'" class="link">{{item.provider.name}}</a>
                                        </div>
                                        <div v-if="itemsProvider[item.provider.id].items.length > 0" class="rating-table rating-table-wide rating-table-promotion rating-table-promotion--no-info">
                                            <div class="r-table">
                                                <div class="r-table-row" v-if="itemProvider.id != item.id" v-for="itemProvider in itemsProvider[item.provider.id].items">
                                                    <div class="r-table-item text-color">
                                                        <a href="javascript:void(0);" class="link-icon"><span class="ellipsis">{{ itemProvider.name }}</span><span class="i i-copy ml8" @click="copyText('/moderator/coupons/'+item.status+'/id'+item.id+'/', $event.target);"></span><span class="i i-ok3 i--green"></span></a>
                                                    </div>
                                                    <div class="r-table-item text-color">
                                                        {{ itemProvider.region.replace('область','обл.') }}
                                                    </div>
                                                    <div class="r-table-item text-color" :class="{'text-color-grey': itemProvider.showCounterFormat == 0}">
                                                        {{ itemProvider.showCounterFormat == 0 ? '—' : itemProvider.showCounterFormat}}
                                                    </div>
                                                    <div class="r-table-item text-color-grey">
                                                        {{ itemProvider.lk_status_publicate_date_diff }}
                                                    </div>
                                                    <div class="r-table-item text-colo" :class="{'text-color-grey': itemProvider.sold == 0 }">
                                                        {{ itemProvider.sold == 0   ? '—' : itemProvider.sold}}
                                                    </div>
                                                    <div class="r-table-item text-color" :class="{'text-color-grey': itemProvider.ext == 0}">
                                                        {{ itemProvider.ext == 0   ? '—' : itemProvider.ext}}
                                                    </div>
                                                    <div class="r-table-item text-color" :class="{'text-color-grey': itemProvider.sumFormat == 0 }">
                                                        {{ itemProvider.sumFormat == 0  ? '—' : itemProvider.sumFormat }} <span v-if="itemProvider.sumFormat != 0" class="rouble">{{ $store.getters['options/setRouble'] }}</span>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                        <div v-else-if="itemsProvider[item.provider.id].isLoadedProcess">
                                            <div class="r-table r-table--ghost" v-show="itemsProvider[item.provider.id].isLoadedProcess">
                                                <div class="r-table-row" v-for="index in +item.provider.actionsCount" :key="index">
                                                    <div class="r-table-item text-color-default text-size-l">
                                                        <span><a href="javascript:void(0);" >&nbsp;</a></span>
                                                    </div>
                                                    <div class="r-table-item text-size-l text-color">
                                                        &nbsp;
                                                    </div>
                                                    <div class="r-table-item text-size-l text-color">
                                                        &nbsp;
                                                    </div>
                                                    <div class="r-table-item text-size-l text-color">
                                                        &nbsp;
                                                    </div>
                                                    <div class="r-table-item text-size-l text-color">
                                                        &nbsp;
                                                    </div>
                                                    <div class="r-table-item text-size-l text-color">
                                                        &nbsp;
                                                    </div>
                                                </div>
                                            </div>
                                        </div>

                                    </div>
                                </div>
                            </tooltip>
                            <div class="r-table-item text-color">
                                <a href="javascript:void(0);" class="link-icon"><span class="ellipsis">{{ item.name }}</span><span class="i i-copy ml8" @click="copyText('/moderator/coupons/'+item.status+'/id'+item.id+'/', $event.target);"></span><span class="i i-ok3 i--green"></span></a>
                            </div>
                            <div class="r-table-item text-color">
                                {{ item.region.replace('область','обл.') }}
                            </div>
                            <div class="r-table-item text-color" :class="{'text-color-grey': item.showCounterFormat == 0 }">
                                {{ item.showCounterFormat == 0 ? '—' : item.showCounterFormat}}
                            </div>
                            <div class="r-table-item text-color-grey">
                                {{ item.lk_status_publicate_date_diff }}
                            </div>
                            <div class="r-table-item text-color" :class="{'text-color-grey': item.sold == 0 }">
                                {{ item.sold == 0   ? '—' : item.sold}}
                            </div>
                            <div class="r-table-item text-color" :class="{'text-color-grey':  item.ext == 0 }">
                                {{ item.ext == 0   ? '—' : item.ext}}
                            </div>
                            <div class="r-table-item text-color" :class="{'text-color-grey': item.sumFormat == 0 }">
                                {{ item.sumFormat == 0  ? '—' : item.sumFormat }} <span v-if="item.sumFormat != 0" class="rouble">{{ $store.getters['options/setRouble'] }}</span>
                            </div>
                        </div>
                    </div>
                    <div class="r-table r-table--ghost" v-show="!isLoadedProcess">
                        <div class="r-table-row" v-for="index in 10" :key="index">
                            <div class="r-table-item text-color-default text-size-l">
                                <span><a href="javascript:void(0);" >&nbsp;</a></span>
                            </div>
                            <div class="r-table-item text-size-l text-color">
                                &nbsp;
                            </div>
                            <div class="r-table-item text-size-l text-color">
                                &nbsp;
                            </div>
                            <div class="r-table-item text-size-l text-color">
                                &nbsp;
                            </div>
                            <div class="r-table-item text-size-l text-color">
                                &nbsp;
                            </div>
                            <div class="r-table-item text-size-l text-color">
                                &nbsp;
                            </div>
                        </div>
                    </div>
                </div>
            </tab>
        </tabs>
    </div>
</template>


<script>
    import {mapState} from 'vuex'
    import RatingListItem from '@/components/rating/RatingListItem.vue'
    import Loader from '@/components/Loader.vue';
    import Popup from '@/components/modal/Popup.vue'
    import Tabs from '@/components/tabs/Tabs.vue';
    import Tab from '@/components/tabs/Tab.vue';
    import Tooltip from '@/components/Tooltip.vue';
    import { copyTextToClipboard } from "@/helpers/util/helper";

    export default {
        name: "CouponsPromotion",
        components: {
            RatingListItem,
            Loader,
            Popup,
            Tabs,
            Tab,
            Tooltip
        },
        data: function () {
            return {
                sortDirection: 'desc',
                unsubscribe: false,
                currentTab: '#tab-all',
                tabs: [
                    {
                        id: 'tab-russian',
                        name: 'Общероссийские'
                    },
                    {
                        id: 'tab-moscow',
                        name: 'Москва и область'
                    },
                    {
                        id: 'tab-piter',
                        name: 'Питер и область'
                    },
                    {
                        id: 'tab-other',
                        name: 'Остальные'
                    },
                    {
                        id: 'tab-all',
                        name: 'Все'
                    },
                ]
            }
        },
        created() {
            //this.$store.commit('promotion/resetPage');
            //this.$store.dispatch('promotion/getActions', {action: 'page', tab: this.currentTab.replace('#tab-','')});
        },
        beforeDestroy(){

        },
        mounted() {
            window.addEventListener('scroll', this.handleScroll);
            ym(39511350, 'hit', this.$route.path, {title: 'Продвижение | ZemSkidki.ru'});
        },
        computed: {
            ...mapState({
                items:  state => state.promotion.items,
                itemsProvider:  state => state.promotion.itemsProvider,
                isLoadedProcess:  state => state.promotion.isLoadedProcess,
                sort:  state => state.promotion.sort.current,
            }),
        },
        methods: {
            changeTab(data) {
                this.currentTab = data.tab.hash;
                this.$store.commit('promotion/resetPage');
                this.$store.dispatch('promotion/getActions', {action: 'page', tab: this.currentTab.replace('#tab-','')});
            },
            setSort: function (sortField) {
                this.$store.commit('promotion/setSort', {order: sortField});
                this.$store.dispatch('promotion/getActions', {action: 'sort', tab: this.currentTab.replace('#tab-','')});
            },
            loadActionProvider: function (id) {
                this.$store.dispatch('promotion/getActionsProvider', {id: id});
            },
            handleScroll: function () {
                let scroll = window.scrollY;
                let viewport = window.innerHeight;
                let bodyHeight = document.body.clientHeight;
                if (bodyHeight - 80 <= scroll + viewport) {
                    this.$store.dispatch('promotion/getActions', {action: 'page', tab: this.currentTab.replace('#tab-','')});
                }
            },
            copyText(txt, target){
              target.parentNode.classList.add('copied');
              setTimeout(function(){
                target.parentNode.classList.remove('copied');
              }, 550);
              copyTextToClipboard('https://zemskidki.ru'+txt);
            },
        }
    }
</script>
