<template>
    <transition name="fade-popup">
        <div class="city-select-container" v-if="show">
            <header class="header header--black">
                <div class="header-main header-main--editor">
                    <div class="inner-wrapper">
                        <div class="h-left">

                        </div>

                        <div class="h-right">
                            <span @click="close" class="btn h-editor-btn btn--ico btn--save">
                                <span class="i i-ok2"></span>
                                <span class="t">Сохранить</span>
                            </span>
                        </div>
                    </div>
                </div>
            </header>

            <div class="content">
                <div class="main-title">
                    <div class="main-title__wrap">
                        <div class="h1">Выберите города</div>
                    </div>

                    <div class="main-title__right">
                        <span class="text-color-grey text-size-s">Выбрано: {{ countSelect }}</span>
                    </div>
                </div>


                <div class="wrapper-content mt32">
                    <div class="wrapper-content-inner">
                        <div class="city-choice-wrap">
                            <div class="city-choice__all-russia">
                                <a class="text-size-s" href="javascript:void(0);" @click="openConstructActionAllCity">Сделать акцию доступной для всей России</a>
                            </div>
                            
                            <div class="city-choice">
                                <!-- для разделителя поле region.isDelimiter -->
                                <div v-for="region in regions" class="city-choice__region" :class="{ open: region.isOpen, '--sep': region.isDelimiter }">
                                    <div class="city-choice__region-title">
                                        <span class="p" @click="showCities(region)"></span>
                                        <span class="label" @click="regionCheck(region)">
                                            <span class="pseudo-checkbox" :data-status="region.isChecked"></span>
                                            <span class="label__text">{{ region.name }}</span>
                                        </span>
                                    </div>
                                    
                                    <div class="city-choice__region-wrap" v-show="region.isOpen" :id="region.id">
                                        <div class="wrapper">
                                            <div class="item" :class="{ active: children.isChecked }" :key="children.xmlId" v-for="children in region.cities">
                                                <input v-model="children.isChecked" type="checkbox" >
                                                <label @click="changeCity(children)">{{ children.name }} </label>
                                            </div>
                                        </div>
                                    </div>

                                </div>
                            </div>

                        </div>
                    </div>
                </div>
            </div>
        </div>
    </transition>
</template>

<script>
    import {mapState} from 'vuex'
    export default {
        name: "CityChoice",
        props: {
            regionsProp: {},
            show: {
                default: function () {
                    return false
                }
            },
            name: ''
        },
        data: function() {
            return {
                regions: {},
                countSelect: 0,
                isShowPopup: false,
            }
        },
        mounted: function() {
            this.updateCitiesList();
        },
        watch: {
            regionsProp: function (val, oldval) {
                this.updateCitiesList();
            },
            show: function () {
                if (this.show) {
                    document.getElementsByTagName('body')[0].classList.add("city-select");
                } else {
                    document.getElementsByTagName('body')[0].classList.remove("city-select");
                }
            },
        },

        computed: {
            ...mapState({
                popups: state => state.popups.items,
            }),
        },

        methods: {
            close: function () {
                for(let i in this.regions){
                    this.regions[i].isOpen = false;
                }
                this.$emit('change',{regions:this.regions,count:this.countSelect});
                if(this.show && this.name){
                    this.$store.commit('popups/close', {name: this.name});
                }
            },
            calcChecked: function () {
                this.countSelect = 0;

                for (let i in this.regions){
                    for (let n in this.regions[i].cities) {
                        if(this.regions[i].cities[n].isChecked === true) {
                            this.countSelect++;
                        }
                    }

                    this.regionStatus(this.regions[i]);
                }
                return this.countSelect;
            },
            updateCitiesList: function () {
                this.regions = {...this.regionsProp};
                this.calcChecked();


            },
            showCities: function(region) {
                region.isOpen = !region.isOpen;
            },

            // клик по области
            regionCheck: function(region) {
                if(region.isChecked === 0 || region.isChecked === 1) {
                    region.isChecked = 2; //включить все города
                    for(let i in region.cities) {
                        region.cities[i].isChecked = true;
                    }
                } else {
                    region.isChecked = 0; //убрать все города

                    for(let i in region.cities) {
                        region.cities[i].isChecked = false;
                    }
                }
                this.$nextTick(function () {
                    this.calcChecked();
                    this.$emit('change',{regions:this.regions,count:this.countSelect});
                });
            },
            changeCity:function(item) {
                item.isChecked = !item.isChecked;
                this.$emit('change',{regions:this.regions,count:this.countSelect});
                this.$nextTick(function () {
                    this.calcChecked();
                });
            },
            // статус выбранных городов в области
            regionStatus: function(region) {
                let countSelect = 0;
                for(let i in region.cities) {
                    if(region.cities[i].isChecked === true) {
                        countSelect++;
                    }
                }

                if(countSelect === region.cities.length) {
                    region.isChecked = 2;
                } else if (countSelect === 0) {
                    region.isChecked = 0;
                } else {
                    region.isChecked = 1;
                }
            },
            openConstructActionAllCity: function () {
                this.$store.dispatch('popups/open', {name: this.popups.constructActionAllCity.name});
            },
        }
    }
</script>

<style scoped>
    .fade-popup-enter-active, .fade-popup-leave-active {
        transition: opacity .15s;
    }
    .fade-popup-enter, .fade-popup-leave-to {
        opacity: 0;
    }
</style>