<template>
    <section class="container__left container-scroll">
        <tabs
            @changed="changeTab"
            class="coupon-details wrapper-content pt0 pb0"
            :tabsNavRight="tabsNavRight()"
        >
            <tab id="tab-info" name="Описание">
                <div class="coupon-details wrapper-content wrapper-content-inner">
                    <div v-if="user.access_rights && user.access_rights.is_moderator">
                        <router-link :to="'/moderator/coupons/img'+$route.params.id+'/'">Исходники картинок</router-link>
                    </div>
                    <!-- перед галереей показываем большую картинку (первая картинка из галереи) -->
                    <div class="gallery-thumbnail-list">
                        <div class="item col-24 gallery-item">
                            <span @click="downloadMainImage" title='Скачать' v-if="user.access_rights && user.access_rights.is_moderator" class="i i-file download_icon"></span>
                            <a @click="showViewer" href="javascript:void(0)" :style="'background-image: url(//api.zemskidki.ru'+action.image+');'"></a>
                        </div>
                    </div>
                    
                    <gallery v-if="action.aboutActionPhoto.length > 0" :items="action.aboutActionPhoto" type="photo" :firstphoto="action.image_for_viewer" :showFirstPhoto="showFirstPhoto"></gallery>
                    <div class="action-detail__info" v-else>
                        <div class="title">Фотогалерея</div>
                        <div class="text text-color-red">не заполнено</div>
                    </div>
                    <gallery v-if="action.aboutActionVideo.length > 0" :items="action.aboutActionVideo" type="video"></gallery>


                    <div class="action-detail__info" v-if="action.aboutActionPromo.length > 0">
                        <div class="text" v-html="action.aboutActionPromo"></div>
                    </div>


                    <div class="action-detail__info" v-if="action.aboutActionInclude.length > 0">
                        <div class="title">Скидка распространяется на:</div>

                        <div class="text" v-html="action.aboutActionInclude"></div>
                    </div>



                    <div class="action-detail__info" v-if="action.aboutActionExclude.length > 0">
                        <div class="title">Скидка НЕ распространяется на:</div>
                        <div class="text" v-html="action.aboutActionExclude"></div>
                    </div>



                    <div class="action-detail__info" v-if="action.aboutActionPrivileges.length > 0">
                        <div class="title">Дополнительные льготы</div>
                        <div class="text" v-html="action.aboutActionPrivileges"></div>
                    </div>



                    <div class="action-detail__attention" v-if="action.aboutActionWarning.length > 0">
                        <div class="title">Обратите внимание!</div>

                        <div class="text" v-html="action.aboutActionWarning"></div>
                    </div>


                    <div class="action-detail__info" v-if="action.aboutActionMore.length > 0">
                        <div class="title">Дополнительная информация</div>
                        <div class="text" v-html="action.aboutActionMore"></div>
                    </div>
                    
                    
                    <div class="action-detail__how" v-if="countHowToUse">
                        <div class="title">Как воспользоваться скидкой?</div>
                        <div class="item" v-if="action.callUsJson !== ''">
                             <div class="subtitle" v-if="countHowToUse > 1">Способ 1</div>

                            <ol>
                                <li>Оформить купон</li>
                                <li v-if="action.callUsJson">Позвонить нам по телефону
                                    <tooltip
                                            :direction="'bottom-center-direction'"
                                            style="display: inline;"
                                            :add-style="{width: 267 + 'px'}">
                                        <span slot="tooltipBtn" class="pseudo-link link-color">{{ action.callUsJson.phone.value }}</span>

                                        <div slot="tooltipContent">
                                            <div class="text-size-m">Номер будет доступен покупателю <br>
                                                только после оформления купона</div>
                                        </div>
                                    </tooltip>
                                    ({{ action.callUsJson.operation.value }}), чтобы {{ action.callUsJson.target.value }}</li>
                            </ol>
                        </div>
                        <div class="item" v-if="action.comeUsJson !== ''">
                             <div class="subtitle" v-if="countHowToUse > 1">Способ {{action.callUsJson ? 2 : 1}}</div>

                            <ol>
                                <li>Оформить купон</li>
                                <li v-if="action.comeUsJson">
                                    Приехать к нам в {{ action.comeUsJson.where.value }}
                                    <span v-if="action.comeUsJson.otherAddresses.length">по любому из указанных адресов, чтобы
                                            {{ action.comeUsJson.target.value }}:<br>
                                            <span style="display: block;">
                                                • <a v-if="action.comeUsJson.coord.value" href="#" @click.prevent="openMap(0)" class="pseudo-link link-color">{{ action.comeUsJson.address.value }}</a>
                                                <span v-else>{{ action.comeUsJson.address.value }}</span>
                                                ({{ action.comeUsJson.operation.value }})
                                            </span>
                                            <span v-if="action.comeUsJson.otherAddresses" style="display: block;" v-for="(item, index) in action.comeUsJson.otherAddresses">
                                                • <a v-if="item.coord.value" href="#" @click.prevent="openMap(index + 1)" class="pseudo-link link-color">{{ item.address.value }}</a>
                                                <span v-else>{{ item.address.value }}</span>
                                                ({{ item.operation.value }})
                                            </span>
                                        </span>
                                    <span v-else-if="this.action.comeUsJson.coord.value">по адресу
                                            <a href="#" @click.prevent="openMap(0)" class="pseudo-link link-color">{{ action.comeUsJson.address.value }}</a> ({{ action.comeUsJson.operation.value }}),
                                            чтобы {{ action.comeUsJson.target.value }}
                                        </span>
                                    <span v-else>по адресу
                                            {{ action.comeUsJson.address.value }} ({{ action.comeUsJson.operation.value }}),
                                            чтобы {{ action.comeUsJson.target.value }}
                                        </span>
                                </li>
                            </ol>
                        </div>
                        <div class="item" v-if="action.goLinkJson !== ''">
                            <div class="subtitle" v-if="countHowToUse > 1">Способ {{countHowToUse}}</div>

                            <ol>
                                <li>Оформить купон</li>
                                <li>Сделать заказ по <tooltip
                                        :direction="'bottom-center-direction'"
                                        style="display: inline"
                                        :add-style="{width: 267 + 'px'}">
                                    <a slot="tooltipBtn" href="javascript:void(0);">ссылке</a>

                                    <div slot="tooltipContent">
                                        <div class="text-size-m"><a target="_blank" :href="action.goLinkJson.link.value">Ссылка</a> будет доступна покупателю
                                            <br>
                                            только после оформления купона</div>
                                    </div>
                                </tooltip>, указав в описании кодовое слово «ЗЕМСКИДКИ»</li>
                            </ol>
                        </div>
                    </div>
                    <p v-else class="text-color-red">не заполнено</p>
                    
                    
                    
                    <div class="action-detail__exclusive">
                        <div class="item">
                            <span class="item__title">Это эксклюзивная скидка.</span>
                            <span class="item__text" v-if="action.onenessDiscount" v-html="onenessDiscountHtml"></span>
                            
                            <!-- текст поменялся -->
                            <!-- <span>
                                Обычным покупателям мы даём скидку не более 10%, а для покупателей<br> с купоном готовы предоставить 27%. Такую скидку невозможно получить, минуя ZemSkidki.ru
                            </span> -->
                        </div>

                        <div class="item">
                            <span class="item__title">Это честная скидка.</span>
                            <span class="item__text" v-if="action.originDiscount" v-html="originDiscountHtml"></span>
                            
                            <!-- <span>
                                На рекламу и продвижение мы тратим до 30% своих доходов. Покупателей<br> с ZemSkidki.ru мы получаем, минуя эти затраты, поэтому можем подвинуться по цене. При этом 3%<br> мы перечисляем на поддержание и развитие проекта, а на оставшиеся 27% предоставляем скидку.
                            </span> -->
                        </div>
                    </div>
                </div>
            </tab>
                
            <slot></slot>
        </tabs>


        


        <div class="wrapper-content mt32">
            <div class="wrapper-content-inner">
                <div class="content-title mb8">
                    <div class="content-title__wrap">
                        <div class="h2">Область действия</div>
                    </div>
                    <div class="content-title__right">
                        <span class="text-size-xs text-color-grey">Этот блок не виден покупателям</span>
                    </div>
                </div>

                <div class="tags --text" v-if="citiesAction.length > 0">
                    <span class="tag-text" v-for="city in citiesAction">
                        <span class="text">{{ city.name }}</span>
                    </span>
                </div>
                <!-- end tags -->
                <div class="text-color-red" v-else>Не заполнено</div>
            </div>
        </div>
        <!-- end wrapper-content -->


        <div class="wrapper-content mt32">
            <div class="wrapper-content-inner">
                <div class="content-title mb8">
                    <div class="content-title__wrap">
                        <div class="h2">Теги для поиска</div>
                    </div>
                    <div class="content-title__right">
                        <span class="text-size-xs text-color-grey">Этот блок не виден покупателям</span>
                    </div>
                </div>

                <div class="tags --text" v-if="action.tags.length > 0">
                    <span class="tag-text" v-for="tag in action.tags">
                        <span class="text">{{ tag }}</span>
                    </span>
                </div>
                <!-- end tags -->
                <div class="text-color-red" v-else>Не заполнено</div>
            </div>
        </div>
        <!-- end wrapper-content -->

        <popup v-if="this.action.comeUsJson" 
                :show="popups.address.show" 
                :name="popups.address.name"
                :add-class="'popup--map'" 
                :is-set-blank="true"
                >
            <Map :markers="getMarkers()"
                 :height="558"
                 :coords="mapCenter"
                 :zoom="11"
            ></Map>
        </popup>
    </section>
    <!-- end container__left -->
</template>


<script>
    import Tabs from '@/components/tabs/Tabs.vue';
    import Tab from '@/components/tabs/Tab.vue';
    import Popup from '@/components/modal/Popup.vue'
    import ActionModals from '@/components/partner/actions/ActionModals.vue';
    import {mapState} from 'vuex';
    import Tooltip from '@/components/Tooltip.vue';
    import Map from '@/components/Map.vue'
    import Gallery from '@/components/Gallery.vue';
    import {actionDiscountHtml} from '@/helpers/tempateHtml';

    export default {
        props:['action'],
        components: {
            Tabs,
            Tab,
            Popup,
            ActionModals,
            Tooltip,
            Map,
            Gallery,
        },
        data: function () {
            return {
                mapCenter: [55.427862,37.550162],
                showFirstPhoto: false,
            }
        },
        /*mounted: function () {
            this.$store.dispatch('partnerActions/initDetailPage', {route: this.$route});
        },*/
        watch: {},
        computed: {
            ...mapState({
                popups: state => state.popups.items,
                cities: state => state.cities.items,
                user: state => state.users.user,
            }),
            citiesAction() {
                let citiesAction = [];
                let cities = [...this.cities];
                for (let city of cities) {
                    if(this.action.city.indexOf(city.xmlId)!== -1){
                        citiesAction.push(city)
                    }
                }
                return citiesAction;
            },
            originDiscountHtml(){
                return this.action.originDiscount ? actionDiscountHtml(this.action.originDiscount, this.action.discount) : ''
            },
            onenessDiscountHtml(){
                return this.action.onenessDiscount ? actionDiscountHtml(this.action.onenessDiscount, this.action.discount) : ''
            },
            countHowToUse(){
                let i = 0;
                if(this.action.callUsJson){
                    i++;
                }
                if(this.action.comeUsJson){
                    i++;
                }
                if(this.action.goLinkJson){
                    i++;
                }
                return i;
            },
        },
        methods: {
            showViewer(){
                this.showFirstPhoto = new Date();
            },
            downloadMainImage(){
                window.open('//api.zemskidki.ru'+this.action.image_original,'_blank');
            },
            balloonTemplate(address) {
                return `
                  <div class="yamap_baloon_link yamap_baloon_link_address hover">
                    <img src="//api.zemskidki.ru/` + this.action.image + `" alt="` + this.action.name + `">
                    ` + this.action.provider.name + `<br>
                    <b class="yamap_baloon_link-name">` + this.action.name + `</b><br/>
                    ` + address + `
                  </div>
                `
            },
            openMap: function(key) {
                let markers = {},
                coords;

                if (this.action.comeUsJson.coord.value) {
                    coords = this.action.comeUsJson.coord.value ? this.action.comeUsJson.coord.value.split(',') : [];
                    markers[0] = [coords[1], coords[0]];
                }

                for (let index in this.action.comeUsJson.otherAddresses) {
                    if (this.action.comeUsJson.otherAddresses[index].coord.value) {
                        let coords = this.action.comeUsJson.otherAddresses[index].coord.value.split(',');
                        markers[parseInt(index) + 1] = [coords[1], coords[0]];
                    }
                }

                if (Object.getOwnPropertyNames(markers).length) {
                    this.mapCenter = markers[key];
                    this.$store.dispatch('popups/open', {name: this.popups.address.name});
                }

            },
            getMarkers: function () {
                let markers = [];

                if (this.action.comeUsJson.coord.value) {
                    let coords = this.action.comeUsJson.coord.value ? this.action.comeUsJson.coord.value.split(',') : [];

                    markers.push({
                        "id": 10,
                        "type": "placemark",
                        "coords": [coords[1], coords[0]],
                        "hint": "",
                        "balloon": {
                            "header": "",
                            "body": this.balloonTemplate(this.action.comeUsJson.address.value),
                            "footer": ""
                        },
                        "cluster": "1"
                    });

                    for (let index in this.action.comeUsJson.otherAddresses) {
                        let coords = this.action.comeUsJson.otherAddresses[index].coord.value.split(',');
                        let obj = {
                            "id": parseInt(index) + 1,
                            "type": "placemark",
                            "coords": [coords[1], coords[0]],
                            "hint": "",
                            "balloon": {
                                "header": "",
                                "body": this.balloonTemplate(this.action.comeUsJson.otherAddresses[index].address.value),
                                "footer": ""
                            },
                            "cluster": "1"
                        };

                        markers.push(obj);
                    }
                }

                return markers;
            },
            tabsNavRight(){
                // let str = "<div class='coupon-stat-mini'><span class='i i-eye'></span><span class='t'>"+(this.action.showCounter > 0 ?this.action.showCounterFormat : 1 )+"</span></div>";
                // if(+this.action.statistics.sold > 0){
                //     str += "<div class='coupon-stat-mini'><span class='i i-cart'></span><span class='t'>"+this.action.statistics.sold+"</span></div>";
                // }
                // return str
            },
            changeTab(data) {
                this.$emit('changed',data)
            }
        },
    }

</script>

<style>
    .yamap_baloon_link.yamap_baloon_link_address {
        position: relative;
        background-size: 100px;
        padding-left: 115px;
        min-height: 100px;
    }

    .yamap_baloon_link.yamap_baloon_link_address img {
        position: absolute;
        top: 0;
        left: 0;
        width: 100px;
        height: 100px;
        border-radius: 10px;
    }

    .yamap_baloon_link.yamap_baloon_link_address .yamap_baloon_link-name {
        color: #2F80ED;
        font-size: 14px;
    }
    .download_icon{
        position: absolute;
        z-index: 1000;
        right: 15px;
        top: 5px;
        cursor: pointer;
    }
</style>