<template>
    <div class="popup-info">
        <div>
            <img src="@/assets/img/icon-ok-big.svg" alt="">
        </div>

        <div class="text mt32">
            <div class="text-size-xxl text-color-green text-bolder">Счёт успешно отправлен поставщику</div>

        </div>

        <div class="mt32">
            <button @click="close" class="btn btn--white-green">Спасибо Алексею Земскову!</button>
        </div>
    </div>
</template>

<script>
    import {mapState} from 'vuex'
    import Popup from '@/components/modal/Popup.vue'

    export default {
        name: "BillSendOk",
        components: {
            Popup
        },
        methods: {
            close: function(){
                this.$store.commit('popups/close', {name: this.popups.billSendOk.name});
            }
        },
        computed: {
            ...mapState({
                popups: state => state.popups.items,
            }),
        },
        beforeDestroy(){
            this.$store.commit('popups/close', {name: this.popups.billSendOk.name});    
        }
    }
</script>

